import React from 'react'
import PageOne from './HospitalAffiliationOI_p1'
import PageTwo from './HospitalAffiliationOI_p2'
import HospitalAffiliationOIContext from './context/HospitalAffiliationOIContext'
import { handleNextButton } from '../utils/hospitalApproverUtils'

const HospitalAffiliationOI = ({match, history}) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  let isDisabledButton = React.useRef()
  let inputRef = React.useRef()
  const [forms, setForms] = React.useState()

  const handleNext = async (value) => {
    if (!isApproved) {
      handleNextButton(forms, inputRef.current.value)
    }
    setIsNextPage(value)
  }

  return (
    <HospitalAffiliationOIContext.Provider value={{paramId: match.params.id, isApproved, setIsApproved, setForms, inputRef, isDisabledButton}}>
      {!isNextPage && <PageOne setPage={handleNext} history={history} /> }
      {isNextPage && <PageTwo setPage={setIsNextPage} history={history} /> }
    </HospitalAffiliationOIContext.Provider>
  )
}
export default HospitalAffiliationOI
