import React from 'react'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import InputBase from '@material-ui/core/InputBase'
import DropDown from '../InputComponents/DropDown/DropDown'
import { StatusSelectComponent, TextInputComponent, NumberInputComponent, RoomTypesComponent } from '../InputComponents/InputComponents'
import classes from './RowComponent.module.scss'
import { useSelector } from 'react-redux'
import { getNestedObjects } from '../../../../../../Components/Utils/utils'

const RowComponent = props => {
  const { pos = 0, rowObj = {}, isEdit = false, onChange } = props

  const RoomTypeItems = useSelector(state => state.roomAndBoard.RoomTypeArr)
  const RoomItems = useSelector(state => state.roomAndBoard.RoomItemsArr)

  const RoomCodeValue = () => {
    if (rowObj.roomType.length > 0) {
      const result = RoomItems.filter(items => { if (items.desc.trim() === rowObj.roomType.trim()) { return items } })
      const resultCode = getNestedObjects(result, ['0', 'code'])
      return resultCode
    } else {
      return ''
    }
  }

  const HandleChangeRoomCode = () => {
    if (rowObj.roomType.length > 0) {
      onChange('roomCode', RoomCodeValue())
    }
    return RoomCodeValue()
  }

  const StatusItems = [
    {id: 101, text: 'Active', value: 'Active'},
    {id: 102, text: 'Inactive', value: 'Inactive'}
  ]

  return (
    <React.Fragment>
      <TableRow className={classes.tableBodyRow}>
        <TableCell align='left'>{isEdit ? HandleChangeRoomCode() : RoomCodeValue()}</TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <DropDown
              id={`${rowObj.pos}RoomType`}
              value={rowObj.roomType}
              items={RoomTypeItems}
              error={!!rowObj.error.roomType}
              errorMsg={rowObj.error.roomType}
              onChange={e => {
                onChange('roomType', e.target.value)
              }}
            />
          ) : (
            rowObj.roomType
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.pos}description`}
              value={rowObj.description}
              error={!!rowObj.error.description}
              errorMsg={rowObj.error.description}
              onChange={e => {
                onChange('description', e.target.value)
              }}
            />
          ) : (
            rowObj.description
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <NumberInputComponent
              id={`${rowObj.pos}price`}
              value={rowObj.price}
              error={!!rowObj.error.price}
              errorMsg={rowObj.error.price}
              onChange={value => {
                onChange('price', value)
              }}
            />
          ) : (
            rowObj.price
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <DropDown
              id={`${rowObj.pos}status`}
              value={rowObj.status}
              items={StatusItems}
              error={!!rowObj.error.status}
              errorMsg={rowObj.error.status}
              onChange={e => {
                onChange('status', e.target.value)
              }}
            />
          ) : (
            rowObj.status
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.pos}remarks`}
              value={rowObj.remarks}
              error={!!rowObj.error.remarks}
              errorMsg={rowObj.error.remarks}
              onChange={e => {
                onChange('remarks', e.target.value)
              }}
            />
          ) : (
            rowObj.remarks
          )}
        </TableCell>
      </TableRow>
    </React.Fragment>
  )
}

export default React.memo(RowComponent, (prevProps, nextProps) => {
  try {
    if (
      prevProps.isEdit === nextProps.isEdit &&
      prevProps.rowObj.status === nextProps.rowObj.status &&
      prevProps.rowObj.description === nextProps.rowObj.description &&
      prevProps.rowObj.roomType === nextProps.rowObj.roomType &&
      prevProps.rowObj.price === nextProps.rowObj.price &&
      prevProps.rowObj.remarks === nextProps.rowObj.remarks &&
      prevProps.rowObj.error.status === nextProps.rowObj.error.status &&
      prevProps.rowObj.error.description === nextProps.rowObj.error.description &&
      prevProps.rowObj.error.roomType === nextProps.rowObj.error.roomType &&
      prevProps.rowObj.error.price === nextProps.rowObj.error.price &&
      prevProps.rowObj.error.remarks === nextProps.rowObj.error.remarks
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
