import React, { memo, useRef } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './EncoderInputFieldNumberFormat.module.scss'
import NumberFormat from 'react-number-format'

function NumberFormatCustom (props) {

  const numberInputRef = useRef()

  const { onChange, defaultValue, ...other } = props

  const handleChange = e => {
    const {target: {value}} = e
    numberInputRef.current.value = value
    onChange(e)
  }

  return (
    <NumberFormat
      {...other}
      suffix='-'
      ref={numberInputRef}
      value={defaultValue}
      onValueChange={(values) => {
        handleChange({
          target: {
            name: props.name,
            value: values.value
          }
        })
      }}
    />
  )
}

function CustomTextFieldNumberFormat (props) {
  const {
    label = '',
    disabled,
    required,
    inputRef,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    defaultValue,
    format,
    name,
    ...other
  } = props

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                disabled={disabled}
                autoComplete='off'
                name={name}
                defaultValue={defaultValue}
                inputProps={{
                  format,
                  defaultValue,
                  onChange
                }}
                inputComponent={NumberFormatCustom}
                {...other}
              />
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

// const isEqual = (prevProps, nextProps) => {
//   return (prevProps.defaultValue === nextProps.defaultValue)
// }
export default memo(CustomTextFieldNumberFormat)
