const sendData = async (path, data) => {
  let response, err
  // const URL = `${process.env.REACT_APP_SERVER_API}`
  try {
    // const { data: res } = await axios.post(URL + `/p/f/partners${path}`, data)
    const { data: res } = await axiosPf({
      url: `/partners${path}`,
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
    response = res
  } catch (e) {
    err = e
  }

  return { response, err }
}

export default sendData
