import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import { dateFormat } from 'Pages/Protected/Partner/Components/Utils/utils'


let affilConstValue = {
  stageName: 'ENCODING',
  status: 'Active',
  affiliationStatus: 'In-Progress'
}

export async function createDentalClinicForm (clinicForm, affiliationType, dentistForm, setIsSuccess) {
  let dentistId = ''
  let clinicId = ''
  try {
    const dentalClinicForm = {...clinicForm}
    const affilType = {...affiliationType, ...affilConstValue}
    const newDentistForm = {...dentistForm}
    try {
      affilType.type = 'DENTIST'
      delete affilType.stageName
      const { data: { data: { id } } } = await PartnerDoctorService.createdPartnerDoctor({...affilType, ...newDentistForm})
      if (id) {
        dentalClinicForm.dentistId = id
        dentistId = id
        affilType.type = 'DENTAL_CLINIC'
        const { data: { data: { id: cId } } } = await AffiliationEncoderService.createFacilities({...affilType, ...dentalClinicForm})
        clinicId = cId
        setIsSuccess(true)
      }
    } catch (error) {
      console.log('error', error)
    }

    affilType.type = 'DENTAL_CLINIC'
    affilType.id = clinicId
    affilType.stageName = 'ENCODING'
    return { clinicId, dentistId, affilType, dentalClinicForm }
  } catch (error) {
    console.log('error', error)
  }
}

export async function updateDentalClinicAndDentist (clinicId, dentistId, affil, dentistData, clinicData, setIsSuccess) {
  const dentalClinicAffil = {...affil}
  const dentistAffil = {...affil}

  clinicData.dentistId = dentistId
  dentalClinicAffil.type = 'DENTAL_CLINIC'
  dentalClinicAffil.id = clinicId

  const dentalClinicRes = await AffiliationEncoderService.updateFacilities(clinicId, {
    ...dentalClinicAffil,
    ...clinicData
  })

  dentistAffil.type = 'DENTIST'
  dentistAffil.id = dentistId
  delete dentistAffil.stageName
  const dentistRes = await PartnerDoctorService.updatePartnerDoctor(dentistId, {
    ...dentistAffil,
    ...dentistData
  })

  try {
    dentalClinicAffil.stageName = 'ENCODING'
    const res = await Promise.all([dentalClinicRes, dentistRes])
    console.log('ressssss', res)
    setIsSuccess(true)
    return {dentalClinicAffil, dentistAffil}
  } catch (error) {
    console.log('update errorr', error)
  }
}

export async function fetchData (id, setIsloading) {
  setIsloading(true)
  try {
    const { data: { data } } = await AffiliationEncoderService.getDentalClinicById(id)
    const { affilType, form: dentalClinicData, dentistId, dentalAssociates, remarks } = mapDentalClinicData(data)
    if (dentistId) {
      const { dentistProfile: dentistData, tinStatus } = await fetchDentistData(dentistId)
      setIsloading(false)
      return {dentalClinicData, dentistData, dentalAssociates, remarks, tinStatus, dentistId, affilType}
    }
  } catch (error) {
    setIsloading(false)
    console.log('errors sa fetch', error)
  }
}

function mapDentalClinicData (res) {
  const affilType = { stageName: '', status: '', affiliationStatus: '', affiliationType: '', dateReceived: '', mno: '', type: '', id: '' }
  const form = {
    unitFlrBldg: '', // required
    bldgName: '', // required
    street: '', // required
    subdivision: '', // required
    region: '', // required
    district: '', // required
    barangay: '', // required
    city: '', // required
    zipCode: '',
    email: '',
  }
  const dentalAssociates = {
    dentalAssociates: [],
    affiliationInfos: []
  }
  const remarksData = {
    internalRemarks: '',
    externalRemarks: ''
  }
  if (res) {
    const { email, stage, status, id, type, name, mno, address, remarks, treasury } = res
    form.name = name
    form.roomNumber = res['room_number']
    form.clinicSchedule = res['clinic_schedule']
    form.natureOfPractice = res['nature_of_practice']

    form.payee = treasury['payee']
    // address
    form.district = address['district']
    form.region = address['region']
    form.street = address['street']
    form.unitFlrBldg = address['unit_flr_bldg']
    form.bldgName = address['building_name']
    form.city = address['city']
    form.barangay = address['barangay']
    form.subdivision = address['subdivision']
    form.zipCode = address['zip_code']
    dentalAssociates.affiliationInfos = res['affiliation_infos'] ? [...res['affiliation_infos']] : []
    dentalAssociates.dentalAssociates = res['dental_associates'] ? [...res['dental_associates']] : []
    form.dentistId = res['dentist_id']

    // contact
    form.email = email || ''
    form.contactPerson = res['contact_person'] || ''
    form.contactPersonNo = res['contact_person_no'] || ''
    remarksData.internalRemarks = remarks['internal_remarks']
    remarksData.externalRemarks = remarks['external_remarks']

    // affiliation type
    affilType.status = status
    affilType.mno = mno
    affilType.type = type
    affilType.id = id
    affilType.affiliationStatus = res['affiliation_status'] ? res['affiliation_status']['name'] : ''
    affilType.stageName = stage.stage_name
    affilType.status = status
    affilType.dateSubmitted = stage.date_submitted
  }

  return {
    affilType,
    form,
    dentistId: res.dentist_id ? res.dentist_id : null,
    remarks: remarksData,
    dentalAssociates
  }
}

async function fetchDentistData (id) {
  try {
    const { data: { data } } = await PartnerDoctorService.getPartnerDoctorById(id)
    return mapData(data)
  } catch (error) {
    console.log('error dentist', error)
  }
}

function mapData (res) {
  let dentistProfile = {
    firstName: '', // required
    middleName: '', // done
    lastName: '', // done
    suffix: '', // done
    birthDate: '',
    civilStatus: '', // done
    gender: '', // done
    emailAddress: '',
    contactNumber: '',
    mainSpecialization: '', // required done
    majorSpecialization: '', // required done
    otherSpecialization: '', // required done
    subSpecialization: '', // required done
    pda: '',
    prcNumber: '',
    prcExpiration: '', // required
    philhealthNumber: '', // done
    philhealthExpiration: '', // done
    tinNumber: '', // done
    id: ''
  }
  const { philhealth, gender, name, specialization, birthdate, license, treasury, id } = res
  const tinStatus = {}
  dentistProfile.philhealthNumber = philhealth['accreditation_number'] ? philhealth['accreditation_number'].toString() : ''
  dentistProfile.philhealthExpiration = philhealth['expiration_date'] ? dateFormat(philhealth['expiration_date'], '') : ''
  dentistProfile.mainSpecialization = specialization['main'] ? specialization['main'] : ''
  dentistProfile.majorSpecialization = specialization['major'] ? specialization['major'] : ''
  dentistProfile.otherSpecialization = specialization['other'] ? specialization['other'] : ''
  dentistProfile.subSpecialization = specialization['sub'] ? specialization['sub'] : ''
  dentistProfile.firstName = name['first'] ? name['first'] : ''
  dentistProfile.lastName = name['last'] ? name['last'] : ''
  dentistProfile.middleName = name['middle'] ? name['middle'] : ''
  dentistProfile.suffix = name['suffix'] ? name['suffix'] : ''
  dentistProfile.gender = gender
  dentistProfile.civilStatus = res['civil_status']
  dentistProfile.prcNumber = license['prc'] ? license['prc']['number'] : ''
  dentistProfile.id = id
  dentistProfile.pda = license['pda'] ? license['pda'] : ''
  dentistProfile.prcExpiration = license['prc'] ? dateFormat(license['prc']['expiration_date'], '') : ''
  dentistProfile.tinNumber = treasury['tin'] ? treasury['tin'] : ''
  dentistProfile.emailAddress = res['email_address']
  dentistProfile.contactNumber = res['contact_number']
  dentistProfile.birthDate = birthdate ? dateFormat(birthdate, '') : ''
  tinStatus.tinStatus = treasury['tin_status'] ? treasury['tin_status'] : ''
  tinStatus.vatTaxRate = treasury['vat'] ? treasury['vat']['tax_rate'] : ''
  tinStatus.nonVatable = treasury['non_vatable'] ? treasury['non_vatable'] : ''

  return {
    dentistProfile,
    tinStatus
  }
}
