import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { Provider } from 'react-redux'

import ProfileList from '../../Components/ProfileList/ProfileList'
import Partner from './Pages/PartnerInformation'
import ContactDetailsViewer from './Pages/ViewerContract/ContractDetailsViewer'
import ListOfAffiliatedFacility from '../Components/ListOfAffiliatedFacility/ListOfAffiliatedFacility'
import DentistList from '../../../Partner/ProfileMaintainer/MedicalDentistProfile/Pages/DentistList/DentistList'
import ContactDetails from '../../Components/ContactDetails/ContactDetails'
import OtherDocuments from './Pages/OtherDocuments/OtherDocuments'
import placeholderImage from '../../Assets/Icons/placeHolderPicture.png'
import DentistParamsContext from './Pages/Context/DentistParamsContext'
import Store from '../../../Store'
import { getNestedObjects } from '../../Components/Utils/utils'

export default function MedicalDentistProfile () {
  const [isOpenAffiliatedDoctorsModal, setIsOpenAffiliatedDoctorsModal] = React.useState(false)
  const [isOpenAffiliatedFacilityModal, setIsOpenAffiliatedFacilityModal] = React.useState(false)
  const [paramId, setParamId] = React.useState('')
  const [afnNumber, setAfnNumber] = React.useState('')
  const [dentistName, setDentistName] = React.useState('')
  const [currentStatus, setCurrentStatus] = React.useState('')
  const [headerData, setHeaderData] = React.useState({
    status: '',
    name: '',
    fullName: ''
  })
  const [changeName, setChangeName] = React.useState(null)
  const [isEdit, setIsEdit] = React.useState(false)
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isEditContact, setIsEditContact] = React.useState(false)

  const breadCrumbsWithEditPartner = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    dentistPartnerProfile: {
      text: 'MEDICAL DENTIST PARTNER PROFILE',
      to: '/medicalDentist'
    },
    EditPartnerInfo: {
      text: 'EDIT PARTNER INFORMATION',
      to: '/editPartnerInformation'
    },
    Doctor: {
      text: headerData.fullName,
      to: '/Doctor'
    }
  }

  const breadCrumbsWithEditContact = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    dentistPartnerProfile: {
      text: 'MEDICAL DENTIST PARTNER PROFILE',
      to: '/medicalDentist'
    },
    EditContactDetails: {
      text: 'EDIT CONTACT DETAILS',
      to: '/editContactDetails'
    },
    Doctor: {
      text: headerData.fullName,
      to: '/Dentist'
    }
  }

  const breadcrumbs = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    dentistPartnerProfile: {
      text: 'MEDICAL DENTIST PARTNER PROFILE',
      to: '/medicalDentist'
    },
    Doctor: {
      text: headerData.fullName,
      to: '/Dentist'
    }
  }

  React.useEffect(() => {
    ShowBreadCrumbs(BreadcrumbsService)
  }, [headerData.name, paramId, dentistName, currentStatus, isToUpdate, isEditContact])

  const ShowBreadCrumbs = BreadcrumbsService => {
    if (isToUpdate) {
      BreadcrumbsService.setItems(breadCrumbsWithEditPartner)
      BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'dentistPartnerProfile', 'EditPartnerInfo', 'Doctor'])
    } else if (isEditContact) {
      BreadcrumbsService.setItems(breadCrumbsWithEditContact)
      BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'dentistPartnerProfile', 'EditContactDetails', 'Doctor'])
    } else {
      BreadcrumbsService.setItems(breadcrumbs)
      BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'dentistPartnerProfile', 'Doctor'])
    }
  }

  const HandleClick = () => {
    setIsToUpdate(false)
    setIsEditContact(false)
  }

  const row = [
    {
      to: `/MedicalDentist/PartnerInformation/${paramId}`,
      name: ' Partner Information',
      path: '/MedicalDentist/PartnerInformation/:dentistId',
      click: () => setIsEditContact(false),
      importPage: (
        <DentistParamsContext.Provider
          value={{
            setParamId: setParamId,
            paramId,
            isToUpdate,
            setIsToUpdate,
            setChangeName,
            setHeaderData,
            headerData,
            isEdit,
            setIsEdit
          }}
        >
          <Partner setDentistName={setDentistName} setCurrentStatus={setCurrentStatus} setAfnNumber={setAfnNumber} />
        </DentistParamsContext.Provider>
      )
    },
    {
      to: `/MedicalDentist/ContractDetails/${paramId}`,
      name: 'Contract Details',
      path: '/MedicalDentist/ContractDetails/:paramId',
      importPage: <ContactDetailsViewer paramId={paramId} />,
      click: () => HandleClick()
    },
    {
      to: `/MedicalDentist/ListOfAffiliateFacility/${paramId}`,
      name: 'List Of Affiliated Facility',
      path: '/MedicalDentist/ListOfAffiliatedFacility/:facilityId',
      click: () => HandleClick(),
      open: setIsOpenAffiliatedFacilityModal
    },
    {
      to: `/MedicalDentist/ContactDetails/${paramId}`,
      name: 'Contact Details',
      path: '/MedicalDentist/ContactDetails/:dentistId',
      importPage: <ContactDetails paramId={paramId} type={'DENTIST'} setIsEditContact={setIsEditContact} />,
      click: () => HandleClick()
    },
    {
      to: `/MedicalDentist/OtherDocuments/${paramId}`,
      name: 'Other Documents',
      path: '/MedicalDentist/OtherDocuments/:paramId',
      importPage: <OtherDocuments paramId={afnNumber} />,
      click: () => HandleClick()
    }
  ]

  return (
    <Provider store={Store}>
      <ProfileList
        ListItems={row}
        HeaderTitle={isToUpdate ? changeName : headerData.name}
        editName={isToUpdate ? !getNestedObjects(changeName, ['props', 'children', '1', '_store', 'validated']) : false}
        leftSideImage={placeholderImage}
        status={currentStatus}
      />
      {isOpenAffiliatedDoctorsModal && (
        <DentistList open={isOpenAffiliatedDoctorsModal} setOpen={setIsOpenAffiliatedDoctorsModal} />
      )}

      {isOpenAffiliatedFacilityModal && (
        <ListOfAffiliatedFacility
          open={isOpenAffiliatedFacilityModal}
          setOpen={setIsOpenAffiliatedFacilityModal}
          // setPartnerInformationDentist={setIsToUpdate}
          // setIsEditContact={setIsEditContact}
          paramId={paramId}
          endPoint={'/affiliatedFacility'}
        />
      )}
    </Provider>
  )
}
