
export default function useInputHook (ref, onChange = null, regex = /[^a-zA-Z0-9 ]/g) {

  const onChangeInput = ({target: {name, value}}) => {
    console.log('regex', regex)
    let str = value.replace(regex, '')
    ref.current.value = str
    onChange({
      target: {
        name,
        value: str
      }
    })
  }

  return [onChangeInput]
}
