import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import Paper from '@material-ui/core/Paper'
import MomentUtils from '@date-io/moment'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import useDebounce from '../CustomHooks/useDebounce'

import classes from './PartnerHeaderCell.module.scss'

const PartnerDateHeaderCell = props => {
  // Control Props
  const { enableSort = true, enableAutoComplete = true, field = {}, width = {}, value = null } = props
  // Type Props
  const { headerTitle, keyProp = '' } = props
  // Getter Props
  const { onChangeValue = () => {}, onClickSort = () => {} } = props
  const [isSort, setIsSort] = React.useState(true)

  React.useEffect(() => {
    onClickSort(field, isSort)
  }, [isSort])

  const onChangeAutoCompleteValue = (filter, val) => {
    onChangeValue(val)
  }

  const onHandleClickSort = () => {
    setIsSort(!isSort)
  }

  return (
    <React.Fragment>
      <TableCell key={`partnerHeaderCell_${keyProp}`} style={width} className={classes.tableHeaderCell}>
        <div className={classes.headerFields}>
          <span className={classes.tableHeaderTitle}>{headerTitle}</span>
          {enableSort && (
            <FontAwesomeIcon icon='sort' className={classes.tableHeaderSortIcon} onClick={onHandleClickSort} />
          )}
        </div>
        {enableAutoComplete && (
          <Paper>
            <MuiPickersUtilsProvider utils={MomentUtils}>
              <KeyboardDatePicker
                clearable
                allowKeyboardControl={false}
                format='MM/DD/YYYY'
                placeholder='mm/dd/yyyy'
                InputProps={{
                  disableUnderline: true,
                  readOnly: true,
                  style: { paddingLeft: '10px' }
                }}
                value={value[field]}
                onChange={date => onChangeAutoCompleteValue(field, date)}
                KeyboardButtonProps={{
                  'aria-label': 'change date'
                }}
                keyboardIcon={<FontAwesomeIcon icon='calendar-day' style={{ fontSize: '1rem' }} />}
              />
            </MuiPickersUtilsProvider>
          </Paper>
        )}
      </TableCell>
    </React.Fragment>
  )
}

export default PartnerDateHeaderCell
