import React, { useState, memo, useMemo, useEffect } from 'react'

// Shared Components
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import AddressDrowpdown from 'Pages/Protected/Partner/Components/AddressDrowpdown'
import MedicalSectionThreeValidation from './validation'
import { useSelector } from 'react-redux'

// Styles
import classes from './sectionThree.module.scss'

const MedicalClinicSectionThree = props => {
  const { regions, cities: newCities, barangays: newBarangays, provinces: newProvinces } =  useSelector(state => state.addressReducer)
  const { isSubmitted = false, isValidate, setData, data, setIsValid, defaultVal } = props
  const [errors, setErrors] = useState({})
  const [form, setForm] = useState({})

  const [isFetching, setIsFetching] = React.useState({})
  const [provinces, setProvinces] = React.useState([...newProvinces])
  const [cities, setCities] = React.useState([...newCities])
  const [barangays, setBarangays] = React.useState([...newBarangays])


  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    let nForm = { ...form }
    setForm(Object.assign(nForm, defaultVal))
    // fetchAddress(defaultVal)
  }, [defaultVal])

  //  const fetchAddress = async data => {
  //    (await utils()).fetchAddress(data, setBarangays, setCities, setProvinces, setFetching)
  //  }

  const isValidForm = useMemo(() => {
    const { isValid } = MedicalSectionThreeValidation(form)
    console.log('is this valid', isValid)
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form])

  const validate = () => {
    const { errors, isValid } = MedicalSectionThreeValidation(form)
    console.log('errors', errors)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setErrors(Object.assign( nErrors, errors))
      return false
    }
  }

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setErrors(e)
    setForm(nform)
  }

  const utils = async () => {
    return await import('Pages/Protected/Partner/Components/Utils/utils')
  }


  const handleAddressChainingDropdown = ({ target: {name, value} }) => {
    const dentalClinic = { ...form }
    dentalClinic[name] = value
    delete errors[name]
    setErrors(errors)
    setForm(dentalClinic)
    callHandleAddressChaining(name, value)
  }

  const callHandleAddressChaining = async (name, value) => {
    (await utils()).handleAddressChaining(name, value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }

  const fetchBarangays = async brgy => await (await utils()).fetchBarangays(brgy)

  const fetchCities = async city => await (await utils()).fetchCities(city)

  const fetchProvince = async prov => await (await utils()).fetchProvinces(prov)

  return (
      <div className={classes.secondPartInputFields}>
      <div id='firstInputLayerFields' className={classes.firstInputLayerFields}>
        <div id='unitFlrBldg'>
          <label>
          Medical Clinic Address <br />
            Unit No./Floor/Bldg.No.
          </label>
          <TextField
            disabled={isSubmitted}
            id='unitFlrBldg'
            name='unitFlrBldg'
            type={'text'}
            onChange={handleOnChange}
            defaultValue={form['unitFlrBldg']}
          />
        </div>
        <div>
          <label>
            <div /> <br /> Building Name
          </label>
          <TextField
            disabled={isSubmitted}
            id='bldgName'
            name='bldgName'
            type={'text'}
            onChange={handleOnChange}
            defaultValue={form['bldgName']}
          />
        </div>
        <div>
          <label>
            <div /> <br /> Street Name *
          </label>
          <TextField
            disabled={isSubmitted}
            id='street'
            name='street'
            error={errors['street']}
            type={'text'}
            onChange={handleOnChange}
            defaultValue={form['street']}
          />
        </div>
      </div>
      <div id='secondInputLayerFields' className={classes.secondInputLayerFields}>
        <div>
          <label>Subdivision *</label>
          <TextField
            disabled={isSubmitted}
            id='subdivision'
            name='subdivision'
            error={errors['subdivision']}
            type='text'
            onChange={handleOnChange}
            defaultValue={form['subdivision']}
          />
        </div>
        <div>
          <label>Region *</label>

          <AddressDrowpdown
            disabled={isSubmitted}
            id='region'
            items={regions}
            value={form['region'] || ''}
            error={errors['region']}
            name='region'
            onChange={handleAddressChainingDropdown}
          />
        </div>
        <div>
          <label>District / Province *</label>
          {isFetching['province'] && <span className={classes['fetching-indicator']}> fetching...</span>}
          <AddressDrowpdown
            disableComponents={isSubmitted ||(provinces instanceof Array && provinces.length <= 0) || isFetching['province']}
            id='district'
            items={provinces}
            value={form['district']}
            error={errors['district']}
            name='district'
            onChange={handleAddressChainingDropdown}
          />
        </div>
      </div>
      <div id='thirdInputLayerFields' className={classes.thirdInputLayerFields}>
        <div>
          <label>Barangay *</label>
          {isFetching['barangay'] && <span className={classes['fetching-indicator']}> fetching... </span>}
          <AddressDrowpdown
            disableComponents={isSubmitted || (barangays instanceof Array && barangays.length <= 0) || isFetching['barangay']}
            id='barangay'
            name='barangay'
            value={form['barangay']}
            error={errors['barangay']}
            items={barangays}
            onChange={handleAddressChainingDropdown}
          />
        </div>
        <div>
          <label>City / Municipality *</label>
          {isFetching['city'] && <span className={classes['fetching-indicator']} > fetching... </span>}
          <AddressDrowpdown
            disableComponents={isSubmitted | (cities instanceof Array && cities.length <= 0) || isFetching['city']}
            id='city'
            items={cities}
            value={form['city']}
            name='city'
            error={errors['city']}
            onChange={handleAddressChainingDropdown}
          />
        </div>
        <div>
          <label>Zip Code *</label>
          <TextField
            disabled={isSubmitted}
            id='zipCode'
            name='zipCode'
            error={errors['zipCode']}
            styled={{fontSize: 10}}
            type={'noSpecialChar'}
            onChange={handleOnChange}
            inputProps={{ maxLength: 4 }}
            defaultValue={form['zipCode']}
          />
        </div>
      </div>
      <div id='clinicScheduleAndRoomNumber' className={classes.clinicScheduleAndRoomNumber}>
        <div id='clinicSchedule'>
          <label id='clinicScheduleLabel'>Clinic Schedule *</label>
          <TextField
            disabled={isSubmitted}
            id='clinicSchedule'
            name='clinicSchedule'
            error={errors['clinicSchedule']}
            type={'text'}
            onChange={handleOnChange}
            defaultValue={form['clinicSchedule']}
          />
        </div>
        <div id='roomNumber'>
          <label id='roomNumberlabel'>Room Number *</label>
          <TextField
            disabled={isSubmitted}
            id='roomNumber'
            name='roomNumber'
            error={errors['roomNumber']}
            type={'text'}
            onChange={handleOnChange}
            defaultValue={form['roomNumber']}
          />
        </div>
      </div>
    </div>
  )
}

export default memo(MedicalClinicSectionThree)
