const onPasteInputHook = (regex = /\w+/) => {
  const handleOnPaste = e => {
    const { clipboardData } = e
    let newVal = clipboardData.getData('Text')
    if (!regex.test(newVal)) {
      e.preventDefault()
      return true
    }
  }

  return [handleOnPaste]
}

export default onPasteInputHook
