const RenderDropDownField = props => {
  const { name, value, onChange, items, error } = props
  let itemData = items === undefined ? [] : items

  return (
    <React.Fragment>
      <NativeSelect
        name={name}
        value={value ? value.toUpperCase() : ''}
        onChange={onChange}
        className={error ? classes.selectInputError : classes.selectInput}
      >
        <option value='' />
        {itemData.map((item, index) => (
          <option key={index} value={item.value ? item.value.toUpperCase() : ''}>
            {item.text}
          </option>
        ))}
      </NativeSelect>
      <div className={classes.error}> {error && error}</div>
    </React.Fragment>
  )
}

const RenderDropDownWithLabelField = props => {
  const [trigger, setTrigger] = React.useState(false)

  const { name, value, onChange, items, error, label, disabled, addressFetch } = props
  let itemData = typeof items === 'undefined' ? [] : items

  React.useEffect(() => {
    fetchData()
  }, [itemData])

  const fetchData = async () => {
    if (await itemData.length === 0) {
      setTrigger(true)
    } else {
      setTrigger(false)
    }
  }

  const checkIfDisabled = () => {
    return addressFetch ? false : trigger ? true : disabled
  }

  return (
    <div id='dropDownwithLabel' className={classes.dropDownwithLabel}>
      <label>{`${label}`}</label>
      <NativeSelect
        name={name}
        value={value}
        onChange={onChange}
        disabled={checkIfDisabled()}
        className={error ? classes.selectInputError : checkIfDisabled() ? classes.selectDisable : classes.selectInput}
      >
        <option value='' />
        {addressFetch
          ? <option value='Loading...'>{'Loading...'}</option>
          : trigger ? <option value='' /> : itemData.map((item, index) => (
            <option key={index} id='options' value={item.value} className={classes.options}>
              {item.text}
            </option>
          ))
        }
      </NativeSelect>
      <div className={classes.error}> {error && error}</div>
    </div>
  )
}

const RenderInputWithlabelFields = props => {
  const { name, value, onChange, error, label, inputRef, type } = props

  const zipCodeLength = () => {
    if (name === 'zipCode') {
      return 4
    }
    return null
  }

  return (
    <div id='RenderInputWithlabelFields' className={classes.dropDownwithLabel}>
      <label>{label}</label>
      <InputBase
        value={value}
        name={name}
        type={type}
        inputRef={inputRef}
        onChange={onChange}
        inputProps={{maxLength: zipCodeLength()}}
        className={error ? classes.selectInputError : classes.selectInput}
        autoComplete='off'
        onKeyDown={e => DisableCharacters(e, type)}
      />
      <div className={classes.error}> {error && error}</div>
    </div>
  )
}

