export function dateFormat (date = new Date(), format = 'MM/DD/YYYY') {
  if (!date) return ''
  return Moment(date).format(format)
}

export function ISOformat (date) {
  if (date === null || date === 'Invalid Date' || date === undefined) return ''
  return Moment(date).format()
}

export function dateFormatChecker (date, format = 'MM/DD/YYYY') {
  return Moment(date, format, true).isValid()
}

export function MonthdateFormat (date) {
  if (
    date === '7000-01-01T00:00:00.000Z' ||
    date === '1970-01-01T00:00:07.000Z' ||
    date === '1970-01-01T00:00:00.000Z' ||
    date === 'Invalid date' ||
    date === undefined ||
    date === 'undefined' ||
    date === null ||
    !isNaN(date) ||
    date.length === 0
  ) {
    return ''
  }
  return Moment(date).format('L')
}

export function YearDateFormat (date) {
  if (
    date === '7000-01-01T00:00:00.000Z' ||
    date === '1970-01-01T00:00:07.000Z' ||
    date === '1970-01-01T00:00:00.000Z' ||
    date === 'Invalid date' ||
    date === undefined ||
    date === 'undefined' ||
    date === null ||
    !isNaN(date) ||
    date.length === 0
  ) {
    return ''
  }
  return Moment(date).format('YYYY-MM-DD')
}

export function DateErrorHandler (date) {
  if (date === null || date === 'Invalid date' || date === undefined || date.length === 0 || date === 'undefined') return 7000
  return Moment(date).format('YYYY-MM-DD')
}

export function validateEmail (email, name, setErrors, errors) {
  let emailPattern = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)
  if (email && !emailPattern) {
    errors[name] = 'Invalid email format'
    setErrors({ ...errors })
  }
}

export function validateMobileNo (mobileNo, name, setErrors, errors) {
  let mobileNoPattern = /^(09|\+639)\d{9}$/.test(mobileNo)
  if (mobileNo && !mobileNoPattern) {
    errors[name] = 'Invalid mobile number format'
    setErrors({ ...errors })
  }
}

export function getTinStatusValue (data) {
  if (data === '12%') {
    return '12%'
  } else if (data.trim() === '') {
    return ''
  } else if (data.trim().length === 0) {
    return ''
  } else if (data === 'Non-VaTable') {
    return 'Non-VATable'
  } else {
    return 'Non-VATable'
  }
}

export function getTinStatus (data) {
  if (data === '12%') {
    return 'VATable'
  } else if (data.trim() === '') {
    return ''
  } else if (data.trim().length === 0) {
    return ''
  } else if (data === 'Non-VaTable') {
    return 'Non-VATable'
  } else {
    return 'Non-VATable'
  }
}

export const getNestedObjects = (nestedObj, pathArr) => {
  return pathArr.reduce((obj, key) => (obj ? obj && obj[key] !== 'undefined' ? (obj[key] ? obj[key] : '') : '' : ''), nestedObj)
}

export const getNestedArrayOfObjects = (nestedObj, pathArr) => {
  return pathArr.reduce((obj, key) => (obj ? obj && obj[key] !== 'undefined' ? (obj[key] ? obj[key] : []) : [] : []), nestedObj)
}

export function formatTinNumber (str) {
  let res = str.match(/(\d{3})(\d{3})(\d{3})(\d{3})/)
  return res ? res.splice(1, 4).join('-') : ''
}

export function formatPhilHealthNumber (str) {
  let res = str.match(/(\d{4})(\d{7})(\d{1})/)
  return res ? res.splice(1, 4).join('-') : ''
}

export function checkArray (data) {
  if (data === undefined || typeof data === 'undefined' || data.length === 0) {
    return []
  }

  return data
}

export function newArray (data) {
  if (data === undefined || typeof data === 'undefined' || data.length === 0) {
    return []
  }

  const newData = data.map(items => {
    return {
      id: items._id,
      text: items.name,
      value: items.name
    }
  })

  return newData
}

export function CompareTwoArray (arr1, arr2) {
  if (arr1.length !== arr2.length) return false

  for (var i = 0; i < arr1.length; i++) {
    if (arr1[i] !== arr2[i]) return false
  }

  return true
}

export function CompareFacilityAddress (prevProps, nextProps, valueToCompare) {
  const PrevregionError = getNestedObjects(prevProps, ['data', '2', 'value', 'props', `${valueToCompare[0]}`])
  const NextregionError = getNestedObjects(nextProps, ['data', '2', 'value', 'props', `${valueToCompare[0]}`])
  const PrevregionItems = getNestedObjects(prevProps, ['data', '2', 'value', 'props', `${valueToCompare[1]}`])
  const NextregionItems = getNestedObjects(nextProps, ['data', '2', 'value', 'props', `${valueToCompare[1]}`])
  const PrevRegionValue = getNestedObjects(prevProps, ['data', '2', 'value', 'props', `${valueToCompare[2]}`])
  const NextRegionValue = getNestedObjects(nextProps, ['data', '2', 'value', 'props', `${valueToCompare[2]}`])

  if (PrevregionError === NextregionError && PrevregionItems === NextregionItems && PrevRegionValue === NextRegionValue) {
    return true
  }
  return false
}

export function facilityDiscountCompareProps (prevProps, nextProps) {
  const PrevFacilityDiscount = prevProps.data.filter(items => items.title === 'FACILITY discount')
  const NexFacilityDiscount = nextProps.data.filter(items => items.title === 'FACILITY discount')

  const PrevFormValue = getNestedObjects(PrevFacilityDiscount, ['0', 'value', 'props', 'formValue'])
  const NextFormValue = getNestedObjects(NexFacilityDiscount, ['0', 'value', 'props', 'formValue'])

  const PrevData = getNestedObjects(PrevFacilityDiscount, ['0', 'value', 'props', 'data'])
  const NextData = getNestedObjects(NexFacilityDiscount, ['0', 'value', 'props', 'data'])

  if (PrevFormValue === NextFormValue && PrevData === NextData) {
    return true
  }
  return false
}

export async function handleAddressChaining (name, value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching) {
  let nForm = { ...form }
  const fetching = { ...isFetching }
  if (name === 'region') {
    nForm['region'] = value
    nForm['district'] = ''
    nForm['city'] = ''
    nForm['barangay'] = ''
    setBarangays([])
    setCities([])
    setForm(nForm)
    fetching['province'] = true
    setIsFetching(fetching)
    try {
      const province = await AddressChaining.fetchAddress({ category: 'province', field: 'region', value })
      fetching['province'] = false
      setIsFetching(fetching)
      setProvinces(province || [])
    } catch (error) {
      console.log('error occured', error)
      fetching['province'] = false
      setIsFetching(fetching)
    }
  }
  if (name === 'district') {
    nForm['district'] = value
    nForm['city'] = ''
    nForm['barangay'] = ''
    setBarangays([])
    setCities([])
    setForm(nForm)
    fetching['city'] = true
    setIsFetching(fetching)
    try {
      const cities = await AddressChaining.fetchAddress({category: 'city', field: 'province', value})
      fetching['city'] = false
      setCities(cities || [])
    } catch (error) {
      console.log('error occured', error)
      fetching['city'] = false
      setIsFetching(fetching)
    }
  }
  if (name === 'city') {
    nForm['city'] = value
    nForm['barangay'] = ''
    setForm(nForm)
    fetching['barangay'] = true
    setIsFetching(fetching)
    try {
      const barangays = await AddressChaining.fetchAddress({category: 'barangay', field: 'city', value})
      fetching['barangay'] = false
      setBarangays(barangays || [])
      setIsFetching(fetching)
    } catch (error) {
      console.log('error occured', error)
      fetching['barangay'] = false
      setIsFetching(fetching)
    }
  }
}

export async function fetchAddress (form, setBarangays, setCities, setProvinces, setFetching, setAddressFetch) {
  setFetching(true)
  setAddressFetch(true)
  try {
    const cities = await AddressChaining.fetchAddress({category: 'city', field: 'province', value: form['district']})
    const barangays = await AddressChaining.fetchAddress({category: 'barangay', field: 'city', value: form['city']})
    const province = await AddressChaining.fetchAddress({ category: 'province', field: 'region', value: form['region'] })
    const res = await Promise.all([province, cities, barangays])
    setFetching(false)
    setAddressFetch(false)
    setProvinces(res ? res[0] : [])
    setCities(res ? res[1] : [])
    setBarangays(res ? res[2] : [])
  } catch (error) {
    setProvinces([])
    setCities([])
    setBarangays([])
    setFetching(false)
    setAddressFetch(false)
    console.log('error in fecth address', error)
  }
}

