export async function fetchRegion (setRegions, isToModify = false, category = 'region') {
  let regions = []
  try {
    regions = await AddressChaining.fetchAddress({ category })
    if (isToModify) {
      if (regions instanceof Array) {
        let newRegions = regions.map(region => {
          return {
            id: region._id,
            text: region.name,
            value: region.name
          }
        })
        console.log('new regions', newRegions)
        setRegions(newRegions)
        return newRegions
      } else {
        setRegions([])
      }
    } else {
      setRegions(regions)
    }
  } catch (error) {
    setRegions([])
    console.log('error in fetch region', error)
  }
}

