import React from 'react'

// Components
import history from '../../../../Components/History/History'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
// Styles
import classes from './MedicalInformation.module.scss'
// Validator


// Constant
import MedicalClinicPageOne from './MedicalClinicPageOne/MedicalClinicPageOne'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  MedicalClinic: {
    text: 'MEDICAL CLINIC AFFILIATION',
    to: '/medical-clinic-affiliation-form'
  }
}

let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']

let headerTitle = 'Medical Clinic Affiliation Form'

const MedicalClinicAffiliation = ({history}) => {

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'MedicalClinic'])
  }, [])

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbs
        id='HeaderWithBreadCrumbs'
        HeaderTitle={headerTitle}
        stepDetails={stepDetails}
        currentStep={1}
      />
      <div id='AffiliationForm-div'>
        <MedicalClinicPageOne  history={history}/>
      </div>
    </div>
  )
}

export default MedicalClinicAffiliation
