import React from 'react'
import Paper from '@material-ui/core/Paper'

import Button from '@material-ui/core/Button'
import classes from './FacilityInformation.module.scss'
import ConfirmationDialog from '../FooterConfirmationDialog'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import { useSelector } from 'react-redux'

const FacilityInformation = (props) => {
  const {
    uploadFileTable,
    dialogText,
    handleProceed,
    isSuccess,
    setIsSuccess,
    save,
    isSubmitted,
    isOpen,
    disable,
    setDisable,
    isClickedSave,
    saveButtonDisabled
  } = props
  const { affiliationType } = useSelector(state => state.affiliationType)
  const PrevPage = (e) => {
    e.preventDefault()
    props.history.goBack()
  }

  React.useEffect(() => {
    console.log('affilaiaton type', affiliationType)
    if (affiliationType['stageName'] === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setDisable(true)
    }
  }, [affiliationType['stageName']])

  return (
    <React.Fragment>
      <CustomAlerSnackBar isSave={isSuccess} setIsSave={setIsSuccess} />
      <div id='bodyContainer' className={classes.bodyContainer}>
        <div id='textTitle' className={classes['text-title']}>
          <text>APPLICATION REQUIREMENTS</text>
        </div>
        <div id='tableContainer' className={classes.tableContainer}>
          <div id='uploadFileTable' className={classes.uploadFileTable}>
            <label id='tableTopText' className={classes.tableTopText}>
              <text>*Please Provide the following documents in order for your application to be processed.</text>
            </label>
            <Paper id='tableBox' className={classes.tableBox}>
              <div id='documentDesriptionText' className={classes.documentDesriptionText}>
                Document Description
              </div>
              <Paper id='uploadingOfDocuments' className={classes.uploadingOfDocuments}>
                {uploadFileTable}
              </Paper>
            </Paper>
          </div>
        </div>
        <div id='buttonContainer' className={classes.buttonContainer}>
          <div id='buttonDiv' className={classes.buttonDiv}>
            <div id='buttonBox' className={classes.buttonBox}>
              <div id='firstChild'>
                <Button variant='contained' className={classes.ReturnButton} onClick={PrevPage}>
                  Return
                </Button>
              </div>
              <div id='secondChild'>
                <Button variant='contained' disabled={saveButtonDisabled} className={saveButtonDisabled ? classes.disabledButton : classes.SaveButton} onClick={save}>
                  <div style={{ fontSize: '12px' }}> Save</div>
                </Button>
              </div>
              <div id='thirdChild'>
                <ConfirmationDialog dialogText={dialogText} handleProceed={handleProceed} isSubmitted={isSubmitted} isOpen={isOpen} disable={disable} setDisable={setDisable} />
              </div>
            </div>
          </div>
        </div>
        <div id='footerDivision'>
          <label id='footer' className={classes.Footer}>
            FACILITY AFFILIATION FORM 2 out of 2
          </label>
        </div>
      </div>
    </React.Fragment>
  )
}

export default FacilityInformation
