import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'unitFlrBldg',
  'bldgName'
]

const schema = Joi.object({
  street: Joi.string()
    .label('Street')
    .required(), // required
  subdivision: Joi.string()
    .label('Subdivision')
    .required(), // required
  region: Joi.string()
    .label('Region')
    .required(), // required
  district: Joi.string()
    .label('District')
    .required(), // required
  barangay: Joi.string()
    .label('Barangay')
    .required(), // required
  city: Joi.string()
    .label('City')
    .required(), // required
  zipCode: Joi.string()
    .label('Zip code')
    .required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  roomNumber: Joi.string()
    .label('Room Number')
    .required() // required
})
export default function MedicalSectionThreeValidation (form) {
  let errors = {}
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}

