import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'

import classes from './Confirmation.module.scss'

const FooterDialogConfirmation = (props) => {
  const { dialogText, handleProceed, isSubmitted, disable, setDisable } = props
  const [open, setOpen] = React.useState(false)
  React.useEffect(() => setOpen(false), [isSubmitted])
  const handleProceedDialog = () => {
    setOpen(false)
    if (setDisable) {
      setDisable(true)
    }
    handleProceed()
  }
  return (
    <React.Fragment>
      <Button
        disabled={disable}
        style={{ backgroundColor: disable ? '#dddfe3' : '#3ab77d', color: 'white', paddingLeft: '1.5em', paddingRight: '1.5em', textTransform: 'capitalize' }}
        className={disable ? classes.disabledButton : classes.submitButton}
        variant='contained'
        onClick={() => {
          setOpen(true)
        }}
      >
        Submit
      </Button>
      <CustomDialog id='ga_custom_dialog' isCancel={open} setIsCancel={() => { setOpen(false) }}>
        <div id='ga_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='ga_custom_dialog_message' style={{ textAlign: 'center' }}>
            Are you sure you want to proceed with the submission of {dialogText} form for approval?
          </h3>
          <div id='ga_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              style={{ textTransform: 'none' }}
              onClick={() => {
                setOpen(false)
              }}
              id='ga_custom_dialog_btn_cancel'
              className={classes.btnRed}
              variant='contained'
            >
              Cancel
            </Button>
            <Button
              style={{ textTransform: 'none' }}
              id='ga_custom_dialog_btn_save'
              className={classes.btnGreen}
              variant='contained'
              onClick={handleProceedDialog}
            >
              Proceed
            </Button>
          </div>
        </div>
      </CustomDialog>
    </React.Fragment>
  )
}

export default FooterDialogConfirmation
