import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableContainer from '@material-ui/core/TableContainer'
import Button from '@material-ui/core/Button'
import TableHead from '@material-ui/core/TableHead'
import Paper from '@material-ui/core/Paper'
import ProfileService from 'Services/Api/Partner/ProfileService'
import RoomTypeService from '../../../../../../../Services/Api/RoomType/RoomType'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import CustomAlertSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import CancelDialog from '../../../Components/CancelDialog/CancelDialog'
import SubmitDialog from '../../../Components/SubmitDialog/SubmitDialog'
import CircularProgress from '@material-ui/core/CircularProgress'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import Pagination from '@material-ui/lab/Pagination'
import Autocomplete from '@material-ui/lab/Autocomplete'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import Modal from '@material-ui/core/Modal'
import {
  updateData,
  setPageData,
  setOnEdit,
  updateRowData,
  setPreviousRecord,
  setOnSave,
  setResetState,
  copyDataToPrev,
  setBodySize,
  setSortRoomCode,
  setRoomType,
  setSortDescription,
  setInputValue,
  setValue,
  updateDataOnSave,
  getRoomTypeItems,
  getRoomItems
} from './Store/Action'

import RowComponent from './Components/RowComponent/RowComponent'

import { getNestedArrayOfObjects, getNestedObjects } from '../../../../Components/Utils/utils'

import AddRoomType from './Components/AddRoomType/AddRoomType'

import classes from './Index.module.scss'

const RoomAndBoards = props => {
  const { open, setOpen, paramId } = props

  const dispatch = useDispatch()
  const roomAndBoardState = useSelector(state => state.roomAndBoard)
  const [loaded, setIsLoaded] = React.useState(false)
  const [isCancelDialog, setIsCancelDialog] = React.useState(false)
  const [isSubmitDialog, setIsSubmitDialog] = React.useState(false)
  const [isSaveDialog, setIsSaveDialog] = React.useState(false)
  const [roomAndBoardId, setRoomAndBoardId] = React.useState('')
  const [openAddRoomType, setOpenAddRoomType] = React.useState(false)

  const fetchData = async () => {
    LoadingService.loading(true)
    try {
      const { data: appData } = await ProfileService.getRoomAndBoard(paramId)
      const {
        data: { data: roomDesc }
      } = await RoomTypeService.get()

      if (getNestedArrayOfObjects(appData, ['data', 'room_and_board']).length > 0) {
        const newData = getNestedArrayOfObjects(appData, ['data', 'room_and_board']).map(obj => {
          return {
            status: obj.status,
            roomCode: obj.roomCode,
            roomType: obj.roomType,
            description: obj.description,
            price: obj.price,
            remarks: obj.remarks,
            error: {},
            identifier: obj._id
          }
        })

        dispatch(updateData(newData))
        dispatch(
          setPageData({
            page: 1,
            size: 10
          })
        )
      }

      let arr = []

      roomDesc.map(items => {
        arr.push({
          id: getNestedObjects(items, ['_id']).trim(),
          text: `${getNestedObjects(items, ['desc']).trim()}`,
          value: `${getNestedObjects(items, ['desc']).trim()}`
        })
      })

      dispatch(getRoomTypeItems(arr))
      dispatch(getRoomItems(roomDesc))
    } catch (e) {
      console.log('error', e)
    }
    LoadingService.loading(false)
  }

  React.useEffect(() => {
    setIsLoaded(true)
    fetchData()
    setRoomAndBoardId(paramId)
    return () => {
      setOpen(false)
    }
  }, [])

  React.useEffect(() => {
    try {
      dispatch(setOnSave())
    } catch (e) {
      console.log('error', e)
    }
  }, [roomAndBoardState.data, roomAndBoardState.page])

  React.useEffect(() => {
    if (roomAndBoardState.isSaving && roomAndBoardState.isRowsValid) {
      setIsSubmitDialog(true)
    }
  }, [roomAndBoardState.isSaving, roomAndBoardState.isRowsValid])

  const handleOnSubmit = () => {
    setIsSubmitDialog(false)
    const updateServices = async (Arr, oldObj) => {
      let newObj = {}
      newObj.id = roomAndBoardId
      newObj.roomAndBoard = Arr
      LoadingService.loading(true)

      const { data: appData } = await ProfileService.updateAndCreateRoomBoard(newObj)

      if (getNestedArrayOfObjects(appData, ['data', 'room_and_board']).length > 0) {
        let newArray = []

        newArray = getNestedArrayOfObjects(appData, ['data', 'room_and_board']).map(items => {
          return {

            status: items.status,
            roomCode: items.roomCode,
            roomType: items.roomType,
            description: items.description,
            price: items.price,
            remarks: items.remarks,
            error: {},
            identifier: items._id
          }
        })

        // newArray.forEach(obj => {
        //  console.log('objeecfts', obj)
        dispatch(updateDataOnSave(newArray))
        // })

        dispatch(
          setPageData({
            page: roomAndBoardState.page,
            size: roomAndBoardState.size
          })
        )

        dispatch(copyDataToPrev())

        setIsSaveDialog(true)
        dispatch(setOnEdit(false))
      }

      LoadingService.loading(false)
    }

    if (roomAndBoardState.data.length > 0) {
      let Arr = []

      roomAndBoardState.data.forEach(obj => {
        Arr.push({
          status: obj.status,
          roomCode: obj.roomCode,
          roomType: obj.roomType,
          description: obj.description,
          price: obj.price,
          remarks: obj.remarks
        })
      })

      updateServices(Arr)
    }
    dispatch(setOnSave(false))
  }

  console.log('roomAndBoardState', roomAndBoardState)

  return (
    <React.Fragment>
      <Modal style={{ outline: 'none' }} open={open} onClose={() => setOpen(false)}>
        <div id='root ' className={classes.root} style={{ outline: 'none' }}>
          <div id='container' className={classes.container}>
            <div className={classes.tableContainer}>
              <div className={classes.TopButton}>
                {!roomAndBoardState.isEdit && <div id='Add-button' className={classes.addButtonDiv}>
                  <Button
                    className={classes.addButton}
                    onClick={() => {
                      setOpenAddRoomType(true)
                    }}
                    variant='contained'
                  >
                 Add Room Type
                  </Button>
                </div>}
                <div id='Update-button' className={classes.updateButtonDiv}>
                  <Button
                    className={classes.updateButton}
                    onClick={() => {
                      dispatch(setOnEdit(true))
                    }}
                    variant='contained'
                  >
                  Update
                  </Button>
                </div>

              </div>
              <TableContainer style={{ minHeight: 440, maxHeight: 440, backgroundColor: '#f5f7fb' }}>
                <CustomAlertSnackBar isSave={isSaveDialog} setIsSave={val => setIsSaveDialog(false)} />
                <CancelDialog
                  isCancel={isCancelDialog}
                  setIsCancel={setIsCancelDialog}
                  handleOnCancel={() => {
                    dispatch(setOnEdit(false))
                    dispatch(setPreviousRecord())
                    setIsCancelDialog(false)
                  }}
                />
                <SubmitDialog
                  isSubmit={isSubmitDialog}
                  setIsSubmit={() => {
                    setIsSubmitDialog(false)
                    dispatch(setOnSave(false))
                  }}
                  handleOnSubmit={handleOnSubmit}
                />
                <Table stickyHeader className={classes.table} size='small'>
                  <TableHead className={classes.tableHeader}>
                    <TableRow>
                      <TableCell style={{ width: '12%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Room Code</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={() => {
                              dispatch(setSortRoomCode())
                            }}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='roomAndBoardsRoomCode'
                            options={roomAndBoardState.roomCode.list}
                            getOptionLabel={option => option.roomCode}
                            onInputChange={(event, newInputValue) => {
                              dispatch(
                                setInputValue({
                                  name: 'roomCode',
                                  inputValue: newInputValue
                                })
                              )
                            }}
                            inputValue={roomAndBoardState.roomCode.inputValue}
                            value={roomAndBoardState.roomCode.value}
                            onChange={(event, newValue) => {
                              dispatch(
                                setPageData({
                                  name: 'roomCode',
                                  value: newValue || '',
                                  page: 1,
                                  size: roomAndBoardState.size
                                })
                              )
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '20%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Room Type</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={() => {
                              dispatch(setRoomType())
                            }}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='roomAndBoardsRoomType'
                            options={roomAndBoardState.roomType.list}
                            getOptionLabel={option => option.roomType}
                            onInputChange={(event, newInputValue) => {
                              dispatch(
                                setInputValue({
                                  name: 'roomType',
                                  inputValue: newInputValue
                                })
                              )
                            }}
                            inputValue={roomAndBoardState.roomType.inputValue}
                            value={roomAndBoardState.roomType.value}
                            onChange={(event, newValue) => {
                              dispatch(
                                setPageData({
                                  name: 'roomType',
                                  value: newValue || '',
                                  page: 1,
                                  size: roomAndBoardState.size
                                })
                              )
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '20%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Room Description</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={() => {
                              dispatch(setSortDescription())
                            }}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='roomAndBoardsRoomDescription'
                            options={roomAndBoardState.description.list}
                            getOptionLabel={option => option.description}
                            onInputChange={(event, newInputValue) => {
                              dispatch(
                                setInputValue({
                                  name: 'description',
                                  inputValue: newInputValue
                                })
                              )
                            }}
                            inputValue={roomAndBoardState.description.inputValue}
                            value={roomAndBoardState.description.value}
                            onChange={(event, newValue) => {
                              dispatch(
                                setPageData({
                                  name: 'description',
                                  value: newValue || '',
                                  page: 1,
                                  size: roomAndBoardState.size
                                })
                              )
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '10%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Price</span>
                      </TableCell>
                      <TableCell style={{ width: '10%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Status</span>
                      </TableCell>
                      <TableCell style={{ width: '13%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Remarks</span>
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    {!loaded && (
                      <div className={classes.loadingComponent}>
                        <CircularProgress />
                      </div>
                    )}
                    {loaded && (
                      <React.Fragment >
                        {roomAndBoardState.pageData.map((dataObj, index) => {
                          return (
                            <RowComponent
                              key={`${dataObj.id}_${index}`}
                              rowObj={dataObj}
                              pos={index}
                              isEdit={roomAndBoardState.isEdit}
                              onChange={(name, value) =>
                                dispatch(
                                  updateRowData({
                                    name: name,
                                    pos: index,
                                    value: value,
                                    identifier: dataObj.identifier,
                                    wholeDataIndex: (roomAndBoardState.size * (roomAndBoardState.page - 1) + index)
                                  })
                                )
                              }
                            />

                          )
                        })}
                      </React.Fragment>
                    )}
                  </TableBody>
                </Table>
              </TableContainer>
              <div className={classes.tablePagination}>
                <div>
                  <span>
                    {roomAndBoardState.pageData.length}-{roomAndBoardState.size} out of{' '}
                    {roomAndBoardState.data.length} Room Types
                  </span>
                </div>
                <div className={classes.options}>
                  <span style={{ marginRight: '10px' }}>
                    <p>Show</p>
                  </span>
                  <FormControl size='small' variant='outlined'>
                    <Select
                      style={{ backgroundColor: 'white' }}
                      value={roomAndBoardState.size}
                      onChange={e => dispatch(setPageData({ page: 1, size: e.target.value }))}
                    >
                      <MenuItem value={10}>10</MenuItem>
                      <MenuItem value={15}>15</MenuItem>
                      <MenuItem value={20}>20</MenuItem>
                    </Select>
                  </FormControl>
                  <Pagination
                    count={roomAndBoardState.totalPage}
                    page={roomAndBoardState.page}
                    onChange={(e, value) => dispatch(setPageData({ page: value, size: roomAndBoardState.size }))}
                    showFirstButton
                    showLastButton
                  />
                </div>
              </div>
            </div>
            <div className={classes.buttonsGroup}>
              {roomAndBoardState.isEdit ? (
                <React.Fragment>
                  <Button
                    onClick={() => {
                      setIsCancelDialog(true)
                    }}
                    id='rc_btn_cancel'
                    className={classes.btnRed}
                    variant='contained'
                  >
                    Cancel
                  </Button>
                  <Button

                    onClick={() => dispatch(setOnSave(true))}
                    id='rc_btn_cancel'
                    disabled={!roomAndBoardState.isRowsValid}
                    className={!roomAndBoardState.isRowsValid ? classes.disable : classes.btnGreen}
                    variant='contained'
                  >
                    Save
                  </Button>
                </React.Fragment>
              ) : (
                <div className={classes.footer}>
                  <Button
                    onClick={() => {
                      setOpen(false)
                    }}
                    id='rc_btn_cancel'
                    className={classes.btnRed}
                    variant='contained'
                  >
                    Close
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      </Modal>
      { openAddRoomType && <AddRoomType open={openAddRoomType} roomAndBoardId={roomAndBoardId} setOpen={setOpenAddRoomType} />}
    </React.Fragment>
  )
}

export default RoomAndBoards
