import React from 'react'
import useAsyncHook from './useAsyncHook'

const useTable = (typeFilter = '', asyncFunction = null, formatter = null) => {
  const isAttached = React.useRef(true)
  const [filter, setFilter] = React.useState({})
  const [count, setCount] = React.useState(0)
  const [page, setPage] = React.useState(1)
  const [limit, setLimit] = React.useState(10)
  const [formatData, setFormatData] = React.useState([])
  const [data, error, loading, executeAsync] = useAsyncHook(asyncFunction)

  React.useEffect(() => {
    const params = {
      tableFilter: filter,
      page: page,
      limit: limit,
      ...(typeFilter ? { typeFilter: typeFilter } : {})
    }
    if (isAttached.current) executeAsync(params)
    return () => {
      isAttached.current = false
    }
  }, [])

  React.useEffect(() => {
    const params = {
      tableFilter: filter,
      page: page,
      limit: limit,
      ...(typeFilter ? { typeFilter: typeFilter } : {})
    }
    if (isAttached.current) executeAsync(params)
  }, [page])

  React.useEffect(() => {
    const params = {
      tableFilter: filter,
      page: 1,
      limit: limit,
      ...(typeFilter ? { typeFilter: typeFilter } : {})
    }
    if (isAttached.current) executeAsync(params)
    setPage(1)
  }, [limit])

  React.useEffect(() => {
    if (formatter !== null && isAttached.current) {
      const { list, count } = formatter(data)
      setFormatData(list)
      setCount(count)
    }
  }, [data])

  React.useEffect(() => {
    const params = {
      tableFilter: filter,
      page: 1,
      limit: limit,
      ...(typeFilter ? { typeFilter: typeFilter } : {})
    }
    if (isAttached.current) executeAsync(params)
    setPage(1)
  }, [filter])

  const onSetFilter = filter => {
    setFilter({ ...filter })
  }

  return {
    data: formatData,
    count: count,
    loading: loading,
    page: page,
    limit: limit,
    setData: setFormatData,
    setLimit: setLimit,
    setPage: setPage,
    setFilter: onSetFilter
  }
}

export default useTable
