import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './JoiValidatiionCustomTextField.module.scss'
import onPasteInputHook from '../Utils/CustomHooks/useInputOnPasteHook'
import useInputHook from '../Utils/CustomHooks/UseInputHook'
import { inputTypes } from './utils'

let types = ['number', 'noSpecialChar', 'lettersOnly']
const CustomTextField = props => {
  const {
    label = '',
    disabled,
    required,
    inputRef,
    value,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    clearText = false,
    setClearText = null,
    specialValue = '',
    type,
    ...other
  } = props

  let specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>]/)
  let forEmail = RegExp(/[!#$%^&*(),?":;'`~{}|<>]([[]]*)/)
  let numbersOnly = RegExp(/^[\d\b]+$/)
  let lettersOnly = RegExp(/^[a-z]+$/)
  const [regex, setRegex] = React.useState({})
  React.useEffect(() => {
  // const {forInput, forOnPaste} = inputTypes(type)
    setRegex(inputTypes(type))
  }, [])

  const [handleOnPaste] = onPasteInputHook(regex.forOnPaste)
  const [newStr, onChangeInput] = useInputHook(onChange, specialValue, regex.forInput)

  const [val, setVal] = React.useState('')
  // let dashAndNum = RegExp(/^[\d -]{14}/)
  React.useEffect(() => {
    setVal(newStr)
  }, [newStr])

  React.useEffect(() => {
    if(clearText) {
      setVal('')
      setClearText(false)
    }
  }, [clearText])

  const DisableCharacters = e => {
    switch (type) {
      case 'number':
        if (
          e.key === 'e' ||
          e.key === 'E' ||
          e.keyCode === 189 ||
          e.keyCode === 111 ||
          e.keyCode === 106 ||
          e.keyCode === 107 ||
          e.keyCode === 109 ||
          e.keyCode === 187
        ) {
          return e.preventDefault()
        } else {
          return {}
        }
      case 'integerOnly':
        if (
          e.key === 'e' ||
          e.key === 'E' ||
          lettersOnly.test(e.key) ||
          e.keyCode === 190 ||
          e.keyCode === 189 ||
          e.keyCode === 111 ||
          e.keyCode === 106 ||
          e.keyCode === 107 ||
          e.keyCode === 109
        ) {
          console.log('is called even if ist a letter')
          return e.preventDefault()
        } else {
          return {}
        }
      case 'noSpecialChar':
        if (
          specialChar.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 191 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'dashAndNum':
        if (
          specialChar.test(e.key) ||
          lettersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'lettersOnly':
        if (
          specialChar.test(e.key) ||
          numbersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }
      case 'email':
        if (
          forEmail.test(e.key) ||
          e.keyCode === 187 ||
          // e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      default:
        return {}
    }
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                onChange={types.includes(type) ? onChangeInput : onChange}
                disabled={disabled}
                onPaste={types.includes(type) ? handleOnPaste : null}
                type={type}
                {...other}
                inputRef={inputRef}
                autoComplete='off'
                onKeyDown={e => DisableCharacters(e)}
                value={types.includes(type) ? val : value}
              />
            </Paper>
            {error && <span style={{fontSize: props.name === 'zipCode' ? 10 : ''}} className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(CustomTextField)
