import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'roomNumber',
  'suffix',
  'unitFlrBldg',
  'bldgName',
  'id',
  'dentistId',
  'mobile',
  'checkPayee'
]

const schema = Joi.object({
  name: Joi.string()
    .label('Name')
    .required(), // required
  email: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(),
  contactPersonNo: Joi.string()
    .label('Mobile Number')
    .required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  natureOfPractice: Joi.string()
    .label('Nature of Practice')
    .required(),
  payee: Joi.string()
    .label('Check Payee')
    .required(), // required
  contactPerson: Joi.string()
    .label('Main Coordinator')
    .required(), // required
  unitFlrBldg: Joi.string().label('Unit no./Floor/Bldg. No.'),
  street: Joi.string()
    .label('Street')
    .required(), // required
  subdivision: Joi.string()
    .label('Subdivision')
    .required(), // required
  region: Joi.string()
    .label('Region')
    .required(), // required
  district: Joi.string()
    .label('District')
    .required(), // required
  barangay: Joi.string()
    .label('Barangay')
    .required(), // required
  city: Joi.string()
    .label('City')
    .required(), // required
  zipCode: Joi.string()
    .label('Zip code')
    .required()
})

let newErrors = {}

export default function DentalClinicFormValidation (form, errors) {
  newErrors = { ...errors }
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      newErrors[i.path[0]] = i.message
      if (notRequiredFields.includes(i.path[0])) {
        delete newErrors[i.path[0]]
      }
    }
    console.log('dental clinic errors', newErrors)
  }

  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
