import React, { useState, useEffect, memo, useMemo } from 'react'
import classes from './AffiliationForm.module.scss'
import DentalCLinicForm from './DentalClinicForm/DentalClinicForm'
import DentistForm from './DentistForm/DentistForm'
import RemarksComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/RemarksComponent'
import TinStatusComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/TinStatusComponent'
import DentalAssociatesAndAffiliation from './AssociatesAndAffiliation'

const DentalClinicAffiliationForm = props => {
  const {
    isSubmitted,
    setValid,
    isTovalidate,
    setDentistData,
    setDentalData,
    dentistData,
    affiliatedData,
    dentalData,
    statusData,
    remarksData
  } = props

  const [dentalClinicForm, setDentalClinicForm] = useState({})

  const [dentistForm, setDentistForm] = useState({})

  const [statusForm, setStatusForm] = useState({})

  const [dentalAssociate, setDentalAssociates] = useState({})

  const [remarksForm, setRemarksForm] = useState({})

  const [dentistIsValid, setDentistIsValid] = useState(false)

  const [clinicIsValid, setClinicIsValid] = useState(false)

  const [statusIsValid, setStatusIsValid] = useState(false)

  const [remarksIsValid, setIsRemarksIsValid] = useState(false)

  useEffect(() => {
    const isValid = dentistIsValid && clinicIsValid && statusIsValid && remarksIsValid
    if (isValid) {
      setValid(true)
      let dental = Object.assign({}, dentalClinicForm, dentalAssociate, remarksForm)
      let dentist = Object.assign({}, dentistForm, statusForm)
      setDentistData(dentist)
      setDentalData(dental)
    } else {
      setValid(false)
    }
  }, [dentistIsValid, clinicIsValid, statusIsValid, remarksIsValid])

  useEffect(() => {
    let dental = Object.assign({}, dentalClinicForm, dentalAssociate, remarksForm)
    let dentist = Object.assign({}, dentistForm, statusForm)
    setDentistData(dentist)
    setDentalData(dental)
  }, [dentalClinicForm, dentalAssociate, remarksForm, dentistForm, statusForm])

  const dentist = useMemo(() => {
    return dentistData
  }, [dentistData])

  return (
    <div id='root' className={classes.root}>
      <div id='topText' className={classes.topText}>
          DENTAL CLINIC INFORMATION
      </div>

      <DentalCLinicForm data={dentalClinicForm} setData={setDentalClinicForm} setIsValid={setClinicIsValid} isValid={clinicIsValid} isValidate={isTovalidate} defaultVal={dentalData} isSubmitted={isSubmitted} />

      <DentistForm data={dentistForm} setData={setDentistForm} setIsValid={setDentistIsValid} isValid={dentistIsValid} isValidate={isTovalidate} defaultVal={dentist} isSubmitted={isSubmitted} />

      <TinStatusComponent data={statusForm} setData={setStatusForm} setIsValid={setStatusIsValid} isValid={statusIsValid} isValidate={isTovalidate} defaultVal={statusData} isSubmitted={isSubmitted} />

      <DentalAssociatesAndAffiliation data={dentalAssociate} setData={setDentalAssociates} defaultVal={affiliatedData} isSubmitted={isSubmitted} />

      <RemarksComponent hasPaddingLeft={false} data={remarksForm} setData={setRemarksForm} isValid={remarksIsValid} setIsValid={setIsRemarksIsValid} isValidate={isTovalidate} defaultVal={remarksData} isSubmitted={isSubmitted} />
    </div>
  )
}

export default memo(DentalClinicAffiliationForm)
