import _ from 'lodash'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
export function fieldHospitalApproverMapping (data) {
  let newForms = {}
  if (!_.isEmpty(data)) {
    const remarks = data.remarks
    newForms.id = data.id
    newForms.type = data['type']
    newForms['internalRemarks'] = remarks['internal_remarks'] ? remarks['internal_remarks'] : remarks['intellicare']
  }
  return newForms
}

export function validateButton (data = '') {
  if (data.length <= 0) {
    return true
  } else {
    return false
  }
}

export async function handleNextButton (form, internalRemarks) {
  const data = Object.assign(form, { internalRemarks })
  try {
    await AffiliationApproverServices.updateAffiliation(data)
  } catch (error) {
    console.log('update eorror', error)
  }
}
