import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import EditIcon from '@material-ui/icons/Edit'
import TrashIcon from '@material-ui/icons/Delete'
import InlineForm from '../InlineText/InlineText'
import classes from './tableData.module.scss'

const row = (x, i, header = [], handleRemove, startEditing, editIdx, handleSave, stopEditing, isSubmitted) => {
  const currentlyEditing = editIdx === i
  return currentlyEditing ? (
    <StyledTableRow key={`inline-form-${i}`}>
      {/* <StyledTableCell className={`${classes.truncate}`} key={`trc-${i}`}> */}
      <InlineForm handleSave={handleSave} header={header} x={x} i={i} stopEditing={stopEditing} />
      {/* </StyledTableCell> */}
    </StyledTableRow>
  ) : (
    <StyledTableRow key={`tr-${i}`}>
      {header.map((y, k) => (
        <StyledTableCell className={`${classes.truncate}`} key={`trc-${k}`} align='left' component='th' scope='row'>
          {x[y.prop]}
        </StyledTableCell>
      ))}
      {(!isSubmitted) && (
        <StyledTableCell className={classes.editAndDelete} align='left'>
          <div className={classes.navigation}>
            <div className={classes.edit}><EditIcon onClick={() => startEditing(i)} /></div>
            <div className={classes.delete}><TrashIcon onClick={() => handleRemove(i)} /></div>
          </div>
        </StyledTableCell>
      )}
    </StyledTableRow>
  )
}

export default ({ data, header = [], handleRemove, startEditing, editIdx, handleSave, stopEditing, isSubmitted }) => (
  <TableContainer className={classes.tableContainer}>
    <Table className={classes.tableRoot} aria-label='customized table'>
      <TableHead>
        <TableRow>
          {header.map((x, i) => (
            <TableCell key={`thc-${i}`} align='left' style={{whiteSpace: 'nowrap', padding: '16px 8px'}}>
              {x.name}
            </TableCell>
          ))}
         {(!isSubmitted) && <TableCell align='left' />}
        </TableRow>
      </TableHead>
      <TableBody>
        {data.map((x, i) => row(x, i, header, handleRemove, startEditing, editIdx, handleSave, stopEditing, isSubmitted))}
      </TableBody>
    </Table>
  </TableContainer>
)

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.lightgrey,
    color: theme.palette.common.lighgrey
  },
  body: {
    fontSize: 12,
    padding: '16px 8px'
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default
    },
    '&:nth-of-type(even)': {
      backgroundColor: '#f5f7fb'
    }
  }
}))(TableRow)
