import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import classes from './index.module.scss'

const IndexComponents = props => {
  const { value, onFocus, style, inputRef, onChange, error, type, name, autoFocus, onBlur, setErrors } = props

  return (
    <React.Fragment>
      <Paper className={error ? classes.InputFieldsError : classes.InputFields}>
        <InputBase
          value={value}
          onFocus={onFocus}
          name={name}
          style={style}
          className={classes.textField}
          type={type}
          autoFocus={autoFocus}
          inputRef={inputRef}
          onChange={onChange}
          onBlur={onBlur}
          variant='outlined'
          autoComplete='off'
        />
      </Paper>
      { error && <span className={classes.error}> { error }</span> }
    </React.Fragment>
  )
}

const index = React.memo(IndexComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.value === nextProps.value && prevProps.onChange === nextProps.onChange && prevProps.error === nextProps.error) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})

export default index
