import React, { useEffect, useRef, memo, useState } from 'react'
import classes from './Remarks.module.scss'

const RemarksComponent = (props) => {

  const [error, setError] = useState('')
  const [form, setForm] = useState({
    externalRemarks: '',
    internalRemarks: ''
  })

  const internalRef = useRef()
  const externalRef = useRef()
  const {
    setData,
    isSubmitted = false,
    data,
    isValidate,
    setIsValid,
    hasPaddingLeft = true,
    defaultVal,
    label1 = 'External Remarks',
    label2 = 'Internal Remarks *',
    name1 = 'externalRemarks',
    name2 = 'internalRemarks'
  } = props

  useEffect(() => {
    internalRef.current.value = defaultVal && defaultVal['internalRemarks'] ? defaultVal['internalRemarks'] : ''
    externalRef.current.value = defaultVal && defaultVal['externalRemarks'] ? defaultVal['externalRemarks'] : ''
    const nForm = {...form}
    setForm(Object.assign(nForm, defaultVal))
  }, [defaultVal])

  useEffect(() => {
    const nForm = { ...form }
    if (nForm['internalRemarks'].length <= 0) {
      setIsValid(false)
    } else {
      setIsValid(true)
    }
  }, [form['internalRemarks']])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  const validate = () => {
    let isValid = form['internalRemarks'].length <= 0
    if (isValid) {
      setError('"Internal Remarks" is requiered')
    } else {
      setError('')
    }
  }

  const handleChange = ({target: {value, name}}) => {
    const nForm = { ...form }
    if (name === name1) {
      internalRef.current = value
      nForm[name] = value
    } else {
      internalRef.current = value
      nForm[name] = value
      setError('')
    }
    setForm(nForm)
  }

  return (
    <div id='remarksField' className={classes.remarksField} style={{ paddingLeft: hasPaddingLeft ? '3em' : 0 }}>
      <div className={classes.RemarksField}>
        <span>
          <div>
            <label>{label1}</label>
            <textarea
              name={name1}
              disabled={isSubmitted}
              onChange={handleChange}
              ref={externalRef}
            />
          </div>
          <div>
            <label>{label2}</label>
            <textarea
              name={name2}
              error={error}
              disabled={isSubmitted}
              ref={internalRef}
              onChange={handleChange}
            />
            <label className={classes.error}>{error} </label>
          </div>
        </span>
      </div>
    </div>
  )
}

export default memo(RemarksComponent)
