import instance from '../instance'
// import axios from 'axios'
const axios = instance('/p/f')

export default class StandardRatesServices {
  static saveStandardRates (argParams, argCancelToken) {
    console.log('saveStandardRates')
    const data = { data: argParams.data, fromUploaded: argParams.fromUploaded, id: argParams.id, type: argParams.type }
    return axios({
      url: `/partners/facilities/hospital/standard-rates`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static getAllById (id) {
    return axios({
      url: `/partners/facilities/standard-rates/id/${id}`,
      headers: { skip_generate_form_data: 1 },
      method: 'GET'
    })
  }

  static getStandardRates (argParams, argCancelToken) {
    const params = {
      id: argParams.id,
      type: argParams.type,
      offset: argParams.offset,
      limit: argParams.limit,
      fromUploaded: argParams.fromUploaded,
      filter: argParams.filter
    }

    return axios({
      url: `/partners/facilities/${params.type}/standard-rates`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }
}
