import React from 'react'
import PageOne from './DentalClinicAffiliation_p1'
import PageTwo from './DentalClinicAffiliation_p2'
import DentalAffiliationContext from './context/DentalAffiliationContext'
import { handleNextButton } from '../utils/hospitalApproverUtils'
const DentalClinicAffiliation = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [dentalForm, setDentalForm] = React.useState({})
  const [form, setForm] = React.useState({})
  const [isApproved, setIsApproved] = React.useState(false)
  let inputRef = React.useRef()
  const [internalRemarks, setInternalRemarks] = React.useState({})

  const handleNext = async (value) => {
    if (!isApproved) {
      handleNextButton(internalRemarks, inputRef.current.value)
    }
    // console.log('internal remarks', internalRemarks)
    setIsNextPage(value)
  }

  return (
    <DentalAffiliationContext.Provider value={{ paramId: match.params.id, dentalForm, setDentalForm, setIsApproved, isApproved, form, setForm, inputRef, setInternalRemarks, internalRemarks }}>
      {!isNextPage && <PageOne setPage={handleNext} history={history} />}
      {isNextPage && <PageTwo setPage={setIsNextPage} history={history} />}
    </DentalAffiliationContext.Provider>
  )
}
export default DentalClinicAffiliation
