import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'dohNumber',
  'dohLicenseType',
  'dohLicenseExp',
  'philhealthExpiration'
]

const schema = Joi.object({
  name: Joi.string()
    .label('Name')
    .required(), // required
  email: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(), // required
  years: Joi.number()
    .label('Years')
    .required(), // required
  months: Joi.number()
    .label('Months')
    .required(), // required
  payee: Joi.string()
    .label('Payee')
    .required(), // required
  philhealthNumber: Joi.string()
    .label('PhilHealth License Number')
    .max(12)
    .min(12)
    .required() // required
})

export default function MedicalClinicSectionOneValidation (form) {
  let errors = {}
  console.log('my laman ba si form med', form)
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
      if (form['philhealthNumber'] === '') {
        console.log('is philheath is empty')
        delete errors['philhealthNumber']
      } else {
        if (form['philhealthNumber'].length === 14) {
          delete errors['philhealthNumber']
        }
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }
  console.log('section one error', errors)
  return {
    errors,
    isValid: isEmpty(errors)
  }
}

