import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'subSpecialization',
  'otherSpecialization',
  'pda',
  'philhealthExpiration',
  'prcLicenseNo',
  'roomNumber',
  'suffix',
  'id',
  'dentistId',
  'mobile',
  'mainCoordinator'
]

const schema = Joi.object({
  emailAddress: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(),
  middleName: Joi.string()
    .label('Middle Name')
    .required(),
  firstName: Joi.string()
    .label('First Name')
    .required(),
  lastName: Joi.string()
    .label('Last Name')
    .required(),
  contactNumber: Joi.string()
    .label('Contact Number')
    .required(), // required,
  birthDate: Joi.date()
    .label('Birth Date')
    .required(), // required
  civilStatus: Joi.string()
    .label('Civil Status')
    .required(), // required
  gender: Joi.string()
    .label('Gender')
    .required(), // required,
  mainSpecialization: Joi.string()
    .label('Main Specification')
    .required(), // required,
  majorSpecialization: Joi.string()
    .label('Major Specification')
    .required(), // required,
  prcExpiration: Joi.date()
    .label('PRC Expiration')
    .required(), // required,
  prcNumber: Joi.string()
    .label('PRC License Number')
    .required(), // required,
  tinNumber: Joi.string()
    .label('Tin Number')
    .required() // required,
    .max(12)
    .min(12),
  philhealthNumber: Joi.string()
    .label('PhilHealth Accreditation No')
    .max(12)
    .min(12)
    .required()
})
let newErrors = {}

let dateTypes = ['birthDate', 'prcExpiration']

export default function dentistFormValidation (form, errors) {
  newErrors = { ...errors }

  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      newErrors[i.path[0]] = i.message
      if (dateTypes.includes(i.context.key)) {
        newErrors[i.path[0]] = `"${i.context.label}" is required`
      }
      if (form['philhealthNumber'] === '') {
        delete newErrors['philhealthNumber']
      } else {
        if (form['philhealthNumber'].length === 12) {
          delete newErrors['philhealthNumber']
        }
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete newErrors[i.path[0]]
      }
    }
    console.log('dentist errors ===', newErrors)
  }

  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
