import React from 'react'
import { arrayObjectNullFilter, arrayObjectDistinct } from 'Utilities/Partner/ArrayUtilities'
import useAsyncHook from './useAsyncHook'
import useDebounce from './useDebounce'

const useTableHeader = (field, typeFilter = '', asyncFunction = null, inputLength = 3, debounceTime = 500) => {
  const isAttached = React.useRef(true)
  const [filter, setFilter] = React.useState({})
  const [inputValue, setInputValue] = React.useState('')
  const [value, setValue] = React.useState('')
  const [options, setOptions] = React.useState([])
  const [data, error, loading, executeAsync] = useAsyncHook(asyncFunction)

  const debounceInputValue = useDebounce(inputValue, debounceTime)

  React.useEffect(() => {
    return () => {
      isAttached.current = false
    }
  }, [])

  React.useEffect(() => {
    if (Object.keys(data).length > 0 && isAttached.current) {
      let newData = arrayObjectNullFilter(data.data, field)
      newData = arrayObjectDistinct(newData, field)
      setOptions(newData)
    }
  }, [data])

  React.useEffect(() => {
    const params = {
      tableFilter: filter,
      typeFilter: typeFilter,
      field: field,
      value: debounceInputValue
    }
    if (debounceInputValue.length >= inputLength && isAttached.current) {
      console.log('I WAS HERE')
      executeAsync(params)
    }
  }, [debounceInputValue])

  React.useEffect(() => {
    const params = {
      tableFilter: filter,
      typeFilter: typeFilter,
      field: field
    }
    if (isAttached.current) executeAsync(params)
  }, [filter])

  const onSetFilter = filter => {
    setFilter({ ...filter })
  }

  return {
    field: field,
    inputValue: inputValue,
    value: value,
    options: options,
    error: error,
    loading: loading,
    setFilter: onSetFilter,
    setInputValue: setInputValue,
    setValue: setValue
  }
}

export default useTableHeader
