import RoomAndBoardValidation from '../../Utilities/Validation'
import isEmpty from 'lodash/isEmpty'

import {
  SET_ON_SAVE,
  SET_ON_EDIT,
  SET_PREVIOUS_RECORD,
  SET_ON_VALIDATE,
  UPDATE_DATA,
  SET_PAGE_DATA,
  UPDATE_ROW_DATA,
  COPY_DATA_TO_PREV,
  RESET_STATE,
  SET_TABLE_SIZE,
  SET_SORT_ROOM_TYPE,
  SET_SORT_ROOM_CODE,
  SET_SORT_DESCRIPTION,
  SET_INPUT_VALUE,
  SET_VALUE,
  UPDATE_DATA_ON_SAVE,
  GET_ROOM_TYPES_ITEM,
  GET_ROOM_ITEMS
} from '../../Utilities/Type'

// import { updateData } from '../Action/SpecialService'

const initialState = {
  data: [],
  size: 10,
  totalPage: 1,
  page: 1,
  pageData: [],
  prevPageData: [],
  roomCode: {
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  },
  roomType: {
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  },
  description: {
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  },
  isEdit: false,
  isRowsValid: false,
  isSaving: false,
  dataToSend: [],
  RoomTypeArr: [],
  RoomItemsArr: []
}

const stateReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_DATA:
      return { ...state, data: [...action.payload] }

    case SET_PAGE_DATA:
      let filterValue = {}
      if (action.payload.name !== undefined && action.payload.value !== undefined) {
        filterValue = {
          [action.payload.name]: { ...state[action.payload.name], value: action.payload.value }
        }
      }
      const newState = { ...state, ...filterValue }

      const objectHeaders = {
        roomCode: (newState.roomCode.value || {}).roomCode || '',
        roomType: (newState.roomType.value || {}).roomType || '',
        description: (newState.description.value || {}).description || '',
        status: '',
        price: '',
        remarks: '',
        error: '',
        identifier: ''
      }
      const temp = [...newState.data]
      const filteredData = temp.filter(obj => {
        const validation = Object.keys(obj).map(keys => {
          if (objectHeaders[keys] !== '') {
            if (obj[keys] === objectHeaders[keys]) {
              return true
            }
            return false
          }
          return true
        })
        return validation.every(Boolean)
      })

      const size = action.payload.size || state.size
      const totalPage = Math.ceil(filteredData.length / size)
      let page = action.payload.page || state.page
      if (page > totalPage) {
        page = totalPage
      }
      let pageData = filteredData.slice(page * size - size, page * size)
      const prevPageData = [...pageData]
      const roomTypeList = newState.data.map(obj => ({ roomType: obj.roomType }))
      const descriptionList = newState.data.map(obj => ({ description: obj.description }))
      const roomCodeList = newState.data.map(obj => ({ roomCode: obj.roomCode }))

      pageData = pageData.sort((a, b) => {
        return a.roomCode - b.roomCode
      })

      return {
        ...newState,
        ...{
          pageData,
          prevPageData,
          size,
          page,
          totalPage,
          roomType: { ...newState.roomType, list: roomTypeList },
          roomCode: { ...newState.roomCode, list: roomCodeList },
          description: { ...newState.description, list: descriptionList }
        }
      }

    case UPDATE_ROW_DATA:
      let newPageData = [...state.pageData]
      let newDataValue = [...state.data]
      let newFilterValue = [ ...state.data ]
      const position = action.payload.pos
      const name = action.payload.name
      const newValue = action.payload.value
      // const newIndex = action.payload.wholeDataIndex
      const identifier = action.payload.identifier
      const resultIndex = newFilterValue.findIndex(items => items.identifier === identifier)

      newPageData[position] = { ...state.pageData[position], [name]: newValue }
      newDataValue[resultIndex] = { ...state.data[resultIndex], [name]: newValue }

      return { ...state, pageData: newPageData, data: newDataValue }

    case SET_ON_SAVE:

      let isErrorEncountered = false
      const validatedObject = state.pageData.map((obj, index) => {
        let testObj = { ...obj }
        delete testObj['error']
        const error = RoomAndBoardValidation(testObj).error

        if (!isEmpty(error)) {
          isErrorEncountered = true
        }
        return { ...obj, error: error || {} }
      })

      let dataToSend = []

      if (!isErrorEncountered) {
        dataToSend = validatedObject.filter((obj, index) => {
          if (
            obj.roomType !== state.prevPageData[index].roomType ||
            obj.description !== state.prevPageData[index].description ||
            obj.price !== state.prevPageData[index].price ||
            obj.status !== state.prevPageData[index].status
          ) {
            return true
          }
          return false
        })
      }
      return {
        ...state,
        ...{ isRowsValid: !isErrorEncountered, pageData: [...validatedObject], isSaving: action.payload, dataToSend }
      }

    case UPDATE_DATA_ON_SAVE:
      // const index = state.data.findIndex(obj => obj.id === action.payload.id)
      // let dataList = [...state.data]
      // dataList[index] = action.payload

      return { ...state, ...{ data: [...action.payload] } }

    case SET_ON_EDIT:
      return { ...state, isEdit: action.payload }
    case SET_ON_VALIDATE:
      return { ...state }
    case SET_PREVIOUS_RECORD:
      return { ...state, pageData: [...state.prevPageData] }

    case COPY_DATA_TO_PREV:
      return { ...state, prevPageData: [...state.pageData] }

    case SET_SORT_ROOM_TYPE:
      let sortedRoomType = []
      if (state.roomType.isSort === true) {
        sortedRoomType = state.pageData.sort((a, b) => {
          if (a.roomType < b.roomType) return -1
          if (a.roomType > b.roomType) return 1
          return 0
        })
      } else {
        sortedRoomType = state.pageData.reverse((a, b) => {
          if (a.roomType < b.roomType) return -1
          if (a.roomType > b.roomType) return 1
          return 0
        })
      }
      return {
        ...state,
        pageData: [...sortedRoomType],
        roomType: { ...state.roomType, isSort: !state.roomType.isSort }
      }

    case SET_SORT_ROOM_CODE:
      let sortedRoomCode = []
      if (state.roomCode.isSort === true) {
        sortedRoomCode = state.pageData.sort((a, b) => {
          if (a.roomCode < b.roomCode) return -1
          if (a.roomCode > b.roomCode) return 1
          return 0
        })
      } else {
        sortedRoomCode = state.pageData.reverse((a, b) => {
          if (a.roomCode < b.roomCode) return -1
          if (a.roomCode > b.roomCode) return 1
          return 0
        })
      }
      return {
        ...state,
        pageData: [...sortedRoomCode],
        roomCode: { ...state.roomCode, isSort: !state.roomCode.isSort }
      }

    case SET_SORT_DESCRIPTION:
      let sortedDescription = []
      if (state.description.isSort === true) {
        sortedDescription = state.pageData.sort((a, b) => {
          if (a.description < b.description) return -1
          if (a.description > b.description) return 1
          return 0
        })
      } else {
        sortedDescription = state.pageData.reverse((a, b) => {
          if (a.description < b.description) return -1
          if (a.description > b.description) return 1
          return 0
        })
      }
      return {
        ...state,
        pageData: [...sortedDescription],
        description: { ...state.description, isSort: !state.description.isSort }
      }

    case SET_INPUT_VALUE:
      const newInputFilterValue = {
        [action.payload.name]: { ...state[action.payload.name], inputValue: action.payload.inputValue }
      }
      return { ...state, ...newInputFilterValue }

    case RESET_STATE:
      return { ...state, ...initialState }

    case GET_ROOM_TYPES_ITEM:
      return { ...state, RoomTypeArr: action.payload }

    case GET_ROOM_ITEMS:
      return { ...state, RoomItemsArr: action.payload }

    default:
      return { ...state }
  }
}

export default stateReducer
