import Joi from '@hapi/joi'

import ExtractValidationResult from './ExtractValidation'

const schema = Joi.object()
  .keys({
    roomCode: Joi.string().label('Room Code').required(),
    roomType: Joi.string().label('Room Type').required(),
    description: Joi.string().label('Room Description').required(),
    price: Joi.number().label('Price').required(),
    status: Joi.string().label('Status').required(),
    remarks: Joi.any(),
    identifier: Joi.any()
  })
  .options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
