import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
// import TextField from '../../../../../SharedComponents/TextField/CustomTextField'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import CustomDialog from '../../../../../Components/CustomDialog/CustomDialog'

import classes from './DialogBoxConfirmation.module.scss'
import { useSelector, useDispatch } from 'react-redux'
import Axios from 'axios'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'

const URL = process.env.REACT_APP_SERVER_API

const DialogBoxConfirmation = props => {
  const { disable, setDisable } = props
  const dispatch = useDispatch()
  const { medClinicForms } = useSelector(state => state.medClinicAffiliationForm)
  const { affiliationType } = useSelector(state => state.affiliationType)
  const { dialogText } = props
  const [open, setOpen] = React.useState(false)
  const { affiliationStatus } = affiliationType
  React.useEffect(() => {
    if (affiliationStatus === 'Submitted') {
      setDisable(true)
    }
  }, [affiliationStatus])

  const renameMedName = (form, toReplaceName, name) => {
    if (form.hasOwnProperty(toReplaceName)) { // changing 'clinicName' to 'name' needed for backend
      form[name] = form[toReplaceName]
      delete form[toReplaceName]
      return form
    } else {
      return form
    }
  }

  const handleProceed = async () => {
    try {
      affiliationType.stageName = 'AFFILIATION_APPROVAL_FOR_OIC'
      affiliationType.affiliationStatus = 'Submitted'
      affiliationType.dateSubmitted = new Date().toISOString()
      affiliationType.status = 'Active'
      const newForm = renameMedName(medClinicForms, 'medName', 'name')
      // let res = await Axios.put(`${URL}/p/f/partners/facilities/${affiliationType.id}`, {
      //   ...affiliationType,
      //   ...newForm
      // })

      let res = await AffiliationEncoderService.updateFacilities(affiliationType.id, {
        ...affiliationType,
        ...newForm
      })

      dispatch(setAffiliationType(affiliationType))
      setDisable(true)
      const stageName = res.data.status === 200 ? res.data.data.stage.stage_name : 'error'
      console.log('response', res)
      window.$stageName = stageName
      setOpen(false)
    } catch (error) {
      console.log('error in save', error)
    }
  }
  return (
    <React.Fragment>
      <Button
        style={{ backgroundColor: disable ? '#dddfe3' : '#3ab77d', color: 'white', paddingLeft: '1.5em', paddingRight: '1.5em', textTransform: 'capitalize' }}
        variant='contained'
        disabled={disable}
        onClick={() => {
          setOpen(true)
        }}
      >
        Submit
      </Button>
      <CustomDialog id='ga_custom_dialog' isCancel={open} setIsCancel={() => { setOpen(false) }}>
        <div id='ga_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='ga_custom_dialog_message' style={{ textAlign: 'center' }}>
            Are you sure you want to proceed with the submission of {dialogText} form for approval?
          </h3>
          <div id='ga_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              style={{ textTransform: 'none' }}
              onClick={() => {
                setOpen(false)
              }}
              id='ga_custom_dialog_btn_cancel'
              className={classes.btnRed}
              variant='contained'
            >
            Cancel
            </Button>
            <Button
              style={{ textTransform: 'none' }}
              id='ga_custom_dialog_btn_save'
              className={classes.btnGreen}
              variant='contained'
              onClick={handleProceed}
            >
            Proceed
            </Button>
          </div>
        </div>
      </CustomDialog>
    </React.Fragment>
  )
}

export default DialogBoxConfirmation
