import React, { useState, memo, useEffect, useRef, useContext } from 'react'
import classes from './TinStatus.module.scss'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import Radio from '@material-ui/core/Radio'
import DropDown from '../../Components/NativeDropDown'
import CustomTextFieldWithIcons from '../EncoderTextFieldWithIcons/CustomTextFieldWithIcons'
import TinStatusValidation from './TinStatusValidation'

const NonVatableData = [
  { id: 1213, text: 'Zero Rated', value: 'ZeroRated' },
  { id: 241243, text: 'Government', value: 'Government' },
  { id: 321421, text: 'Others', value: 'Others' }
]

const TinStatusComponent = (props) => {

  const { data, setData, setIsValid, isValidate, defaultVal, isSubmitted = false, hasPaddingLeft = true } = props
  const [errors, setErrors] = useState({})
  const [hasLimit] = useState(true)
  const [form, setForm] = useState({
    tinStatus: '',
    nonVatable: '',
    vatTaxRate: ''
  })

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  useEffect(() => {
    let nForm = { ...form }
    setForm(Object.assign(nForm, defaultVal))
  }, [defaultVal])

  useEffect(() => {
    const nform = { ...form }
    const nErrors = { ...errors }
    if (nform['tinStatus'] === '12%') {
      nform['nonVatable'] = ''
      delete nErrors['nonVatable']
      setErrors(nErrors)
      setForm(nform)
    }
  }, [form['tinStatus']])

  const validate = () => {
    const { newErrors, isValid } = TinStatusValidation(form)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setErrors(Object.assign(nErrors, newErrors))
      return false
    }
  }

  const isValidForm = React.useMemo(() => {
    const { isValid } = TinStatusValidation(form)
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form])

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleChange = ({target: {value, name}}) => {
    const nform = { ...form }
    const nErrors = { ...errors }
    nform[name] = value
    delete nErrors[name]
    setErrors(nErrors)
    setForm(nform)
  }

  return (
    <div id='tinStatusRadioButtons' className={classes.TinStatusContainer} style={{paddingLeft: hasPaddingLeft ? '3em' : 0}}>
      <div className={classes.tinStatusWrapper}>
        <FormControl component='fieldset'>
          <FormLabel className={classes.label} component='legend'>
            TIN Status *
          </FormLabel>
          <RadioGroup
            id='tinStatus'
            aria-label='gender'
            name='tinStatus'
            disabled={isSubmitted}
            value={form['tinStatus']}
            onChange={handleChange}
          >
            <FormControlLabel value='12%' control={<Radio color='primary' disabled={isSubmitted} />} label='VATable' />
            <FormControlLabel value='Non-VaTable' style={{whiteSpace: 'nowrap'}} control={<Radio color='primary' disabled={isSubmitted} />} label='Non-VATable' />
          </RadioGroup>
          {errors['tinStatus'] && (
            <FormHelperText style={{ color: 'red' }}>{errors['tinStatus']} </FormHelperText>
          )}
        </FormControl>
        <div id='tinStatusDropDown' className={classes.dropDown}>
          <DropDown
            id='nonVatable'
            items={NonVatableData}
            onChange={handleChange}
            value={form['nonVatable']}
            name='nonVatable'
            disableComponents={form['tinStatus'] === '12%' || isSubmitted}
            error={errors['nonVatable']}
          />
        </div>
      </div>
      <div id='vatTaxRate' className={classes.TaxRate}>
        <label>TAX Rate *</label>
        <div id='vatTaxRate'>
          <CustomTextFieldWithIcons
            type={'number'}
            hasLimit={hasLimit}
            maxLimit={100}
            name='vatTaxRate'
            error={errors['vatTaxRate']}
            disabled={isSubmitted}
            tags={'%'}
            onChange={handleChange}
            defaultValue={form['vatTaxRate']}
          />
        </div>
      </div>
    </div>
  )
}

export default memo(TinStatusComponent)
