import React from 'react'
import PageOne from './MedicalClinicAffiliationOI_p1'
import PageTwo from './MedicalClinicAffiliationOI_p2'
import MedicalClinicOIContext from './context/MedicalClinicOIContext'
import { handleNextButton } from '../utils/hospitalApproverUtils'

const MedicalClinicOI = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [approved, setIsApproved] = React.useState(false)
  let inputRef = React.useRef()
  let isDisabledButton = React.useRef()

  const [internalRemarks, setInternalRemarks] = React.useState({})

  const handleNext = async (value) => {
    if (!approved) {
      handleNextButton(internalRemarks, inputRef.current.value)
    }
    // console.log('internal remarks', internalRemarks)
    setIsNextPage(value)
  }
  return (
    <MedicalClinicOIContext.Provider value={{paramId: match.params.id, approved, setIsApproved, inputRef, setInternalRemarks, isDisabledButton}}>
      {!isNextPage && <PageOne setPage={handleNext} history={history} /> }
      {isNextPage && <PageTwo setPage={setIsNextPage} history={history} /> }
    </MedicalClinicOIContext.Provider>
  )
}
export default MedicalClinicOI
