import React, { useState, useEffect } from 'react'
import Select from '@material-ui/core/Select'
import { useSelector } from 'react-redux'

function InlineSelect ({ onChange, value }) {
  const { doctorSpecialization } = useSelector(state => state.autoSuggestData)
  const [val, setValue] = useState('')
  useEffect(() => {
    setValue(value)
  }, [])
  const handleChange = ({target: {name, value}}) => {
    onChange(value, name)
    setValue(value)
  }

  const items = React.useMemo(() => {
    return (
      doctorSpecialization.map(item => {
        return (
          <option key={item._id} id={item._id} value={item.description}>
            {item.description}
          </option>
        )
      })
    )
  }, [])

  return (
    <Select
      native
      value={val}
      variant='outlined'
      onChange={handleChange}
      inputProps={{
        name: 'mainSpecification',
        id: 'MainSpecification'
      }}
    >
      <option aria-label='None' value='' />
      {items}
    </Select>
  )
}

export default InlineSelect
