import instance from '../instance'
const axiosPf = instance('/p/f')
const axiosPd = instance('/p/d')
const axiosOs = instance('/p/os')
const axiosPc = instance('/p/c')

const URL = process.env.REACT_APP_SERVER_API

export default class AffiliationApproverServices {
  static getFacilityPartnerById (id) {
    return axiosPf({
      url: `/partners/facilities/id/${id}`,
      method: 'GET'
    })
  }

  static getDoctorPartnerById (id) {
    return axiosPd({
      url: `/partners/doctors/${id}`,
      method: 'GET'
    })
  }

  static getOSPartnerById (id) {
    return axiosOs({
      url: `/partners/other-services/id/${id}`,
      method: 'GET'
    })
  }

  static getFacilitiesToApproveOICTile (params) {
    return axiosPf({
      url: '/partners/facilities/approver/dashboard',
      method: 'GET',
      params
    })
  }

  static getPartnersToApproveTile (params) {
    return axiosPc({
      url: '/partners/common/approver/dashboard',
      method: 'GET',
      params
    })
  }

  static getFacilitiesToApproveOICList (params) {
    return axiosPf({
      url: '/partners/facilities/approver/dashboard/view-list',
      method: 'GET',
      params
    })
  }

  static getPartnersToApproveList (params) {
    return axiosPc({
      url: '/partners/common/approver/dashboard/view-list',
      method: 'GET',
      params
    })
  }

  static approveAffiliationForOIC (id, stage) {
    return axiosPf({
      url: `/partners/facilities/approver/submit-for-oic`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: { id, stage }
    })
  }

  static approveAffiliation (id, data) {
    return axiosPc({
      url: `/partners/common/approver/approve-affiliation`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: {id, data}
    })
  }

  static disapproved (id, type) {
    return axiosPc({
      url: `/partners/common/affiliation/disapprove`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: {id, type}
    })
  }

  static updateAffiliation (data) {
    return axiosPc({
      url: '/partners/common/approver/remarks',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }
}
