import React from 'react'
import PageOne from './DentalClinicAffiliationOI_p1'
import PageTwo from './DentalClinicAffiliationOI_p2'
import DentalClinicAffiliationOIContext from './context/DentalClinicAffiliationOIContext'
import { handleNextButton } from '../utils/hospitalApproverUtils'
const DentalClinicAffiliationOI = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  let inputRef = React.useRef()
  const [internalRemarks, setInternalRemarks] = React.useState({})

  const handleNext = async (value) => {
    if (!isApproved) {
      handleNextButton(internalRemarks, inputRef.current.value)
    }
    console.log('internal remarks', internalRemarks)
    setIsNextPage(value)
  }
  return (
    <DentalClinicAffiliationOIContext.Provider value={{paramId: match.params.id, isApproved, setIsApproved, inputRef, setInternalRemarks}}>
      {!isNextPage && <PageOne setPage={handleNext} history={history} /> }
      {isNextPage && <PageTwo setPage={setIsNextPage} history={history} /> }
    </DentalClinicAffiliationOIContext.Provider>
  )
}
export default DentalClinicAffiliationOI
