import React from 'react'
import Paper from '@material-ui/core/Paper'
import NumberFormat from 'react-number-format'
import TextField from '@material-ui/core/TextField'
import Autocomplete from '@material-ui/lab/Autocomplete'
import { InputBase } from '@material-ui/core'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import MomentUtils from '@date-io/moment'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import useDebounce from '../CustomHooks/useDebounce'

import classes from './InputComponent.module.scss'

const InputComponent = props => {
  const {
    readOnly = true,
    isHeader = false,
    name = '',
    value = '',
    defaultMenu = '',
    index,
    type = 'text',
    isDelete = false,
    withPaper = true,
    suggestions = [],
    menu = [],
    onClickDeleteRow = '',
    onChangeRow = '',
    onChangeText = ''
  } = props

  const [suggestionsList, setSuggestionsList] = React.useState([])
  const debounceValue = useDebounce(value, 600)

  React.useEffect(() => {
    if (type === 'suggest' && debounceValue && debounceValue.length > 0) {
      const newSuggestionsList = suggestions.filter(suggestion =>
        suggestion.text.toLowerCase().includes(debounceValue.toLowerCase())
      )
      setSuggestionsList(newSuggestionsList)
    } else {
      setSuggestionsList([])
    }
  }, [debounceValue])

  const withPaperComponent = component => {
    return (
      <Paper component={'form'} style={{ width: '100%', padding: '0 10px' }}>
        {component}
      </Paper>
    )
  }

  const inputBuilder = type => {
    switch (type) {
      case 'text': {
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <InputBase
            value={value === null || !value ? '' : value}
            style={{ width: '100%' }}
            type='text'
            onChange={e =>
              onChangeRow ? onChangeRow(index, name, e.target.value) : onChangeText(name, e.target.value)
            }
          />
        )
      }
      case 'date': {
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              allowKeyboardControl={false}
              format='MM/DD/YYYY'
              placeholder='mm/dd/yyyy'
              InputProps={{
                disableUnderline: true,
                readOnly: true,
                style: { paddingLeft: '10px' }
              }}
              value={value === '' ? null : value}
              onChange={date =>
                onChangeRow
                  ? onChangeRow(index, name, date.format('MM/DD/YYYY'))
                  : onChangeText(date.format('MM/DD/YYYY'))
              }
              KeyboardButtonProps={{
                'aria-label': 'change date'
              }}
              keyboardIcon={<FontAwesomeIcon icon='calendar-day' style={{ fontSize: '1rem' }} />}
            />
          </MuiPickersUtilsProvider>
        )
      }
      case 'price': {
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <NumberFormat
            value={value}
            onValueChange={value =>
              onChangeRow ? onChangeRow(index, name, value.floatValue || '') : onChangeText(value.floatValue || '')
            }
            // className={classes.integerComponent}
            fixedDecimalScale
            customInput={InputBase}
            thousandSeparator
            decimalScale={2}
            isNumericString
            allowNegative={false}
            displayType={'input'}
            allowLeadingZeros={false}
          />
        )
      }
      case 'number': {
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <NumberFormat
            value={value}
            onValueChange={value =>
              onChangeRow ? onChangeRow(index, name, value.floatValue || '') : onChangeText(value.floatValue || '')
            }
            // className={classes.integerComponent}
            decimalSeparator={false}
            customInput={InputBase}
            isNumericString
            allowNegative={false}
            displayType={'input'}
            allowLeadingZeros={false}
          />
        )
      }
      case 'suggest': {
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <Autocomplete
            freeSolo
            value={value}
            options={suggestionsList.length > 0 ? suggestionsList.map(option => option.text) : []}
            onChange={(event, newValue) => (onChangeRow ? onChangeRow(index, name, newValue) : onChangeText(newValue))}
            onInputChange={(event, newValue) => {
              onChangeRow(index, name, newValue)
            }}
            disableUnderline
            renderInput={params => (
              <TextField {...params} InputProps={{ ...params.InputProps, disableUnderline: true }} />
            )}
          />
        )
      }
      case 'select': {
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <Select
            style={{ width: '100%' }}
            value={value || defaultMenu}
            onChange={e => (onChangeRow ? onChangeRow(index, name, e.target.value) : onChangeText(e.target.value))}
            disableUnderline
          >
            {menu.length > 0 &&
              menu.map(obj => {
                return <MenuItem value={obj.value}>{obj.text}</MenuItem>
              })}
          </Select>
        )
      }
      default:
        if (readOnly && !isHeader) {
          return value
        }
        return (
          <InputBase
            style={{ width: '100%' }}
            value={value}
            type={type}
            onChange={e => (onChangeRow ? onChangeRow(index, name, e.target.value) : onChangeText(e.target.value))}
          />
        )
    }
  }

  const builder = (type, readOnly, isHeader) => {
    if (readOnly) {
      if (isHeader) return withPaperComponent(inputBuilder(type))
      return inputBuilder(type)
    } else {
      return withPaperComponent(inputBuilder(type))
    }
  }

  return (
    <React.Fragment>
      <div style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
        {builder(type, readOnly, isHeader)}
        {isDelete && !readOnly && (
          <FontAwesomeIcon
            className={classes.tableHeaderSortIcon}
            icon='trash'
            onClick={() => onClickDeleteRow(index)}
          />
        )}
      </div>
    </React.Fragment>
  )
}

export default React.memo(InputComponent, (prevProps, nextProps) => {
  try {
    if (
      prevProps.value === nextProps.value &&
      prevProps.name === nextProps.name &&
      prevProps.index === nextProps.index &&
      prevProps.readOnly === nextProps.readOnly
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
