import instance from '../instance'
// import axios from 'axios'
const axios = instance('/p/f')
const axios_pc = instance('/p/c')

export default class ContractManagerServices {
  static getAllPrintContracts (params) {
    return axios_pc({
      url: '/partners/common/contracts/dashboard-print',
      method: 'GET',
      params
    })
  }

  static getSingleContractData (data) {
    const { type, id, facilityID = '' } = data
    return axios_pc({
      url: `/partners/common/contracts/dashboard/print/${type}/${id}/${facilityID}`,
      method: 'GET'
    })
  }

  static getSingleRenewContractData (data) {
    const { type, id, facilityID = '' } = data
    return axios_pc({
      url: `/partners/common/contracts/dashboard/renew/${type}/${id}/${facilityID}`,
      method: 'GET'
    })
  }

  static getAllPrintContractsType (argParams, argCancelToken) {
    const params = { dateFilter: argParams.dateFilter, typeFilter: argParams.typeFilter }
    return axios_pc({
      url: '/partners/common/contracts/dashboard/print',
      method: 'GET',
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static getAllRenewContractsType (argParams, argCancelToken) {
    const params = { typeFilter: argParams.typeFilter }
    return axios_pc({
      url: '/partners/common/contracts/dashboard/renew',
      method: 'GET',
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static getAllRenewContracts (params) {
    return axios_pc({
      url: '/partners/common/contracts/dashboard-renew',
      method: 'GET',
      params
    })
  }

  static getContract (params) {
    return axios({
      url: `/partners/profile/contractDetails/${params.type}/${params.id}/${params.facilityId}?`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static setTerminate (data) {
    return axios_pc({
      url: '/partners/common/contracts/terminate',
      method: 'PUT',
      data
    })
  }

  static setRenew (data) {
    return axios_pc({
      url: '/partners/common/contracts/renew',
      method: 'PUT',
      data
    })
  }

  static getContractViewListReEvaluate () {
    return axios_pc({
      url: '/partners/common/contracts/view/contract/reevaluate',
      method: 'GET'
    })
  }

  static getContractViewListPrint (argParams, argCancelToken) {
    const params = {
      tableFilter: argParams.tableFilter,
      typeFilter: argParams.typeFilter,
      page: argParams.page,
      limit: argParams.limit
    }
    return axios_pc({
      url: '/partners/common/contracts/view/contract/list',
      method: 'GET',
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static getContractViewListPrintField (argParams, argCancelToken) {
    const params = {
      tableFilter: argParams.tableFilter,
      typeFilter: argParams.typeFilter,
      field: argParams.field,
      value: argParams.value
    }
    return axios_pc({
      url: '/partners/common/contracts/view/contract/list/field',
      method: 'GET',
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }
}
