import React, { useState, useEffect, memo } from 'react'
// Styles
import classes from './AffiliationForm.module.scss'
import MedicalClinicSectionOne from './SectionOne'
import MedicalClinicSectionTwo from './SectionTwo'
import MedicalClinicSectionThree from './SectionThree'
import MedicalClinicSectionFour from './SectionFour'
import RemarksComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/RemarksComponent'
import { setMedClinicFormData } from 'Pages/Protected/Store/actions/Partner/medicalClinicAffiliationForm'
import { useDispatch } from 'react-redux'

const AffiliationForm = props => {
  const dispatch = useDispatch()

  const { isSubmitted, setValid, isTovalidate, setForm, fetchFormData } = props
  const [remarksForm, setRemarksForm] = useState({})
  const [sectionOneForm, setSectionOneForm] = useState({})
  const [sectionTwoForm, setSectionTwoForm] = useState({})
  const [sectionThreeForm, setSectionThreeForm] = useState({})
  const [sectionFourForm, setSectionFourForm] = useState({})
  const [sectionOneFormIsValid, setSectionOneFormIsValid] = useState(false)
  const [sectionTwoFormIsValid, setSectionTwoFormIsValid] = useState(false)
  const [sectionThreeFormIsValid, setSectionThreeFormIsValid] = useState(false)
  const [sectionFourFormIsValid, setSectionFourFormIsValid] = useState(false)

  const [remarksIsValid, setIsRemarksIsValid] = useState(false)

  useEffect(() => {
    const isValid = sectionOneFormIsValid && sectionTwoFormIsValid && sectionThreeFormIsValid && remarksIsValid
    if (isValid) {
      setValid(true)
      const formData = Object.assign({}, sectionOneForm, sectionTwoForm, sectionThreeForm, sectionFourForm, remarksForm)
      dispatch(setMedClinicFormData(formData))
      setForm(formData)
    } else {
      setValid(false)
    }
  }, [sectionOneFormIsValid, sectionTwoFormIsValid, sectionThreeFormIsValid, remarksIsValid])

  useEffect(() => {
    const formData = Object.assign({}, sectionOneForm, sectionTwoForm, sectionThreeForm, sectionFourForm, remarksForm)
    setForm(formData)
    dispatch(setMedClinicFormData(formData))
  }, [sectionOneForm, sectionTwoForm, sectionThreeForm, sectionFourForm, remarksForm])

  return (
    <div className={classes.root}>
      <div className={classes.textContainer}>
        <div>FACILITY INFORMATION</div>
        <div>General Information</div>
      </div>
      <MedicalClinicSectionOne
        data={sectionOneForm}
        setData={setSectionOneForm}
        setIsValid={setSectionOneFormIsValid}
        isValid={sectionOneFormIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={fetchFormData && fetchFormData.formOne}
      />

      <MedicalClinicSectionTwo
        data={sectionTwoForm}
        setData={setSectionTwoForm}
        setIsValid={setSectionTwoFormIsValid} isValid={sectionTwoFormIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={fetchFormData && fetchFormData.formTwo}
      />

      <MedicalClinicSectionThree
        data={sectionThreeForm}
        setData={setSectionThreeForm}
        setIsValid={setSectionThreeFormIsValid}
        isValid={sectionThreeFormIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={fetchFormData && fetchFormData.formThree}
      />

      <MedicalClinicSectionFour
        data={sectionFourForm}
        setData={setSectionFourForm}
        setIsValid={sectionFourFormIsValid}
        isValid={setSectionFourFormIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={fetchFormData && fetchFormData.formFour}
      />

      <RemarksComponent
        data={remarksForm}
        setData={setRemarksForm}
        hasPaddingLeft={false}
        isValid={remarksIsValid}
        setIsValid={setIsRemarksIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={fetchFormData && fetchFormData.remarksForm}
      />
    </div>
  )
}

export default memo(AffiliationForm)
