import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import { useInputNumberHook } from '../../../../../Components/Utils/utils'
import classes from './TaxRateInput.module.scss'

const CustomTextField = (props) => {
  const { label = '', required, readOnly, constainerStyle = {}, currentDataRef, setIsChange, index, addRowForm, setForm, setAddRowForm, setCurrentData, error, onChange, type, value, minWidth, ...other } = props

  let specialChar = RegExp(/[!@#$%^&*(),.?":{}|<>]/)
  let numbersOnly = RegExp(/^[\d\b]+$/)
  let lettersOnly = RegExp(/^[a-z]+$/)
  let noDash = RegExp(/[-]/)

  const [num, onChangeInput] = useInputNumberHook(100)

  const DisableCharacters = e => {
    switch (type) {
      case 'number':
        if (e.key === 'e' || e.key === 'E' || noDash.test(e.key)) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'noSpecialChar':
        return specialChar.test(e.key) && e.preventDefault()

      case 'dashAndNum':
        if (specialChar.test(e.key) || lettersOnly.test(e.key)) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'lettersOnly':
        if (specialChar.test(e.key) || numbersOnly.test(e.key)) {
          return e.preventDefault()
        } else {
          return {}
        }

      default:
        return {}
    }
  }

  const HandleChange = e => {
    onChangeInput(e.target.value)
    setIsChange(true)
  }

  React.useEffect(() => {
    const contactForm = { ...addRowForm }
    contactForm['rate'] = num.length > 0 ? Number(num) : ''
    setForm(contactForm)

    const updateFormAddRow = { ...addRowForm }
    updateFormAddRow['rate'] = num.length > 0 ? Number(num) : ''
    setAddRowForm(updateFormAddRow)
  }, [num])

  React.useEffect(() => {
    const dataRefArr = currentDataRef.current
    const updateForm = [...dataRefArr]
    // const dataRefIndex = dataRefArr.length

    if (typeof updateForm[index] !== 'undefined') {
      updateForm[index]['rate'] = value
      setCurrentData(updateForm)
    }
  }, [value])

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper} style={minWidth}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                value={value}
                onChange={HandleChange}
                type={type}
                {...other}
                autoComplete='new-password'
                onKeyDown={e => DisableCharacters(e)}
              />
            </Paper>
            <div className={classes.errorField}>
              {error && <span className={classes.error}>{error}</span>}
            </div>
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(CustomTextField)
