const sortFunction = (key, data, isAscending) => {
  if (data.length === 0) return []
  if (isAscending) {
    return data.sort((a, b) => {
      if (a[key].value < b[key].value) return -1
      if (a[key].value > b[key].value) return 1
      return 0
    })
  } else {
    return data.reverse((a, b) => {
      if (a[key].value < b[key].value) return -1
      if (a[key].value > b[key].value) return 1
      return 0
    })
  }
}

export default sortFunction
