export function inputTypes (type) {
  switch (type) {
    case 'number':
      return {forInput: /[^0-9]+/g, forOnPaste: /[0-9]+/g}

    case 'integerOnly':
      return {forInput: /[^0-9]+/g, forOnPaste: /[0-9]+/g}

    case 'noSpecialChar':
      return {forInput: /[^a-zA-Z0-9 ]/g, forOnPaste: /[a-zA-Z0-9 ]/g}
    // case 'dashAndNum':
    case 'lettersOnly':
      return {forInput: /[^a-zA-Z]+/g, forOnPaste: /[a-zA-Z]+/g}
    // case 'email':

    default:
      return {}
  }
}
