import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './JoiValidatiionCustomTextField.module.scss'
import NumberFormat from 'react-number-format'

function NumberFormatCustom (props) {
  const { inputRef, onChange, defaultValue, format, ...other } = props
  return (
    <NumberFormat
      {...other}
      suffix='-'
      format={format}
      defaultValue={defaultValue}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            value: values.value
          }
        })
      }}
    />
  )
}

function CustomTextFieldNumberFormat (props) {
  const {
    label = '',
    disabled,
    required,
    inputRef,
    // value,
    readOnly,
    constainerStyle = {},
    defaultValue,
    error,
    onChange,
    type,
    format,
    name,
    ...other
  } = props

  let specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>]/)
  let forEmail = RegExp(/[!#$%^&*(),?":;'`~{}|<>]([[]]*)/)
  let numbersOnly = RegExp(/^[\d\b]+$/)
  let lettersOnly = RegExp(/^[a-z]+$/)
  // let dashAndNum = RegExp(/^[\d -]{14}/)

  const DisableCharacters = e => {
    switch (type) {
      case 'number':
        if (
          e.key === 'e' ||
          e.key === 'E' ||
          e.keyCode === 189 ||
          e.keyCode === 111 ||
          e.keyCode === 106 ||
          e.keyCode === 107 ||
          e.keyCode === 109
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'noSpecialChar':
        if (
          specialChar.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 191 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'dashAndNum':
        if (
          specialChar.test(e.key) ||
          lettersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'lettersOnly':
        if (
          specialChar.test(e.key) ||
          numbersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }
      case 'email':
        if (
          forEmail.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      default:
        return {}
    }
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                {...other}
                className={classes.input}
                disabled={disabled}
                type={type}
                autoComplete='off'
                name={name}
                onKeyDown={e => DisableCharacters(e)}
                inputComponent={NumberFormatCustom}
                inputProps={{
                  defaultValue,
                  format,
                  onChange
                }}
              />
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(CustomTextFieldNumberFormat)
