import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'roomCode',
  'price',
  'status',
  'remarks'
]

const schema = Joi.object({
  roomType: Joi.string().label('Room Type').required(),
  description: Joi.string().label('Room Description').required(),
  remarks: Joi.string().label('Remarks')
})

export default function Validation (form) {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
