import isEmpty from 'lodash/isEmpty'
import Joi, { date } from '@hapi/joi'

const notRequiredFields = [
  // 'status',
  // 'claim_type',
  // 'discount_application',
  // 'payment_type',
  // 'terms_in_days',
  // 'rate',
  // 'effectivity_date',
  // 'end_date',
  // 'discount_to_be_deducted_from',
  // 'discount_type',
  // 'other_details_of_discount'
]

const schema = Joi.object({
  status: Joi.string()
    .label('Status').required(), // required
  discount_type: Joi.string()
    .label('Discount Type').required(),
  claim_type: Joi.string()
    .label('Claim Type').required(), // required
  discount_application: Joi.string()
    .label('Discount Application').required(), // required
  included_procedure: Joi.array()
    .label('Included Procedure').required(), // required
  payment_type: Joi.string()
    .label('Payment Type').required(), // required
  terms_in_days: Joi.number()
    .label('Terms in days').required(), // required
  rate: Joi.number().positive()
    .label('Tax Rate').required(), // required
  effectivity_date: Joi.date()
    .label('Effectivity Date').required(), // required
  end_date: Joi.date()
    .label('End Date').required(), // required
  discount_to_be_deducted_from: Joi.string()
    .label('Discount to be deducted from').required(), // required
  other_details_of_discount: Joi.string()
    .label('Other details of discount').required(),
  addProcedure: Joi.string().label('Procedure').messages({'any.required': `"Procedure" is required`}).required()
})

export default function AddDiscountValidation (form, targetValue) {
  let errors = {}

  const discountApplication = Boolean(form['discount_application']) && form['discount_application'].length > 0 ? form['discount_application'] : ''
  const rate = Boolean(form['rate']) && form['rate'].length > 0 ? form['rate'] : ''
  const includedProcedure = Boolean(form['included_procedure']) && form['included_procedure'].length > 0 ? form['included_procedure'] : ''
  const termsInDays = Boolean(form['terms_in_days']) && form['terms_in_days'].length > 0 ? form['terms_in_days'] : ''
  const discountType = Boolean(form['discount_type']) && form['discount_type'].length > 0 ? form['discount_type'] : ''
  const otherDetailsOfDiscount = Boolean(form['other_details_of_discount']) && form['other_details_of_discount'].length > 0 ? form['other_details_of_discount'] : ''

  const errorMessageForProcedure = (errors) => {
    if (targetValue === 'discount_application') {
      delete errors['addProcedure']
      if (discountApplication === 'Per Procedure') {
        delete errors['addProcedure']
        if (includedProcedure === '') {
          errors['addProcedure'] = 'Procedure is required'
        } else {
          delete errors['addProcedure']
        }
      }
    } else {
      if (discountApplication !== '') {
        delete errors['addProcedure']
        if (discountApplication === 'Per Procedure') {
          delete errors['addProcedure']
          if (includedProcedure === '') {
            errors['addProcedure'] = 'Procedure is required'
          } else {
            delete errors['addProcedure']
          }
        }
      }
    }
  }

  const validateIfEmployeeDiscount = errors => {
    if (form && discountType === 'Early Payment Discount') {
      if (form && termsInDays !== '') {
        if (form && Number.isInteger(Number(termsInDays))) {
          delete errors['terms_in_days']
        } else {
          errors['terms_in_days'] = 'Terms in days must be an integer'
        }
      }
    }
  }

  const ValidateDate = errors => {
    if (form && form['effectivity_date'] && form['end_date']) {
      if (Date.parse(form['effectivity_date']) >= Date.parse(form['end_date'])) {
        errors['effectivity_date'] = '"Effectivity Date" should be on or before "End Date"'
      } else {
        delete errors['effectivity_date']
      }
    }
  }

  const result = schema.validate(form, { abortEarly: false })

  const taxRatePattern = new RegExp(/^(\d{0,12}(\.\d{1,12})?|100(\.00?)?)$/)

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      if (targetValue === 'discount_type' && otherDetailsOfDiscount === '') {
        if (discountType !== '' && otherDetailsOfDiscount === '') {
          errors[i.path[0]] = i.message
          delete errors['addProcedure']

          if (rate !== '') {
            if (!taxRatePattern.test(rate)) {
              errors['rate'] = 'Tax Rate must not exceed 100%'
            } else {
              delete errors['rate']
            }
          }

          validateIfEmployeeDiscount(errors)
          errorMessageForProcedure(errors)
          ValidateDate(errors)
        }
      } else if (discountType !== '' && otherDetailsOfDiscount === '') {
        errors[i.path[0]] = i.message
        delete errors['addProcedure']

        if (rate !== '') {
          if (!taxRatePattern.test(rate)) {
            errors['rate'] = 'Tax Rate must not exceed 100%'
          } else {
            delete errors['rate']
          }
        }

        validateIfEmployeeDiscount(errors)
        errorMessageForProcedure(errors)
        ValidateDate(errors)
      } else if (targetValue === 'discount_type' && otherDetailsOfDiscount !== '') {
        if (rate !== '') {
          if (!taxRatePattern.test(rate)) {
            errors['rate'] = 'Tax Rate must not exceed 100%'
          } else {
            delete errors['rate']
          }
        }

        validateIfEmployeeDiscount(errors)
        errorMessageForProcedure(errors)
        ValidateDate(errors)
      } else {
        if (rate !== '') {
          if (!taxRatePattern.test(rate)) {
            errors['rate'] = 'Tax Rate must not exceed 100%'
          } else {
            delete errors['rate']
          }
        }

        validateIfEmployeeDiscount(errors)
        errorMessageForProcedure(errors)
        // ValidateDate(errors)
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  console.log('errors', errors)

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
