import React from 'react'
import Pagination from '@material-ui/lab/Pagination'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import classes from './TablePagination.module.scss'

const TablePagination = props => {
  const { page, pageCount = '', total, limit, text = '', onClickLimit = () => {}, onClickPage = () => {} } = props

  return (
    <div className={classes.tablePagination}>
      <div>
        <span>
          {total}-{limit} out of {pageCount} {text}
        </span>
      </div>
      <div className={classes.options}>
        <span style={{ marginRight: '10px' }}>
          <p>Show</p>
        </span>
        <FormControl size='small' variant='outlined'>
          <Select style={{ backgroundColor: 'white' }} value={limit} onChange={e => onClickLimit(e.target.value)}>
            <MenuItem value={10}>10</MenuItem>
            <MenuItem value={15}>15</MenuItem>
            <MenuItem value={20}>20</MenuItem>
          </Select>
        </FormControl>
        <Pagination
          count={Math.ceil(pageCount / limit)}
          page={page}
          onChange={(e, value) => onClickPage(value)}
          showFirstButton
          showLastButton
        />
      </div>
    </div>
  )
}

export default TablePagination
