import { PropTypes } from 'prop-types'
import React from 'react'
import classes from './index.module.scss'
import { newArray, getNestedObjects } from '../../../Components/Utils/utils'
import {
  RenderInputWithlabel
} from '../../../Components/InputsForProfiles/InputsForProfile'
import DropDownWithLabel from '../InputFields/DropDownWithLabel'

const FacilityAddress = props => {
  const {
    streetError,
    regionError,
    districtProvError,
    cityMunError,
    barangayError,
    zipError,
    streetRef,
    zipRef,
    regionValue,
    districtValue,
    cityValue,
    barangayValue,
    handleChange,
    regionItems,
    provincesItems,
    citiesItems,
    barangayItems,
    handleDropdown,
    isFetching,
    addressFetch
  } = props

  let regionArr = []
  let provinceArr = []
  let citiesArr = []
  let barangayArr = []

  regionArr = newArray(regionItems)
  provinceArr = newArray(provincesItems)
  citiesArr = newArray(citiesItems)
  barangayArr = newArray(barangayItems)

  return (
    <div className={classes.facilityAddressFields}>
      <div id='streetVillage' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={streetError}
          name={'street'}
          inputRef={streetRef}
          label={'Street / Village'}
          onChange={handleChange}
        />
      </div>
      <div id='Region' className={classes.facilityAddressLabels}>
        <DropDownWithLabel
          error={regionError}
          name={'region'}
          items={regionArr}
          value={regionArr.length > 0 ? regionValue : 'fetching...'}
          label={'Region'}
          onChange={handleDropdown}
          addressFetch={!(regionArr.length > 0)}
          disabled={!(regionArr.length > 0)}
        />
      </div>
      <div id='district/Province' className={classes.facilityAddressLabels}>
        <DropDownWithLabel
          error={districtProvError}
          name={'district'}
          items={provinceArr}
          value={addressFetch ? 'fetching...' : getNestedObjects(isFetching, ['province']) ? 'fetching...' : districtValue}
          label={'District / Province'}
          onChange={handleDropdown}
          disabled={!(regionValue)}
          addressFetch={addressFetch}
        />
      </div>
      <div id='city/Municipality' className={classes.facilityAddressLabels}>
        <DropDownWithLabel
          error={cityMunError}
          name={'city'}
          items={citiesArr}
          value={addressFetch ? 'fetching...' : getNestedObjects(isFetching, ['city']) ? 'fetching...' : cityValue}
          label={'City / Municipality'}
          onChange={handleDropdown}
          disabled={!(districtValue)}
          addressFetch={addressFetch}
        />
      </div>
      <div id='barangay' className={classes.facilityAddressLabels}>
        <DropDownWithLabel
          error={barangayError}
          items={barangayArr}
          name={'barangay'}
          label={'Barangay'}
          value={addressFetch ? 'fetching...' : getNestedObjects(isFetching, ['barangay']) ? 'fetching...' : barangayValue}
          onChange={handleDropdown}
          disabled={!(cityValue)}
          addressFetch={addressFetch}
        />
      </div>
      <div id='zipCode' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={zipError}
          name={'zipCode'}
          inputRef={zipRef}
          label={'Zip Code'}
          type={'noSpecialChar'}
          onChange={handleChange}
        />
      </div>
    </div>
  )
}

export default React.memo(FacilityAddress)

FacilityAddress.propTypes = {
  streetError: PropTypes.any,
  regionError: PropTypes.any,
  districtProvError: PropTypes.any,
  cityMunError: PropTypes.any,
  barangayError: PropTypes.any,
  zipError: PropTypes.any,
  regionValue: PropTypes.any,
  districtValue: PropTypes.any,
  cityValue: PropTypes.any,
  barangayValue: PropTypes.any,
  handleChange: PropTypes.func.isRequired
}
