
import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import NativeSelect from '@material-ui/core/NativeSelect'
import classes from './index.module.scss'

const DropDown = props => {
  const {
    name,
    onChange,
    items,
    error,
    label,
    disabled,
    addressFetch
  } = props

  const { value = '' } = props

  const [trigger, setTrigger] = React.useState(false)

  let itemData = typeof items === 'undefined' ? [] : items

  const [_error, setError] = React.useState()

  React.useEffect(() => {
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  React.useEffect(() => {
    fetchData()
  }, [itemData])

  const fetchData = async () => {
    if (await itemData.length === 0) {
      setTrigger(true)
    } else {
      setTrigger(false)
    }
  }

  const checkIfDisabled = () => {
    return addressFetch ? true : trigger ? true : disabled
  }

  return (
    <div id='dropDownwithLabel' className={classes.dropDownwithLabel}>
      <label>{label}</label>
      <NativeSelect
        name={name}
        value={value}
        onChange={onChange}
        disabled={checkIfDisabled()}
        classes={{ root: classes.select }}
        input={<InputBase
          className={`${classes.input} ${_error && !value && classes['has-errors']} ${checkIfDisabled() && classes.disable}`}
        />}
      >
        <option value='' />
        {addressFetch
          ? <option value={'fetching...'}>{'Fetching...'}</option>
          : trigger ? <option value='' /> : itemData.map((item, index) => (
            <option key={index} id='options' value={item.value} className={classes.options}>
              {item.text}
            </option>
          ))
        }
      </NativeSelect>
      <div className={classes.error}> {error && error}</div>
    </div>
  )
}

export default React.memo(DropDown, (prevProps, nextProps) => {
  try {
    if (prevProps.addressFetch === nextProps.addressFetch &&
        prevProps.disabled === nextProps.disabled &&
        prevProps.error === nextProps.error &&
        prevProps.items === nextProps.items &&
        prevProps.label === nextProps.label &&
        prevProps.name === nextProps.name &&
        prevProps.value === nextProps.value) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
