import React from 'react'
import Grid from '@material-ui/core/Grid'
import moment from 'moment'

import PartnerCard from '../../../Components/PartnerCard/PartnerCardV2'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import OICProcessorServices from 'Services/Api/Partner/OICProcessorServices'
import { forContactPrintFormat, forRenewContractPrintFormat } from 'Utilities/Partner/DataFormatter'
import PrintViewList from '../../Components/ForPrintViewList/ForPrintViewList'
import RenewViewList from '../../Components/ForRenewViewList/ForRenewViewList'

import useTileState from '../../../CustomHooks/useTileState'

// Import scss module
import classes from './Dashboard.module.scss'

const Dashboard = props => {
  const [isLoaded, setIsLoaded] = React.useState(false)
  const contractForPrint = useTileState(ContractManagerServices.getAllPrintContractsType, forContactPrintFormat)
  const contractForRenewal = useTileState(ContractManagerServices.getAllRenewContractsType, forRenewContractPrintFormat)
  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome([])
    setIsLoaded(true)
  }, [])

  const onClickPrintRow = obj => {
    const { id, facilityID, type } = obj
    if (contractForPrint.typeFilter === 'facilities') {
      let facilityType = type.toLowerCase()
      if (facilityType === 'clinic') {
        facilityType = 'medical'
      } else if (facilityType === 'dental clinic') {
        facilityType = 'dental'
      }
      return props.history.push(`/contractmanager/view/${facilityType}/${id}`)
    } else {
      return props.history.push(`/contractmanager/view/doctor/${id}/${facilityID}`)
    }
  }

  const onClickRenewRow = async obj => {
    const { id, facilityID, type } = obj
    if (contractForRenewal.typeFilter === 'facilities') {
      let facilityType = type
      if (!facilityType) {
        let data = await OICProcessorServices.getFacilityFormInfo(id)
        if (data.data.data) {
          facilityType = data.data.data.type.toLowerCase()
        }
      }
      if (facilityType) {
        facilityType = facilityType.toLowerCase()
        if (facilityType === 'clinic') {
          facilityType = 'medical'
        } else if (facilityType === 'dental clinic') {
          facilityType = 'dental'
        }
        return props.history.push(`/contractmanager/evaluate/${facilityType}/${id}`)
      }
    } else {
      return props.history.push(`/contractmanager/evaluate/doctor/${id}/${facilityID}`)
    }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          {contractForPrint.openViewList && (
            <PrintViewList
              type={contractForPrint.typeFilter}
              open={contractForPrint.openViewList}
              setOpen={contractForPrint.setOpenViewList}
              onHandleDoubleClick={onClickPrintRow}
            />
          )}
          {contractForRenewal.openViewList && (
            <RenewViewList
              type={contractForRenewal.typeFilter}
              open={contractForRenewal.openViewList}
              setOpen={contractForRenewal.setOpenViewList}
              onHandleDoubleClick={onClickRenewRow}
            />
          )}
          <div className={classes.root}>
            <h1 className={classes.pageHeader}>Dashboard</h1>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={8}>
                <PartnerCard
                  isLoading={contractForPrint.loading}
                  headerCount={contractForPrint.count}
                  headerTitle={'Contract for Printing'}
                  cardTitle='Your Task Tray'
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  typeListFilter={[
                    { key: 'facilities', value: 'Facilities' },
                    { key: 'doctors', value: 'Doctors' }
                  ]}
                  defaultTypeFilter={contractForPrint.typeFilter}
                  defaultFilter={contractForPrint.dateFilter}
                  getSelectedFilter={filter => contractForPrint.executeDateFilter(filter)}
                  getSelectedTypeFilter={filter => contractForPrint.executeTypeFilter(filter)}
                  headerList={
                    contractForPrint.typeFilter === 'facilities'
                      ? ['Medical Partner ID', 'Date Submitted', 'Partner Type', 'Partner Name', 'Location']
                      : [
                          'Medical Partner ID',
                          'Facility ID',
                          'Date Submitted',
                          'Partner Type',
                          'Partner Name',
                          'Location'
                        ]
                  }
                  dataList={contractForPrint.data}
                  getSelectedRow={obj => onClickPrintRow(obj)}
                  actionText={'Print Contract'}
                  openViewList={() => contractForPrint.setOpenViewList(true)}
                />
              </Grid>
              <Grid item xs={4}>
                <PartnerCard
                  headerCount={'0'}
                  headerCountStyle={classes.headerCountStyle}
                  cardTitle='Your Ticket List'
                  cardTitleStyle={classes.cardTitleStyle}
                  headerTitle={'Open Tickets'}
                  openViewList={() => false}
                  selectListFilter={[{ key: 'assignedToAll', value: 'Assigned To All' }]}
                  defaultFilter={'assignedToAll'}
                  getSelectedFilter={() => false}
                />
              </Grid>
            </Grid>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={6}>
                <PartnerCard
                  isLoading={contractForRenewal.loading}
                  headerCount={contractForRenewal.count}
                  headerTitle={'Evaluate Contract for Renewal'}
                  typeListFilter={[
                    { key: 'facilities', value: 'Facilities' },
                    { key: 'doctors', value: 'Doctors' }
                  ]}
                  defaultTypeFilter={contractForRenewal.typeFilter}
                  getSelectedTypeFilter={filter => contractForRenewal.executeTypeFilter(filter)}
                  headerList={
                    contractForRenewal.typeFilter === 'facilities'
                      ? ['Medical Partner ID', 'Date of Expiration', 'Partner Name']
                      : ['Medical Partner ID', 'Facility ID', 'Date of Expiration', 'Partner Name']
                  }
                  dataList={contractForRenewal.data}
                  getSelectedRow={obj => onClickRenewRow(obj)}
                  actionText={'Evaluate'}
                  openViewList={() => contractForRenewal.setOpenViewList(true)}
                />
              </Grid>
            </Grid>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default Dashboard
