import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { affiliatedHospitals } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import Button from '@material-ui/core/Button'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import classes from '../scss/TableDisplay.module.scss'

const Hospital = () => {
  const [hospitals, setHospitals] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  React.useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    LoadingService.loading(true)
    const { data } = await ReportServices.getFacilitiesByType('HOSPITAL')
    const dataArr = data.data
    dataArr.forEach((value, index) => {
      dataArr[index].dataCol = {
        status: value.status,
        facility_lvel: value.classification,
        medical_id: value.id,
        hospital_name: value.name,
        unit_no: value.address ? value.address.street : null,
        barangay: value.address ? value.address.barangay : null,
        city: value.address ? value.address.city : null,
        district: value.address ? value.address.district : null,
        region: value.address ? value.address.region : null,
        contact:
          value.contacts && value.contacts.length > 0
            ? value.contacts[0].cNumber !== 'undefined'
              ? value.contacts[0].cNumber
              : null
            : null
      }
    })

    setHospitals(data.data)
    LoadingService.loading(false)
  }
  return (
    <div>
      <div className={classes.rightBody}>
        <div>
          <div id='root' className={classes.root}>
            <ReactTable columns={affiliatedHospitals} data={hospitals} setDownloadData={setDownloadData} />
          </div>
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>
          Download
        </Button>
        <PreviewDownload
          openPreview={openPreview}
          setOpenPreview={setOpenPreview}
          downloadData={downloadData}
          headerColumn={affiliatedHospitals}
          filename={`${moment().format('YYYYMMDD')}-hospital.csv`}
        />
      </div>
    </div>
  )
}

export default Hospital
