import React from 'react'
import CheckIcon from '@material-ui/icons/Check'
import CancelIcon from '@material-ui/icons/Cancel'
import classes from './InlineText.module.scss'
import TextField from '@material-ui/core/TextField'
import moment from 'moment'
import TableCell from '@material-ui/core/TableCell'
import InlineSelect from '../inlineSelect/InlineSelect'

const InlineForm = props => {
  const { x } = props
  const [values, setValues] = React.useState(x)
  const [errors, setErrors] = React.useState({})

  const change = async (value, name, newType = false) => {
    let newErrrors = { ...errors }
    let newVal = value
    if (newType) {
      newVal = newVal.replace(/[^a-zA-Z0-9 ]/g, '')
    }
    newErrrors[name] = undefined
  if(name === 'prcExpiryDate') {
    console.log('format date new date',await formatDate(value))
    newVal = await formatDate(value)
  }
    setValues({ ...values, [name]: newVal })
    setErrors(newErrrors)
  }

  const validate = () => {
    let newErrors = {...errors}
    let isValidEmail = true
    console.log('values sa validate', values)
    for (let key in values) {
      if (values[key] === '') {
        newErrors[key] = 'Required Field'
      }
    }
    if(values.hasOwnProperty('cEmail')) {
      let emailPattern = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.cEmail)

      if(!emailPattern) newErrors['cEmail'] = '"Email"must be a valid email'
      isValidEmail = emailPattern ? false : true
    }

    setErrors(newErrors)
    if(values.hasOwnProperty('cEmail')) {
      console.log('called has cmeal', isValidEmail)
      return isValidEmail || Object.values(values).includes('')
    } else {
      console.log('called without cmeal')
      return Object.values(values).includes('')
    }
  }

  const onSubmit = e => {
    e.preventDefault()
    const err = validate()
    if (!err) {
      props.handleSave(props.i, values)
    }
  }

  const formatDate = async (date) => {
    let format = await import('../../Utils/utils')
    return format.dateFormat(date)
  }

  const { header, i } = props

  return (
    <>
    {header.map((y, k) => (
      <TableCell style={{padding: '16px 8px'}}>
        <div style={{display: 'flex', flexDirection: 'column', width: '100%'}}>
          {y.isASelect ?
            <InlineSelect onChange={change} value={values[y.prop]} /> :
            <TextField
              id='outlined-basic'
              name={y.prop}
              type={y.isDatePicker ? 'date' : (y.newType ? 'noSpecialChar' : 'text')}
              onChange={({target: {value, name}}) => change(value, name, y.newType)}
              className={errors[y.prop] ? classes.textEditError : classes.textEdit}
              value={y.isDatePicker ? moment(values[y.prop]).format('YYYY-MM-DD') : values[y.prop]}
              errorText={errors[y.prop]}
              variant='outlined'
            />
        }
          {errors[y.prop] && <span className={classes['error-message']}> {errors[y.prop]} </span>}
        </div>
      </TableCell>
      ))}
      <TableCell>
        <div className={classes.checkIcon}>
          <CheckIcon onClick={onSubmit} />
          <CancelIcon onClick={props.stopEditing} />
        </div>
      </TableCell>
    </>
  )
}

export default InlineForm
