import React, { useState, useEffect, useRef } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './CustomTextFieldWithIcons.module.scss'
import { inputTypes } from 'Pages/Protected/Partner/Components/JoiValidationInputField/utils'
import useInputHook from '../EncoderCustomHooks/UseInputHook'

let types = ['number', 'noSpecialChar', 'lettersOnly', 'integerOnly']

const CustomTextFieldWithIcons = props => {
  const {
    label = '',
    tags = '',
    styles,
    disabled,
    required,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    defaultValue = '',
    type,
    ...rest
  } = props
  const { value = '', name = '' } = props
  const inputRef = useRef()
  const [_error, setError] = useState()
  const [regex, setRegex] = useState({})

  useEffect(() => {
    inputRef.current.value = defaultValue
  }, [defaultValue])

  useEffect(() => {
    setError(error)
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
    if (typeof value === 'number') {
      if (value <= 0) setError(error)
    }
  }, [error, value])


  useEffect(() => {
    setRegex(inputTypes(type))
  }, [])

  const [onChangeInput] = useInputHook(inputRef, onChange, regex.forInput)


  const handleOnChange = e => {
    setError()
    const {target: {value}} = e
    if (typeof onChange === 'function') {
      inputRef.current.value = value
      onChange(e)
    }
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                name={name}
                onChange={types.includes(type) ? onChangeInput : handleOnChange}
                type={type}
                disabled={disabled}
                readOnly={readOnly}
                inputRef={inputRef}
                autoComplete='off'
                {...rest}
              />
              <div id='button' className={classes.button}>
                <label id='tags' className={`${styles}`}>
                  {tags}
                </label>
              </div>
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default CustomTextFieldWithIcons
