const countDuplicate = (arrayOfObject, checkFields) => {
  const matchedElementsList = []
  const countedElement = []
  if (arrayOfObject.length === 0) {
    return 0
  }
  arrayOfObject.forEach((element, index) => {
    const matchedElements = arrayOfObject.filter((object, index2) => {
      if (index === index2 || countedElement.includes(index2)) return false
      if ('deleted' in element || 'deleted' in object) return false
      let isMatch = false
      if (checkFields.length > 0) {
        for (const key of checkFields) {
          if (element[key].value === object[key].value) {
            isMatch = true
          } else {
            if (element[key].value && object[key].value && element[key].value.trim() === object[key].value.trim()) {
              isMatch = true
            } else {
              isMatch = false
              break
            }
          }
        }
      } else {
        for (const key in element) {
          if (key !== '_id' && key !== 'deleted') {
            if (element[key].value === object[key].value) {
              isMatch = true
            } else {
              if (element[key].value && object[key].value && element[key].value.trim() === object[key].value.trim()) {
                isMatch = true
              } else {
                isMatch = false
                break
              }
            }
          }
        }
      }

      if (isMatch === true) {
        countedElement.push(index2)
      }
      return isMatch
    })
    if (matchedElements.length > 0) {
      matchedElementsList.push(element)
    }
    matchedElementsList.push(...matchedElements)
    countedElement.push(index)
  })
  return matchedElementsList
}

export default countDuplicate
