import React, { useState, memo, useEffect } from 'react'
import classes from './AssociatesAndAffiliation.module.scss'
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import DoctorSpecialization from 'Pages/Protected/Partner/Components/DoctorSpecialization'
import { isDateValid } from 'Pages/Protected/Partner/Components/DatePicker/DatePickerUtils'
import AffiliationTable from 'Pages/Protected/Partner/Components/AffiliationTable/AffiliationTable1'
import DentistAssociatesValidationForm from '../formValidation/DentalAssociatesValidationForm'
import AffiliationFormInput from '../../../../../../Components/AfiiliationTableWithInput/AfiiliationTableWithInput'
import CalendarInlineWithInlineEdit from '../../../../../../Components/DatePicker/DatePickerInlineEdit'

import { dateFormat } from 'Pages/Protected/Partner/Components/Utils/utils'
import { dataheader } from '../../Utilities/dataHeader'
import isEmpty from 'lodash/isEmpty'

const DentalAssociatesAndAffiliation = (props) => {

  const [errors, setErrors] = useState({})
  const [editIdx, setEditIdx] = useState(-1)
  const [addToGroup, setAddToGroup] = useState([])
  const [dentalAssociates, setDentalAssociates] = useState([])
  const [form, setForm] = useState({
    dentalAssociates: [],
    affiliationInfos: []
  })
  const [assocForm, setAssocForm] = useState({})
  const [clearText, setClearText] = useState(false)
  const [clearAffiliationGroupText, setclearAffiliationGroupText] = useState(false)
  const { isSubmitted = false, data, setData, defaultVal } = props

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    const nForm = {...form}
    setAddToGroup(defaultVal['affiliationInfos'])
    setForm(Object.assign(nForm, defaultVal))
  }, [defaultVal])

  const handleAssocDatePicker = (name, date) => {
    const { isValid, dateFormatted } = isDateValid(date)
    const newDentistError = { ...errors }
    if (date !== '') {
      if (isValid) {
        const newDentistForm = { ...assocForm }
        newDentistForm[name] = dateFormatted
        setAssocForm(newDentistForm)
        newDentistError[name] = undefined
      } else {
        newDentistError[name] = 'Invalid date format'
      }
    } else {
      newDentistError[name] = undefined
    }
    setErrors(newDentistError)
  }

  const deleteDentalAssociate = index => {
    const newForm = {...form}
    const newArr = [...newForm['dentalAssociates']]
    newForm['dentalAssociates'] = newArr.filter((contact, i) => i !== index)
    setForm(newForm)
  }

  const startEditing = i => {
    setEditIdx(i)
  }

  const stopEditing = () => {
    setEditIdx(-1)
  }

  const handleSave = (i, x) => {
    const newForm = {...form}
    const newArr = [...newForm['dentalAssociates']]
    newForm['dentalAssociates'] = newArr.map((row, j) => (j === i ? x : row))
    setForm(newForm)
    setEditIdx(-1)
  }

  const handleAddDentistAssoc = () => {
    const copiedForm = { ...assocForm }
    const newCop = { ...assocForm }
    const newForm = {...form}
    copiedForm['prcExpiryDate'] = dateFormat(copiedForm['prcExpiryDate'])
    const { newErrors, isValid } = DentistAssociatesValidationForm(assocForm, errors)

    if (isValid && isEmpty(errors)) {
      /* reset associate general info */
      setDentalAssociates([...dentalAssociates, copiedForm])
      newForm['dentalAssociates'] = [...newForm['dentalAssociates'], copiedForm]
      setClearText(true)
      setForm(newForm)
      newCop['mainSpecification'] = ''
      newCop['fullName'] = ''
      newCop['contactNo'] = ''
      newCop['prcLinceseNumber'] = ''
      newCop['prcExpiryDate'] = ''
      setAssocForm(newCop)
      setErrors({})
    } else {
      setErrors(newErrors)
      console.log('dentist associate newErrors', newErrors)
    }
  }

  const handleChange = ({target: { value, name }}) => {
    const nform = {...assocForm}
    nform[name] = value
    setAssocForm(nform)
  }

  const handleAffiliatedGroupChange = ({target: { value, name }}) => {
    const nform = {...form}
    nform[name] = value
    setForm(nform)
  }

  const handleAddDentalAssoc = () => {
    if (!isSubmitted) {
      handleAddDentistAssoc()
    }
  }

  const handleGroup = (e) => {
    e.preventDefault()
    const str = { ...form }
    let obj = {}
    obj.affiliatedGroup = str.affiliatedGroup
    if (str.affiliatedGroup) {
      const newAddGroup = (addToGroup instanceof Array) ? [...addToGroup] : []
      setAddToGroup([...newAddGroup, obj])
      setclearAffiliationGroupText(true)
    }
  }

  const HandleRemove = React.useCallback(
    (i) => {
      setAddToGroup((addToGroup) => addToGroup.filter((row, j) => j !== i))
    },
    [addToGroup]
  )

  const HandleEdit = React.useCallback(
    (i, x) => {
      setAddToGroup((addToGroup) => addToGroup.map((row, j) => (j === i ? x : row)))
    },
    [addToGroup]
  )

  const handleGroupValue = React.useCallback(
    (data) => {
      let value = data
      const nForm = { ...form }
      nForm['affiliationInfos'] = value
      nForm['affiliatedGroup'] = ''
      setForm(nForm)
    },
    [addToGroup]
  )

  return (
    <div className={classes.Wrapper}>
      <div id='dentalAssociateInformation' className={classes.dentalAssociateInformation}>
        <div id='dentalAssociateGenInfo' className={classes.dentalAssociateGenInfo}>
          Dental Associate General Information
        </div>
        <div id='dentalAssocInfoFirstInputFields' className={classes.dentalAssocInfoFirstInputFields}>
          <div id='FN' className={classes.fn}>
            <label>Full Name </label>
            <div>
              <TextField
                clearTextField={clearText}
                setClearText={setClearText}
                onChange={handleChange}
                error={errors['fullName']}
                id='fullName'
                name='fullName'
                disabled={isSubmitted}
              />
            </div>
          </div>
          <div id='CN' className={classes.cn}>
            <label>Contact Number </label>
            <div>
              <TextField
                onChange={handleChange}
                error={errors['contactNo']}
                id='contactNo'
                name='contactNo'
                disabled={isSubmitted}
                clearTextField={clearText}
                setClearText={setClearText}
              />
            </div>
          </div>
          <div id='MS' className={classes.ms}>
            <label>Main Specialization </label>
            <div>
              <DoctorSpecialization
                id='mainSpecification'
                name='mainSpecification'
                onChange={handleChange}
                value={assocForm['mainSpecification']}
                error={errors['mainSpecification']}
                disabled={isSubmitted}
              />
            </div>
          </div>
        </div>
        <div id='dentalAssocInfoSecondInputFields' className={classes.dentalAssocInfoSecondInputFields}>
          <div id='PRC' className={classes.PRC}>
            <label>PRC License Number </label>
            <div>
              <TextField
                onChange={handleChange}
                clearTextField={clearText}
                setClearText={setClearText}
                type={'noSpecialChar'}
                error={errors['prcLinceseNumber']}
                id='prcLinceseNumber'
                name='prcLinceseNumber'
                specialValue={assocForm['prcLinceseNumber']}
                disabled={isSubmitted}
              />
            </div>
          </div>
          <div id='PRCED' className={classes.PRCED}>
            <label>PRC Expiry Date </label>
            <div>
              <CalendarInlineWithInlineEdit
                error={errors['prcExpiryDate']}
                errors={errors}
                setErrors={setErrors}
                id='prcExpiryDate'
                name='prcExpiryDate'
                placeholder='Select date'
                value={assocForm['prcExpiryDate']}
                onChange={handleAssocDatePicker}
                disabled={isSubmitted}
              />

            </div>
          </div>
          <div id='addTo' className={classes.addTo}>
            <label onClick={handleAddDentalAssoc}>+Add to list</label>
          </div>
        </div>
      </div>
      <div id='tableFields' className={classes.tableFields}>
        <AffiliationTable
          handleRemove={deleteDentalAssociate}
          dataheader={dataheader} // Feed Header Name
          startEditing={startEditing}
          stopEditing={stopEditing}
          editIdx={editIdx}
          dataValue={form['dentalAssociates'] || []}
          handleSave={handleSave}
          isSubmitted={isSubmitted}
        />
      </div>
      <div id='affiliationTableFields' className={classes.affiliationTableFields}>
        <label id='AffiliaionText' className={classes.AffiliaionText}>
          Affiliations
        </label>
        <div id='tableDropDownInput' className={classes.tableDropDownInput}>
          <label id='textDesciption' className={classes.textDesciption}>
            Dental Organizations and Specialty Societies
          </label>
          <AffiliationFormInput
            handleValue={addToGroup}
            HandleRemove={HandleRemove}
            onClick={handleGroup}
            AddToGroupsArr={handleGroupValue}
            HandleEdit={HandleEdit}
            inputField={
              <TextField
                disabled={isSubmitted}
                name='affiliatedGroup'
                placeholder='Type Affiliate group'
                type={'noSpecialChar'}
                onChange={handleAffiliatedGroupChange}
                clearTextField={clearAffiliationGroupText}
                setClearText={setclearAffiliationGroupText}
              />
            }
            dataheader={[{ prop: 'affiliatedGroup' }]}
          />
        </div>
      </div>
    </div>
  )
}

export default memo(DentalAssociatesAndAffiliation)
