import React, { useState, useEffect, useRef } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './CustomTextFieldWithIcons.module.scss'
import UseInputLimitHook from '../EncoderCustomHooks/inputLimitHook'

const CustomTextFieldWithIcons = props => {

  const {
    label = '',
    tags = '',
    styles,
    disabled,
    inputRef,
    required,
    readOnly,
    constainerStyle = {},
    error,
    maxLimit,
    onChange,
    defaultValue,
    hasLimit = false,
    type,
    ...rest
  } = props
  const { value = '', name = '' } = props
  const [_error, setError] = useState()
  const textfieldRef = useRef()
  const [onChangeInputLimit] = UseInputLimitHook(onChange, maxLimit, textfieldRef)


  useEffect(() => {
    setError(error)
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
    if (typeof value === 'number') {
      if (value <= 0) setError(error)
    }
  }, [error, value])

  useEffect(() => {
    textfieldRef.current.value = defaultValue
  }, [defaultValue])

  const handleOnChange = e => {
    const { target: { value } } = e
    textfieldRef.current.value = value
    onChange(e)
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                name={name}
                onChange={hasLimit ? onChangeInputLimit : handleOnChange}
                type={type}
                disabled={disabled}
                readOnly={readOnly}
                inputRef={textfieldRef}
                autoComplete='off'
                inputProps={{
                  inputProps: { min: hasLimit ? 0 : null }
                }}
                {...rest}
              />
              <div id='button' className={classes.button}>
                <label id='tags' className={`${styles}`}>
                  {tags}
                </label>
              </div>
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default CustomTextFieldWithIcons
