import React, { useState, useMemo, useEffect, Fragment } from 'react'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import FooterWithButtons from 'Pages/Protected/Partner/Components/DynamicFooterWithButton/FooterWithButtons'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'
import AffiliationForm from '../../Components/AffiliationForm/affiliationformop'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import { setMedClinicFormData } from 'Pages/Protected/Store/actions/Partner/medicalClinicAffiliationForm'
import classes from './MedicalClinic.module.scss'
import { barangayAction, cityAction, provinceAction } from 'Pages/Protected/Store/actions/Partner/addressAction'

let pathUrl = '/medical-clinic-affiliation-requirements'
let affilConstValue = {
  stageName: 'ENCODING',
  status: 'Active',
  source: 'PNM',
  affiliationStatus: 'In-Progress',
  dateSubmitted: new Date().toISOString()
}

function MedicalClinicPageOne ({ history }) {
  const { id } = useParams()
  const dispatch = useDispatch()

  const { affiliationType } = useSelector(state => state.affiliationType)

  const [isSuccess, setIsSuccess] = useState(false)
  const [affilID, setAffilID] = React.useState(null)
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [isValid, setValid] = useState(false)
  const [isTovalidate, setIsToValidate] = useState(false)
  const [loading, setIsloading] = useState(false)
  const [form, setForm] = useState({})
  const [fetchFormData, setFetchFormData] = useState({})

  useEffect(() => {
    if (isTovalidate) {
      setTimeout(() => {
        setIsToValidate(false)  // reset validation trigger
      }, 200)
    }
  }, [isTovalidate])

  useEffect(() => {
    if (id || affiliationType.id) {
      const paramId = id ? id : affiliationType.id
      fetch(paramId)
    }
    if (affiliationType.hasOwnProperty('id')) {
      setAffilID(affiliationType['id'])
    }
    setSubmitted(affiliationType.stageName)
  }, [])

  const disabledButton = useMemo(() => {
    if (isValid) {
      return false
    } else {
      return true
    }
  }, [isValid])

  const handleSubmit = e => {
    e.preventDefault()
    if (!isSubmitted) {
      setIsToValidate(true)
      if (isValid) {
        save()
      }
    }
  }

  const handleResetForm = () => {
    console.log('handleResetForm')
  }

  const fetch = async id => {
    setIsloading(true)
    try {
      let { affil, formData, form: medForm } = await (await apiUtils()).mapAndFetchMedical(id, setIsloading)
      const { formThree } = formData
      setAffilID(id)
      setSubmitted(affil.stageName)
      dispatch(setAffiliationType(affil))
      dispatch(barangayAction(await fetchBarangays(formThree['city'])))
      dispatch(cityAction(await fetchCities(formThree['district'])))
      dispatch(provinceAction(await fetchProvince(formThree['region'])))
      setFetchFormData(formData)
      dispatch(setMedClinicFormData(medForm))
      setIsloading(false)

    } catch (error) {
      console.log('error in fetching data', error)
      setIsloading(false)
    }
  }

  const save = async () => {
    let affil = Object.assign(affiliationType, affilConstValue)
    if (!affilID) {
      store(affil)
    } else {
      update(affil)
    }
  }

  const apiUtils = async () => {
    return await import('../../Components/Utilities/services/medicalAffilService')
  }

  const utils = async () => {
    return await import('Pages/Protected/Partner/Components/Utils/utils')
  }


  const fetchBarangays = async brgy => await (await utils()).fetchBarangays(brgy)

  const fetchCities = async city => await (await utils()).fetchCities(city)

  const fetchProvince = async prov => await (await utils()).fetchProvinces(prov)

  const setSubmitted = stageName => {
    if (stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
    }
  }

  const store = async affil => {
    try {
      const { data: { data: { id } } } = await AffiliationEncoderService.createFacilities({ ...affil, ...form })
      console.log('id sa store', id)
      affiliationType.id = id
      setAffilID(id)
      dispatch(setAffiliationType(affiliationType))
      setIsSuccess(true)
    } catch (error) {
      console.log('fail to save', error)
    }
  }

  const update = async affil => {
    const paramId = id ? id : affiliationType.id
    try {
    let {data: {data} } = await AffiliationEncoderService.updateFacilities(paramId, { ...affil, ...form })
      setIsSuccess(true)
      console.log('affil sa update brad', data)
    } catch (error) {
      console.log('update error', error)
    }

  }

  if (loading) return <ProgressComponent />
  return (
    <Fragment>
      <AffiliationForm
        history={history}
        setValid={setValid}
        isTovalidate={isTovalidate}
        setForm={setForm}
        isSubmitted={isSubmitted}
        fetchFormData={fetchFormData}
      />
      <div className={classes.footerContainer}>
        <FooterWithButtons
          footerStatus={'Medical Clinic'}
          onClickSave={handleSubmit}
          disableButton={isSubmitted ? false : disabledButton}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          isSubmitted={isSubmitted}
          handleReset={handleResetForm}
          pathUrl={pathUrl} // use for proceed button
          save={save}
          history={history}
        />
      </div>
    </Fragment>
  )
}

export default MedicalClinicPageOne
