import React from 'react'
import useAsyncHook from './useAsyncHook'

const useTileState = (asyncFunction = null, formatFunction = null, date = true, type = true) => {
  const isAttached = React.useRef(true)
  const [dateFilter, setDateFilter] = React.useState('today')
  const [typeFilter, setTypeFilter] = React.useState('facilities')
  const [count, setCount] = React.useState(0)
  const [formatData, setFormatData] = React.useState([])
  const [openViewList, setOpenViewList] = React.useState(false)
  const [data, error, loading, executeAsync] = useAsyncHook(asyncFunction)

  React.useEffect(() => {
    return () => {
      isAttached.current = false
    }
  }, [])

  React.useEffect(() => {
    if (formatFunction !== null && isAttached.current) {
      const { list, count } = formatFunction(data)
      setFormatData(list)
      setCount(count)
    }
  }, [data])

  React.useEffect(() => {
    const params = {}
    if (date) params['dateFilter'] = dateFilter
    if (type) params['typeFilter'] = typeFilter
    if (isAttached.current) executeAsync(params)
  }, [dateFilter, typeFilter])

  const executeDateFilter = filter => {
    if (isAttached.current) setDateFilter(filter)
  }

  const executeTypeFilter = filter => {
    if (isAttached.current) setTypeFilter(filter)
  }

  return {
    count: count,
    data: formatData,
    rawData: data,
    dateFilter: dateFilter,
    typeFilter: typeFilter,
    loading: loading,
    error: error,
    openViewList: openViewList,
    setCount: setCount,
    setOpenViewList: setOpenViewList,
    executeDateFilter: executeDateFilter,
    executeTypeFilter: executeTypeFilter
  }
}

export default useTileState
