import React from 'react'
import DoctorAffiliationContext from './context/DoctorAffiliationContext'
import Page1 from './DoctorAffiliation_p1'
import Page2 from './DoctorAffiliation_p2'
import { handleNextButton } from '../utils/hospitalApproverUtils'

const DoctorAffiliation = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  const [form, setForm] = React.useState({})
  const [internalRemarks, setInternalRemarks] = React.useState({})
  const inputRef = React.useRef()

  const [affiliationType, setAffiliationType] = React.useState({})

  const handleNext = async (value) => {
    if (!isApproved) {
      handleNextButton(internalRemarks, inputRef.current.value)
    }
    setIsNextPage(value)
  }

  return (
    <React.Fragment>
      <DoctorAffiliationContext.Provider value={{ paramId: match.params.id, setAffiliationType, affiliationType, isApproved, setIsApproved, form, setForm, inputRef, setInternalRemarks, internalRemarks }}>
        {!isNextPage && <Page1 setPage={handleNext} history={history} />}
        {isNextPage && <Page2 setPage={setIsNextPage} history={history} />}
      </DoctorAffiliationContext.Provider>
    </React.Fragment>
  )
}
export default DoctorAffiliation
