import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import CustomDialog from '../../../../../Components/CustomDialog/CustomDialog'

import classes from './DialogBoxConfirmation.module.scss'
import { useSelector, useDispatch } from 'react-redux'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'

const DialogBoxConfirmation = props => {
  const dispatch = useDispatch()
  const { doctorForms } = useSelector(state => state.AffiliationDoctorForm)
  const { affiliationType } = useSelector(state => state.affiliationType)

  const { dialogText, isSubmitted, disable, setDisable, setIsApproved } = props
  const [open, setOpen] = React.useState(false)

  const handleProceed = async () => {
    try {
      affiliationType.stageName = 'FOR_AFFILIATION_APPROVAL'
      affiliationType.affiliationStatus = 'Submitted'
      affiliationType.dateSubmitted = new Date().toISOString()

      let res = await PartnerDoctorService.updatePartnerDoctor(affiliationType.id, {
        ...affiliationType,
        ...doctorForms
      })

      dispatch(setAffiliationType(affiliationType))
      setIsApproved(true)
      const stageName = res.data.status === 200 ? res.data.data.stage.stage_name : 'error'
      window.$stageName = stageName
      setOpen(false)
    } catch (error) {
      console.log('error in save', error)
    }
  }
  return (
    <React.Fragment>
      <Button
        style={{
          backgroundColor: disable ? '#dddfe3' : '#3ab77d',
          color: 'white',
          paddingLeft: '1.5em',
          paddingRight: '1.5em',
          textTransform: 'capitalize'
        }}
        className={classes.submitButton}
        variant='contained'
        disabled={disable}
        onClick={() => {
          setOpen(true)
          // setDisable(true)
        }}
      >
        Submit
      </Button>
      <CustomDialog id='ga_custom_dialog' isCancel={open} setIsCancel={() => { setOpen(false) }}>
        <div id='ga_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='ga_custom_dialog_message' style={{ textAlign: 'center' }}>
            Are you sure you want to proceed with the submission of {dialogText} form for approval?
          </h3>
          <div id='ga_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              style={{ textTransform: 'none' }}
              onClick={() => { setOpen(false) }}
              id='ga_custom_dialog_btn_cancel'
              className={classes.btnRed}
              variant='contained'
            >
              Cancel
            </Button>
            <Button
              style={{ textTransform: 'none' }}
              id='ga_custom_dialog_btn_save'
              className={classes.btnGreen}
              variant='contained'
              onClick={handleProceed}
            >
              Proceed
            </Button>
          </div>
        </div>
      </CustomDialog>
    </React.Fragment>
  )
}

export default DialogBoxConfirmation
