import React from 'react'
import Button from '@material-ui/core/Button'
import CustomDialog from '../../Components/CustomDialog/CustomDialog'
import useMediaQuery from '@material-ui/core/useMediaQuery'
import { useTheme } from '@material-ui/core/styles'

import classes from './YesOrNoDialogBox.module.scss'

const YesOrNoDialogBox = props => {
  const { PrevPage } = props

  const [open, setOpen] = React.useState(false)
  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'))

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div>
      <Button
        variant='contained'
        color='secondary'
        className={classes.cancelButton}
        onClick={handleClickOpen}
      >
        Cancel
      </Button>
      <CustomDialog id='cancelCustomDialog' isCancel={open} setIsCancel={handleClose}>
        <div id='cancelDialogContainer' className={classes.dialogContainer}>
          <h3 id='cancelDialogMessage'>All changes will not be saved. Are you sure you want to cancel?</h3>
          <div id='cancelDialogButtonGroup' className={classes.btnGroup}>
            <Button
              onClick={handleClose}
              id='cancelDialogButtonCancel'
              className={classes.btnCancel}
              variant='contained'
            >
            No
            </Button>
            <Button
              id='cancelDialogButtonCancel'
              className={classes.btnSave}
              variant='contained'
              onClick={PrevPage}
            >
            Yes
            </Button>
          </div>
        </div>
      </CustomDialog>
    </div>
  )
}

export default YesOrNoDialogBox
