import React from 'react'
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import { GeneralController } from 'Controllers/GeneralController'
import TableList from '../../../../Components/TableList'
import StandardRatesService from 'Services/Api/Partner/StandardRatesService'
import { CSVLink } from 'react-csv'
import { Button } from '@material-ui/core'
import moment from 'moment'

const StandardRates = props => {
  const { paramId, type, open, setOpen } = props
  const [exportData, setExportData] = React.useState([])
  const [hasChanges, setHasChanges] = React.useState(false)
  const [entries, setEntries] = React.useState([])
  const [CPTCodes, setCPTCodes] = React.useState([{ text: '' }])
  const [CPTDescription, setCPTDescription] = React.useState([{ text: '' }])
  const [isLoadedCPT, setIsLoadedCPT] = React.useState(false)

  React.useEffect(() => {
    fetchCPTCodes(() => {
      setIsLoadedCPT(true)
    })
    exportButton(paramId)
  }, [])

  React.useEffect(() => {
    console.log('Triggered Something...', hasChanges)
    if (hasChanges) {
      exportButton(paramId)
    }
  }, [hasChanges])

  const exportButton = async facilityId => {
    console.log('exporting csv...')
    if (facilityId === '') {
      console.log('No Facility Id', facilityId)
      return
    }
    console.log('Standard Rate ID : ', facilityId)

    const { data: newData } = await StandardRatesService.getAllById(facilityId)
    console.log('Standard Rate Fetched Data.: ', newData)
    const standardRate = newData
      ? newData.data
        ? newData.data.standard_rate
          ? newData.data.standard_rate
          : []
        : []
      : []

    const dataArr = standardRate
    if (dataArr.length > 0) {
      const header = Object.keys(dataArr[0]).filter(key => !['_id'].includes(key))
      const data = dataArr.map(row => {
        delete row._id // remove _id
        row.effectivityDate = moment(row.effectivityDate).format('YYYY-MM-DD')
        return Object.values(row).map(value => {
          return value
        })
      })
      setExportData([header, ...data])
    }
  }

  const fetchCPTCodes = cb => {
    IcdCptStorageServices.readStorageEntries(IcdCptStorageServices.OBJECT_STORE_NAME_CPT, function (entries) {
      // Check if there are no CPT entries
      if (!entries || entries.length === 0) {
        // Trigger fetching and saving of ICD and CPT entries
        GeneralController.fetchAndSaveIcdAndCptEntries()
        return
      }
      const codes = entries.map(entry => ({ text: entry.code }))
      const description = entries.map(entry => ({ text: entry.description }))
      setCPTCodes(codes)
      setCPTDescription(description)
      setEntries(entries)
      cb()
    })
  }

  return (
    <React.Fragment>
      {isLoadedCPT && (
        <TableList
          saveAsyncFunc={(data, token) => {
            const args = { ...data, id: paramId, type: type }
            return StandardRatesService.saveStandardRates(args, token)
          }}
          getAsyncFunc={(data, token) => {
            const args = { ...data, id: paramId, type: type }
            return StandardRatesService.getStandardRates(args, token)
          }}
          readOnlyHeadersButtonsProps={[
            {
              component: (
                <CSVLink
                  data={exportData}
                  onClick={() => {
                    exportButton(paramId)
                    if (exportData.length <= 0) {
                      return false
                    }
                  }}
                  filename={`${paramId}-standard-rate.csv`}
                  style={{ textDecoration: 'none' }}
                >
                  <Button
                    variant='contained'
                    style={{
                      color: '#f5f7fb',
                      textTransform: 'none',
                      margin: '0 5px',
                      backgroundColor: '#3ab77d',
                      textDecoration: 'none'
                    }}
                  >
                    Export
                  </Button>
                </CSVLink>
              )
            },
            {
              text: 'Upload',
              style: { backgroundColor: '#3ab77d' },
              onClick: ({ setUploadRates }) => setUploadRates(true)
            },
            {
              text: 'Update',
              style: { backgroundColor: '#3ab77d' },
              onClick: ({ setReadOnly }) => setReadOnly(false)
            }
          ]}
          onUpdateHeadersButtonsProps={[
            {
              text: 'Add Procedure',
              style: { backgroundColor: '#3ab77d' },
              onClick: ({ onClickAdd }) => onClickAdd()
            },
            {
              text: 'Upload',
              style: { backgroundColor: '#3ab77d' },
              onClick: ({ setUploadRates }) => setUploadRates(true)
            }
          ]}
          readOnlyFooterButtonsProps={[
            {
              text: 'Close',
              style: { backgroundColor: '#fd5451' },
              onClick: () => setOpen(false)
            }
          ]}
          onUpdateFooterButtonsProps={[
            {
              text: 'Cancel',
              style: { backgroundColor: '#fd5451' },
              onClick: ({ setIsCancel }) => setIsCancel(true)
            },
            {
              text: 'Save',
              style: { backgroundColor: '#3ab77d' },
              onClick: ({ setIsSubmit }) => setIsSubmit(true)
            }
          ]}
          readOnlyHeaderTableProps={[
            {
              text: 'Status',
              enableSort: false,
              field: 'status',
              customStyle: { width: '100px' }
            },
            {
              text: 'Facility CPT Code',
              type: 'text',
              field: 'facilityCpt',
              customStyle: { width: '150px' }
            },
            {
              text: 'Facility Procedure Description',
              type: 'text',
              field: 'facilityProcedure',
              customStyle: { width: '200px' }
            },
            {
              text: 'HIMS CPT Code',
              type: 'text',
              field: 'cpt',
              customStyle: { width: '150px' }
            },
            {
              text: 'Procedure Description',
              type: 'text',
              field: 'procedure',
              customStyle: { width: '200px' }
            },
            {
              text: 'Price',
              enableSort: false,
              field: 'price',
              customStyle: { width: '120px' }
            },
            {
              text: 'Remarks',
              enableSort: false,
              field: 'remarks',
              customStyle: { width: '200px' }
            }
          ]}
          onUpdateHeaderTableProps={[
            {
              text: 'Status',
              enableSort: false,
              field: 'status',
              customStyle: { width: '100px' }
            },
            {
              text: 'Facility CPT Code',
              type: 'text',
              field: 'facilityCpt',
              customStyle: { width: '150px' }
            },
            {
              text: 'Facility Procedure Description',
              type: 'text',
              field: 'facilityProcedure',
              customStyle: { width: '300px' }
            },
            {
              text: 'HIMS CPT Code',
              type: 'text',
              field: 'cpt',
              customStyle: { width: '150px' }
            },
            {
              text: 'Procedure Description',
              type: 'text',
              field: 'procedure',
              customStyle: { width: '300px' }
            },
            {
              text: 'Price',
              enableSort: false,
              field: 'price',
              customStyle: { width: '150px' }
            },
            {
              text: 'Effectivity Date',
              enableSort: false,
              field: 'effectivityDate',
              customStyle: { minWidth: '170px' }
            },
            {
              text: 'Remarks',
              enableSort: false,
              field: 'remarks',
              customStyle: { width: '200px' }
            }
          ]}
          dataStructure={{
            status: {
              type: 'select',
              defaultMenu: 'Active',
              menu: [
                { text: 'Active', value: 'Active' },
                { text: 'Inactive', value: 'Inactive' }
              ],
              isDelete: true
            },
            facilityCpt: { type: 'text', filter: true },
            facilityProcedure: { type: 'text', filter: true },
            cpt: {
              entries: entries,
              key: 'code',
              target: 'description',
              type: 'suggest',
              suggestions: CPTCodes,
              relation: 'procedure',
              filter: true
            },
            procedure: {
              entries: entries,
              key: 'description',
              target: 'code',
              type: 'suggest',
              suggestions: CPTDescription,
              relation: 'cpt',
              filter: true
            },
            price: { type: 'price' },
            effectivityDate: { type: 'date' },
            remarks: { type: 'text' }
          }}
          filter={{
            facilityCpt: '',
            facilityProcedure: '',
            cpt: '',
            procedure: ''
          }}
          sort={{
            facilityCpt: false,
            facilityProcedure: false,
            cpt: false,
            procedure: false
          }}
          validation={{
            rowDuplicate: { enable: false, checkFields: ['cpt', 'procedure'] }
          }}
          cptCodes={entries}
          openModal={open}
          setOpenModal={setOpen}
          setHasChanges={setHasChanges}
          paramId={paramId}
          type={type}
        />
      )}
    </React.Fragment>
  )
}

export default StandardRates
