import React, { useState, useEffect, memo } from 'react'

// Shared Components
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import CustomTextFieldNumberFormat from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderInputFieldNumberFormat'
import CustomCalendar from 'SharedComponents/DatePicker2/DatePicker2'

// Styles
import classes from './sectionOne.module.scss'
import CustomTextFieldWithIcons from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderCustomTextFieldWithIcons/CustomTextFieldWithIcons'
import MedicalClinicSectionOneValidation from './validation'

const MedicalClinicSectionOne = props => {
  const {data, isSubmitted = false, setData, setIsValid, isValidate, defaultVal } = props
  const [errors, setError] = useState({})
  const [form, setForm] = useState({
    philhealthNumber: '',
    philhealthExpiration: null,
    dohLicenseExp: null
  })

  useEffect(() => {
    if(isValidate) {
      validate()
    }
  }, [isValidate])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    let nForm = { ...form }
    setForm(Object.assign(nForm, defaultVal))
  }, [defaultVal])

  const isValidForm = React.useMemo(() => {
    const { isValid } = MedicalClinicSectionOneValidation(form)
    console.log('is this valid', isValid)
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form])

  const validate = () => {
    const {errors, isValid } = MedicalClinicSectionOneValidation(form)
    console.log('errors', errors)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setError(Object.assign( nErrors, errors))
      return false
    }
  }

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])


  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setError(e)
    setForm(nform)
  }

  const handleDatePicker = async (date, name) => {
    let newError = { ...errors }
    let newDate = (await utils()).dateFormat(date)
    if(newDate === 'Invalid date') {
      newError[name] = 'Invalid Date'
    } else {
      const nForm = { ...form }
      nForm[name]= newDate
      setForm(nForm)
      delete newError[name]
    }
    setError(newError)
  }

  const utils = async () => {
    let util = await import('Pages/Protected/Partner/Components/DatePicker/DatePickerUtils')
    return util
  }

  return (
    <div className={classes.firstPartInputFields}>
      <div id='name'>
        <label id='medicalClinicLabel'>Medical Clinic Name *</label>
        <TextField
          disabled={isSubmitted}
          error={errors['name']}
          name='name'
          type={'text'}
          onChange={handleOnChange}
          defaultValue={form['name']}
        />
      </div>
      <div id='firstLayerFields' className={classes.firstLayerFields}>
        <div id='email'>
          <label id='emailLabel'>Email Address *</label>
          <TextField
            disabled={isSubmitted}
            error={errors['email']}
            name='email'
            type={'email'}
            onChange={handleOnChange}
            defaultValue={form['email']}
          />
        </div>
        <div id='yearsMonths'>
          <label>Years Operation *</label>
          <div id='twoInputFields'>
            <div id='years'>
              <CustomTextFieldWithIcons
                type={'integerOnly'}
                disabled={isSubmitted}
                error={errors['years']}
                name='years'
                styles={classes.customTag}
                tags={'years'}
                onChange={handleOnChange}
                defaultValue={form['years']}
              />
            </div>
            <div id='months'>
              <CustomTextFieldWithIcons
                name='months'
                disabled={isSubmitted}
                error={errors['months']}
                type={'integerOnly'}
                styles={classes.customTag}
                tags={'months'}
                onChange={handleOnChange}
                defaultValue={form['months']}
              />
            </div>
          </div>
        </div>
      </div>
      <div id='secondLayerFields' className={classes.secondLayerFields}>
        <div id='dohNumber'>
          <label>DOH License Number</label>
          <TextField
            disabled={isSubmitted}
            name='dohNumber'
            error={errors['dohNumber']}
            specialValue={form['dohNumber']}
            type={'noSpecialChar'}
            onChange={handleOnChange}
            defaultValue={form['dohNumber']}
          />
        </div>
        <div id='dohLicenseType' className={classes.dohLicenseType}>
          <label>DOH License Type</label>
          <TextField
            disabled={isSubmitted}
            name='dohLicenseType'
            type={'noSpecialChar'}
            error={errors['dohLicenseType']}
            onChange={handleOnChange}
            defaultValue={form['dohLicenseType']}
          />
        </div>
        <div id='dohLicenseExp'>
          <label>DOH License Expiry Date</label>
          <div id='dohDatePicker'>
            <CustomCalendar
              error={errors['dohLicenseExp']}
              id='dohLicenseExp'
              name='dohLicenseExp'
              value={form['dohLicenseExp']}
              onChange={(date) => handleDatePicker(date, 'dohLicenseExp', true, true)}
              invalidDateMessage={false}
              maxDateMessage={false}
              minDateMessage={false}
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='thirdLayerFields' className={classes.thirdLayerFields}>
        <div id='philhealthNumber'>
          <label>PhilHealth Accreditation No.</label>
          <CustomTextFieldNumberFormat
            defaultValue={form['philhealthNumber']}
            error={errors['philhealthNumber']}
            name='philhealthNumber'
            disabled={isSubmitted}
            placeholder='0000-0000000-0'
            format='####-#######-#'
            onChange={handleOnChange}
          />
        </div>
        <div id='philhealthExpiration'>
          <label>PhilHealth Expiry Date</label>
          <div id='philHealthdiv'>
            <CustomCalendar
              error={errors['philhealthExpiration']}
              id='philhealthExpiration'
              name='philhealthExpiration'
              onChange={(date) => handleDatePicker(date, 'philhealthExpiration')}
              value={form['philhealthExpiration']}
              disabled={isSubmitted}
              invalidDateMessage={false}
              maxDateMessage={false}
              minDateMessage={false}
            />
          </div>
        </div>
        <div id='checkPayee'>
          <label>Check Payee *</label>
          <TextField
            disabled={isSubmitted}
            name='payee'
            error={errors['payee']}
            type={'text'}
            onChange={handleOnChange}
            defaultValue={form['payee']}
          />
        </div>
      </div>
    </div>
  )
}

export default memo(MedicalClinicSectionOne)
