import moment from 'moment'
import { dateFormatChecker } from '../Utils/utils'

export default function datePickerValidation (form, name, newErrors = {}, required = false) {
  if (required && typeof form[name] === 'string' && form[name].length <= 0) {
    newErrors[name] = errorMessage(name)
  }
  if (typeof form[name] === 'string' && form[name].length > 0) {
    newErrors[name] = 'Invalid date format'
  }
  return newErrors
}

function errorMessage (name) {
  let errorMessage = {
    birthDate: '"Birth date" is not allowed to be empty',
    prcExpiration: '"Prc Expiry Date" is not allowed to be empty'
  }
  return errorMessage[name]
}

export function datepickerInvalidDate (form, name, newErrors = {}) {
  if (form[name] === null) {
    newErrors[name] = 'Invalid date format'
  }
  return newErrors
}

export function isDateValid (date) {
  let nDate = dateFormatChecker(date) ? moment(date).toISOString() : formatToDate(date)
  return {
    isValid: isValid(date),
    dateFormatted: nDate,
    isAfterDateNow: isValid(date) ? isAfterDateNow(nDate) : false
  }
}

function formatToDate (val) {
  let str = val.match(/(\d{2})(\d{2})(\d{4})/)
  return str ? str.splice(1, 4).join('/') : ''
}

export function isValid (date) {
  return moment(dateFormatChecker(date) ? date : formatToDate(date), 'MM/DD/YYYY', true).isValid()
}

export function dateValid (date) {
  return moment(date, 'MM/DD/YYYY', true).isValid()
}

export function isAfterDateNow (date) {
  return moment(date).isAfter(new Date())
}

export function dateFormat (date = new Date(), format = 'MM/DD/YYYY') {
  if (!date) return ''
  return moment(date).format(format)
}
