import React from 'react'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import InputComponent from '../../../InputComponent/InputComponent'

const TableRowsComponent = props => {
  const {
    readOnly,
    readOnlyHeaderTableProps,
    onUpdateHeaderTableProps,
    rowData = [],
    dataStructure = {},
    onClickDeleteRow,
    onChangeRow
  } = props

  const generateTableCells = (
    obj,
    dataStructure,
    index,
    onChangeRow,
    readOnly,
    readOnlyHeaderTableProps,
    onUpdateHeaderTableProps
  ) => {
    if (readOnly) {
      return readOnlyHeaderTableProps.map(key => {
        return (
          <TableCell>
            <InputComponent
              key={index}
              readOnly={readOnly}
              index={index}
              style={{ width: '100%' }}
              name={key.field}
              value={obj[key.field].value}
              {...dataStructure[key.field]}
              onChangeRow={onChangeRow}
            />
          </TableCell>
        )
      })
    }
    return onUpdateHeaderTableProps.map(key => {
      return (
        <TableCell>
          <InputComponent
            key={index}
            readOnly={readOnly}
            index={index}
            style={{ width: '100%' }}
            name={key.field}
            value={obj[key.field].value}
            {...dataStructure[key.field]}
            onClickDeleteRow={onClickDeleteRow}
            onChangeRow={onChangeRow}
          />
        </TableCell>
      )
    })
  }

  const generateTableRow = (
    data,
    dataStructure,
    onChangeRow,
    readOnly,
    readOnlyHeaderTableProps,
    onUpdateHeaderTableProps
  ) => {
    return (
      <React.Fragment>
        {data.map((obj, index) => {
          if (obj.deleted === true) {
            return <React.Fragment />
          }
          return (
            <TableRow key={index}>
              {generateTableCells(
                obj,
                dataStructure,
                index,
                onChangeRow,
                readOnly,
                readOnlyHeaderTableProps,
                onUpdateHeaderTableProps
              )}
            </TableRow>
          )
        })}
      </React.Fragment>
    )
  }

  return (
    <React.Fragment>
      {rowData.length > 0 ? (
        generateTableRow(
          rowData,
          dataStructure,
          onChangeRow,
          readOnly,
          readOnlyHeaderTableProps,
          onUpdateHeaderTableProps
        )
      ) : (
        <React.Fragment />
      )}
    </React.Fragment>
  )
}

export default TableRowsComponent
