import React from 'react'
import MedicalClinicAffilitiationContext from './context/MedicalClinicAffilitiationContext'
import Page1 from './MedicalClinicAffiliation_p1'
import Page2 from './MedicalClinicAffiliation_p2'
import { handleNextButton } from '../utils/hospitalApproverUtils'
export default function MedicalClinicAffilition ({ match, history }) {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setApproved] = React.useState(false)
  const [form, setForm] = React.useState(false)
  let inputRef = React.useRef()
  const [internalRemarks, setInternalRemarks] = React.useState({})

  const handleNext = async (value) => {
    if (!isApproved) {
      handleNextButton(internalRemarks, inputRef.current.value)
    }
    // console.log('internal remarks', internalRemarks)
    setIsNextPage(value)
  }
  return (
    <React.Fragment>
      <MedicalClinicAffilitiationContext.Provider value={{paramId: match.params.id, isApproved, setApproved, form, setForm, inputRef, setInternalRemarks, internalRemarks}}>
        {!isNextPage && <Page1 setPage={handleNext} history={history} /> }
        {isNextPage && <Page2 setPage={setIsNextPage} history={history} /> }
      </MedicalClinicAffilitiationContext.Provider>
    </React.Fragment>
  )
}
