import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'

export default function CustomAlertSnackbar (props) {
  const { onActivate, delay = '', customFunc = null } = props
  const [activate, setActivate] = React.useState(onActivate)

  React.useEffect(() => {
    setActivate(onActivate)
  }, [onActivate])

  React.useEffect(() => {
    const autoClose = setTimeout(() => {
      if (onActivate === true) {
        setActivate(false)
        if (customFunc !== null) customFunc()
      }
    }, delay || 1500)
    return () => clearTimeout(autoClose)
  })

  return (
    <React.Fragment>
      <Dialog open={activate}>
        <DialogContent>
          <p style={{ padding: '20px', color: '#151c1b' }}>{props.children}</p>
        </DialogContent>
      </Dialog>
    </React.Fragment>
  )
}
