import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import InputComponent from '../../../InputComponent/InputComponent'

import classes from './TableHeader.module.scss'
import FacilityGroupingService from 'Services/Api/RoomType/RoomType'

const TableHeader = props => {
  const {
    enableSort = true,
    field = {},
    loading = false,
    options = [],
    inputValue = '',
    customStyle = {},
    value = null,
    keyProp = '',
    type = '',
    filter = '',
    onChangeText = '',
    sort = {},
    setSort = '',
    width = ''
  } = props

  const buildInputComponent = (type, field, filter, onChangeText) => {
    return (
      <InputComponent
        style={{ width: '100%' }}
        type={type}
        isHeader
        value={filter[field]}
        name={field}
        onChangeText={onChangeText}
      />
    )
  }

  return (
    <TableCell
      key={`partnerHeaderCell_${keyProp}`}
      style={{ verticalAlign: 'top' }}
      className={classes.tableHeaderCell}
    >
      <div className={classes.headerFields} style={{ ...customStyle }}>
        <span className={classes.tableHeaderTitle}>{props.children}</span>
        {enableSort && (
          <FontAwesomeIcon
            icon='sort'
            className={classes.tableHeaderSortIcon}
            onClick={() => {
              setSort(field, !sort[field])
            }}
          />
        )}
      </div>
      {type && buildInputComponent(type, field, filter, onChangeText)}
    </TableCell>
  )
}

export default TableHeader
