import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import NativeSelect from '@material-ui/core/NativeSelect'
import classes from './index.module.scss'

const RenderDropDownField = props => {
  const { name, value = '', onChange, items, error } = props
  let itemData = items === undefined ? [] : items
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  return (
    <div id='root' className={classes.selectInput}>
      <NativeSelect
        name={name}
        value={value ? value.toUpperCase() : ''}
        onChange={onChange}
        classes={{ root: classes.select }}
        input={<InputBase
          className={`${classes.input} ${_error && !value && classes['has-errors']}`}
        />}
      >
        <option value='' />
        {itemData.map((item, index) => (
          <option key={index} value={item.value ? item.value.toUpperCase() : ''}>
            {item.text}
          </option>
        ))}
      </NativeSelect>
      <div className={classes.error}> {error && error}</div>
    </div>
  )
}

export default React.memo(RenderDropDownField)
