import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import NativeSelect from '@material-ui/core/NativeSelect'
import MenuItem from '@material-ui/core/MenuItem'
import NumberFormat from 'react-number-format'
import FormHelperText from '@material-ui/core/FormHelperText'
import Select from '@material-ui/core/Select'

import classes from './InputComponents.module.scss'

export const StatusSelectComponent = props => {
  const { id = '', value, error, errorMsg, onChange } = props
  return (
    <React.Fragment>
      <NativeSelect id={id} value={value}
        input={<InputBase className={classes.input} />}
        className={error && !value ? classes.inputFieldsError : classes.inputFields} onChange={onChange}>
        <option value={'Active'}>Active</option>
        <option value={'Inactive'}>Inactive</option>
      </NativeSelect>
      {error && <FormHelperText style={{color: 'red'}}>{errorMsg}</FormHelperText>}
    </React.Fragment>
  )
}

export const RoomTypesComponent = props => {
  const { id = '', value, onChange, error, errorMsg, items = [] } = props
  const itemDatas = items || []

  return (
    <React.Fragment>
      <NativeSelect id={id} value={value}
        input={<InputBase className={classes.selectInput} />}
        className={error && !value ? classes.SelectFieldsError : classes.SelectFields} onChange={onChange}>
        {itemDatas.map(items =>
          <option key={items.id} value={items.value} style={{marginLeft: '20px'}}>{items.text}</option>
        )}
      </NativeSelect>
      {error && <FormHelperText style={{color: 'red'}}>{errorMsg}</FormHelperText>}
    </React.Fragment>
  )
}

export const TextInputComponent = props => {
  const { id = '', error = '', value, onChange, errorMsg } = props

  return (
    <React.Fragment>
      <InputBase
        value={value}
        type='text'
        onChange={onChange}
        multiline
        className={error && !value ? classes.inputFieldsError : classes.inputFields}
        autoComplete='off'
      />
      {error && <FormHelperText style={{color: 'red'}}>{errorMsg}</FormHelperText>}
    </React.Fragment>
  )
}

export const NumberInputComponent = props => {
  const { id = '', error = '', disabled = false, value, errorMsg, onChange } = props

  return (
    <React.Fragment>
      <NumberFormat
        id={id}
        className={error && !value ? classes.inputFieldsError : errorMsg ? classes.inputFieldsError : classes.inputFields}
        disabled={disabled}
        value={value}
        onValueChange={value => onChange(value.formattedValue)}
        customInput={InputBase}
        isNumericString
        decimalScale={2}
        decimalSeparator={'.'}
        fixedDecimalScale
        allowLeadingZeros
        allowNegative={false}
        displayType={'input'}
      />
      {error && <FormHelperText style={{color: 'red'}}>{errorMsg}</FormHelperText>}
    </React.Fragment>
  )
}
