import instance from '../instance'
const facilities = instance('/p/f')
export default class FacilityGroupingService {
  static async add (data) {
    return facilities({
      url: `/partners/room_type/add`,
      method: 'POST',
      data
    })
  }

  static async get () {
    return facilities({
      url: `/partners/room_types`,
      method: 'GET'
    })
  }

  static async update (data) {
    // await axios.put(`${URL}/p/f/partners/room_type/${data._id}`, { roomTypeDesc: data.desc, roomTypeRemarks: data.remarks, roomTypeCode: data.code, status: data.status })
    return facilities({
      url: `/partners/room_type/${data._id}`,
      method: 'PUT',
      data: { roomTypeDesc: data.desc, roomTypeRemarks: data.remarks, roomTypeCode: data.code, status: data.status }
    })
  }
}
