import React, { useState, memo, useEffect } from 'react'
// Material UI
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import FormHelperText from '@material-ui/core/FormHelperText'

// Styles
import classes from './sectionTwo.module.scss'
import TinStatusComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/TinStatusComponent'
import SectionTwoValidation from './validation'

const MedicalClinicSectionTwo = props => {
  const { isSubmitted = false, isValidate, setData, data, setIsValid, defaultVal } = props
  const [tinStatIsValid, setTinStatusIsValid] = useState(false)
  const [errors, setError] = useState({})
  const [form, setForm] = useState({
    classification: '',
    processType: ''
  })

  useEffect(() => {
    let nForm = { ...form }
    setForm(Object.assign(nForm, defaultVal))
  }, [defaultVal])

  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  const isValidForm = React.useMemo(() => {
    const { isValid } = SectionTwoValidation(form)
    if (isValid && tinStatIsValid) {
      return true
    } else {
      return false
    }
  }, [form, tinStatIsValid])

  const validate = () => {
    const { errors, isValid } = SectionTwoValidation(form)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setError(Object.assign(nErrors, errors))
      return false
    }
  }

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setError(e)
    setForm(nform)
  }

  return (
    <div className={classes.radioButtonParts}>
      <div id='medClinicRadioButtons'>
        <FormControl component='fieldset'>
          <FormLabel className={classes.label} component='legend'>
          Medical Clinic Classification *
          </FormLabel>

          <RadioGroup
            id='classification'
            aria-label='gender'
            name='classification'
            value={form['classification'] && form['classification']}
            onChange={handleOnChange}
          >
            <FormControlLabel value='Full Service' control={<Radio color='primary' disabled={isSubmitted} />} label='Full Service' />
            <FormControlLabel value='Diagnostics' control={<Radio color='primary' disabled={isSubmitted} />} label='Diagnostics' />
            <FormControlLabel value='Specialty' control={<Radio color='primary' disabled={isSubmitted} />} label='Specialty' />
          </RadioGroup>
          {errors['classification'] && (
            <FormHelperText style={{ color: 'red' }}>{errors['classification']} </FormHelperText>
          )}
        </FormControl>
      </div>
      {/* tin status area of reponsability  */}
      <TinStatusComponent data={form} setData={setForm} setIsValid={setTinStatusIsValid} defaultVal={defaultVal} isValidate={isValidate} hasPaddingLeft={false} newErrors={errors} />
      <div id='processTypeRadioButtons' className={classes.processTypeRadioButtons}>
        <div id='radioFields' className={classes.processType}>
          <FormControl component='fieldset'>
            <FormLabel className={classes.label} component='legend'>
              Process Type *
            </FormLabel>
            <RadioGroup
              disabled={isSubmitted}
              id='processType'
              aria-label='gender'
              name='processType'
              value={form['processType']}
              onChange={handleOnChange}
            >
              <FormControlLabel value='Umbrella' control={<Radio color='primary' disabled={isSubmitted} />} label='Umbrella' />
              <FormControlLabel value='Regular' control={<Radio color='primary' disabled={isSubmitted} />} label='Regular' />
            </RadioGroup>
            {errors['processType'] && (
              <FormHelperText style={{ color: 'red' }}>{errors['processType']} </FormHelperText>
            )}
          </FormControl>
        </div>
      </div>
    </div>
  )
}

export default memo(MedicalClinicSectionTwo)
