import { useState, useEffect } from 'react'

const useInputHook = (onChange = null, defaultValue = '', regex = /[^a-zA-Z0-9 ]/g) => {
  const [newStr, setNum] = useState('')
  useEffect(() => {
    setNum(defaultValue)
  }, [defaultValue])

  const onChangeInput = ({target: {name, value}}) => {
    let str = value.replace(regex, '')
    onChange({
      target: {
        name,
        value: str
      }
    })
    setNum(str)
  }

  return [newStr, onChangeInput]
}

export default useInputHook
