import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
// import TextField from '../../../../../SharedComponents/TextField/CustomTextField'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import CustomDialog from '../../../../../Components/CustomDialog/CustomDialog'

import classes from './DialogBoxConfirmation.module.scss'
import { useSelector } from 'react-redux'
import Axios from 'axios'
const URL = process.env.REACT_APP_SERVER_API
let color

const DialogBoxConfirmation = props => {
  const { hospitalForms } = useSelector(state => state.AffiliationHospitalForm)
  const { affiliationType } = useSelector(state => state.affiliationType)
  const { dialogText, setDisable, disable, setIsApproved } = props
  const [open, setOpen] = React.useState(false)

  React.useEffect(() => {
    if (affiliationType.affiliationStatus === 'Submitted') {
      setDisable(true)
      setIsApproved(true)
    }
  }, [])

  const handleProceed = async () => {
    try {
      affiliationType.stageName = 'AFFILIATION_APPROVAL_FOR_OIC' // found this bug it causes duplicate to be passed it just have to override
      affiliationType.affiliationStatus = 'Submitted'
      affiliationType.dateSubmitted = new Date().toISOString()
      console.log('dialog affiliation type', affiliationType)
      console.log('hospitalForms', hospitalForms)
      let res = {}
      if (typeof affiliationType.id === 'undefined') {
        // res = await Axios.post(`${URL}/p/f/partners/facilities`, { ...affiliationType, ...hospitalForms })
        res = await AffiliationEncoderService.createFacilities({
          ...affiliationType,
          ...hospitalForms
        })

        affiliationType.id = res.data.data.id
      } else {
        // res = await Axios.put(`${URL}/p/f/partners/facilities/${affiliationType.id}`, {
        //   ...affiliationType,
        //   ...hospitalForms
        // })
        res = await AffiliationEncoderService.updateFacilities(affiliationType.id, {
          ...affiliationType,
          ...hospitalForms
        })
      }
      setDisable(true)
      setIsApproved(true)
      setOpen(false)
    } catch (error) {
      console.log('error in save', error)
    }
  }

  if (disable) {
    color = 'lightgray'
  } else {
    color = '#3ab77d'
  }

  return (
    <React.Fragment>
      <Button
        style={{ backgroundColor: color, color: 'white', paddingLeft: '1.5em', paddingRight: '1.5em', textTransform: 'capitalize' }}
        className={classes.submitButton}
        variant='contained'
        onClick={() => {
          setOpen(true)
        }}
        disabled={disable}
      >
        Submit
      </Button>
      <CustomDialog id='ga_custom_dialog' isCancel={open} setIsCancel={() => { setOpen(false) }}>
        <div id='ga_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='ga_custom_dialog_message' style={{ textAlign: 'center' }}>
            Are you sure you want to proceed with the submission of {dialogText} form for approval?
          </h3>
          <div id='ga_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              style={{ textTransform: 'none' }}
              onClick={() => {
                setOpen(false)
              }}
              id='ga_custom_dialog_btn_cancel'
              className={classes.btnRed}
              variant='contained'
            >
              Cancel
            </Button>
            <Button
              style={{ textTransform: 'none' }}
              id='ga_custom_dialog_btn_save'
              className={classes.btnGreen}
              variant='contained'
              onClick={handleProceed}
            >
              Proceed
            </Button>
          </div>
        </div>
      </CustomDialog>
    </React.Fragment>
  )
}

export default DialogBoxConfirmation
