import React, { useState, useEffect, memo, useContext } from 'react'
import classes from './DentalClinic.module.scss'
import AddressDropDown from '../../../../../../Components/AddressDrowpdown'
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import DentalClinicFormValidation from './dentalClinicFormValidation/DentalClinicFormValidation'

import { useSelector } from 'react-redux'

const DentalCLinicForm = ({ data, setData, isSubmitted = false, setIsValid, isValidate, defaultVal }) => {

 const { regions, cities: newCities, barangays: newBarangays, provinces: newProvinces } =  useSelector(state => state.addressReducer)

  const [isFetching, setIsFetching] = useState({})
  const [errors, setErrors] = useState({})
  const [form, setForm] = useState({})
  const [provinces, setProvinces] = useState([...newProvinces])
  const [cities, setCities] = useState([...newCities])
  const [barangays, setBarangays] = useState([...newBarangays])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    setProvinces(newProvinces)
    setBarangays(newBarangays)
    setCities(newCities)
  }, [newCities, newBarangays, newProvinces])

  useEffect(() => {
    if(isValidate) {
      validate()
    }
  }, [isValidate])

  useEffect(() => {
    let nForm = { ...form }
    setForm(Object.assign(nForm, defaultVal))
    // fetchAddress(defaultVal)
  }, [defaultVal])

  //  const fetchAddress = async data => {
  //    (await utils()).fetchAddress(data, setBarangays, setCities, setProvinces, setFetching)
  //  }

  const isValidForm = React.useMemo(() => {
    const { isValid } = DentalClinicFormValidation(form)
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form])

  const validate = () => {
    const {newErrors, isValid } = DentalClinicFormValidation(form)
    const nErrors = { ...errors }
    console.log('dental clinic form is valid form', isValid)
    if (isValid) {
      return true
    } else {
      setErrors(Object.assign( nErrors, newErrors))
      return false
    }
  }

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  useEffect(() => {
    let isSubscribed = true
    return () => isSubscribed = false
  }, [])


  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setErrors(e)
    setForm(nform)
  }


  const utils = async () => {
    let util = await import('Pages/Protected/Partner/Components/Utils/utils')
    return util

  }

  const handleAddressChainingDropdown = ({ target: {name, value} }) => {
    const dentalClinic = { ...form }
    dentalClinic[name] = value
    delete errors[name]
    setErrors(errors)
    setForm(dentalClinic)
    callHandleAddressChaining(name, value)
  }

  const callHandleAddressChaining = async (name, value) => {
    (await utils()).handleAddressChaining(name, value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }

  return (
    <div id='clinicGeneralInformation' className={classes.clinicGeneralInformation}>
      <div id='clinicTitle' className={classes.clinicTitle}>
        Clinic General Information
      </div>
      <div id='firstInputFields' className={classes.InputFields}>
        <div id='dentalClinicName' className={classes.name}>
          <label>Dental Clinic Name *</label>
          <div>
            <TextField
              defaultValue={form['name']}
              onChange={handleOnChange}
              error={errors['name']}
              id='name'
              name='name'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='contactNumber' className={classes.SameSizeFields}>
          <label>Contact Number *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              defaultValue={form['contactPersonNo']}
              error={errors['contactPersonNo']}
              id='contactPersonNo'
              name='contactPersonNo'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='SecondInputFields' className={classes.InputFields}>
        <div id='NameOfContact' className={classes.NameOfContact}>
          <label>Name of Contact Person *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['contactPerson']}
              defaultValue={form['contactPerson']}
              id='contactPerson'
              name='contactPerson'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='EmailAdd' className={classes.EmailAdd}>
          <label>Email Address *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type='email'
              error={errors['email']}
              defaultValue={form['email']}
              id='email'
              name='email'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='payee' className={classes.SameSizeFields}>
          <label>Check Payee *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['payee']}
              defaultValue={form['payee']}
              id='payee'
              name='payee'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='ThirdInputFields' className={classes.InputFields}>
        <div id='clinicAddress' className={classes.DentalAdd}>
          <label>
            Dental Clinic Address <br />
            Unit No./Floor/Bldg No
          </label>
          <div>
            <TextField
              onChange={handleOnChange}
              id='unitFlrBldg'
              name='unitFlrBldg'
              error={errors['unitFlrBldg']}
              defaultValue={form['unitFlrBldg']}
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='bldgName' className={classes.bldgName}>
          <label>
            <br />
            Building Name
          </label>
          <div>
            <TextField
              onChange={handleOnChange}
              id='bldgName'
              name='bldgName'
              error={errors['bldgName']}
              defaultValue={form['bldgName']}
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='StreetName' className={classes.SameSizeFields}>
          <label>
            <br />
            Street Name *
          </label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['street']}
              defaultValue={form['street']}
              id='street'
              name='street'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='FourthInputFields' className={classes.InputFields}>
        <div id='subdivision' className={classes.subdivision}>
          <label>Subdivision *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['subdivision']}
              id='subdivision'
              name='subdivision'
              disabled={isSubmitted}
              defaultValue={form['subdivision']}
            />
          </div>
        </div>
        <div id='region' className={classes.region}>
          <label>Region *</label>
          <div>
            <AddressDropDown
              onChange={handleAddressChainingDropdown}
              value={form['region']}
              items={regions}
              error={errors['region']}
              id='region'
              name='region'
              disableComponents={isSubmitted}
            />
          </div>
        </div>
        <div id='DistrictProvince' className={classes.DistrictProvince}>
          <label>District / Province *</label>
          {isFetching['province'] && <span className={classes['fetching-indicator']}> fetching...</span>}
          <div>
            <AddressDropDown
              onChange={handleAddressChainingDropdown}
              // value={isFetching['province'] ? loadingMessage : form['district']}
              value={form['district']}
              items={provinces}
              error={errors['district']}
              id='district'
              name='district'
              isFetching={isFetching['province']}
              disableComponents={isSubmitted || (provinces instanceof Array && provinces.length <= 0) || isFetching['province']}
            />
          </div>
        </div>
      </div>
      <div id='FifthInputFields' className={classes.InputFields}>
        <div id='barangay' className={classes.barangay}>
          <label>Barangay *</label>
          {isFetching['barangay'] && <span className={classes['fetching-indicator']}> fetching... </span>}
          <div>
            <AddressDropDown
              onChange={handleAddressChainingDropdown}
              // value={isFetching['barangay'] ? loadingMessage : form['barangay']}
              value={form['barangay']}
              items={barangays}
              error={errors['barangay']}
              id='barangay'
              name='barangay'
              isFetching={isFetching['barangay']}
              disableComponents={isSubmitted || (barangays instanceof Array && barangays.length <= 0) || isFetching['barangay']}
            />
          </div>
        </div>
        <div id='cityMunicipality' className={classes.cityMunicipality}>
          <label>City / Municipality *</label>
          {isFetching['city'] && <span className={classes['fetching-indicator']} > fetching... </span>}
          <div>
            <AddressDropDown
              onChange={handleAddressChainingDropdown}
              value={form['city']}
              // value={isFetching['city'] ? loadingMessage : form['city']}
              items={cities}
              error={errors['city']}
              id='city'
              name='city'
              isFetching={isFetching['city']}
              disableComponents={isSubmitted | (cities instanceof Array && cities.length <= 0) || isFetching['city']}
            />
          </div>
        </div>
        <div id='zipCode' className={classes.zipCode}>
          <label>Zip Code *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['zipCode']}
              id='zipCode'
              name='zipCode'
              disabled={isSubmitted}
              defaultValue={form['zipCode']}
              inputProps={{ maxLength: 4 }}
            />
          </div>
        </div>
      </div>
      <div id='SixInputFields' className={classes.InputFields}>
        <div id='schedule' className={classes.schedule}>
          <label>Clinic Schedule *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['clinicSchedule']}
              id='clinicSchedule'
              name='clinicSchedule'
              defaultValue={form['clinicSchedule']}
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='roomNumber' className={classes.zipCode}>
          <label>Room Number</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type={'number'}
              error={errors['roomNumber']}
              defaultValue={form['roomNumber']}
              id='roomNumber'
              name='roomNumber'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='SevenInputFields' className={classes.InputFields}>
        <div id='natureOfPractice' className={classes.natureOfPractice}>
          <label>Nature of Practice *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['natureOfPractice']}
              id='natureOfPractice'
              defaultValue={form['natureOfPractice']}
              type={'noSpecialChar'}
              specialValue={form['natureOfPractice']}
              name='natureOfPractice'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default memo(DentalCLinicForm)
