import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import TableCell from '@material-ui/core/TableCell'
import IconButton from '@material-ui/core/IconButton'
import classes from '../FacilityDiscount.module.scss'
import Button from '@material-ui/core/Button'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import CustomDeleteSnackbar from '../../../Components/CustomSnackBar/CustomAlertDelSnackBar'
import CustomDialogSave from '../../../TableMaintainer/FacilityGroupingMaintenance/components/CustomDialogSave/CustomDialogSave'
import AddRowForm from './AddRowForm'
// import axios from 'axios'
import { useDispatch } from 'react-redux'
import { setFacilityToDelete, setEnableButton } from '../../../../../Protected/Store/actions'
import ProfileService from '../../../../../../Services/Api/Partner/ProfileService'
import { getNestedObjects } from '../../../Components/Utils/utils'

// const URL = `${process.env.REACT_APP_SERVER_API}`

let arrToDelete = []

const RowActions = props => {
  const {
    data,
    currentDataRef,
    setCurrentData,
    index,
    LimitRows,
    setLimitRows,
    LimitRowsRef,
    setForm,
    haveData,
    currentData,
    form,
    errors,
    setErrors,
    setIsDisabled,
    setShouldValidate,
    shouldValidate,
    setShowSave,
    setEnableSaveButton,
    revertEdit,
    setIsEdit,
    setEditIndex,
    confirmCancel,
    deletedSucessfully,
    isEdit,
    setRowDeleted,
    setSaveTable
  } = props

  let dispatch = useDispatch()

  let LimitRowsDef = LimitRowsRef ? LimitRowsRef.current : null

  const [isSave, setIsSave] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [originalData, setOriginalData] = React.useState({})

  const [displayForm, setDisplayForm] = React.useState({
    status: '',
    discount_type: '',
    claim_type: '',
    discount_application: '',
    included_procedure: [],
    payment_type: '',
    terms_in_days: '',
    rate: '',
    effectivity_date: new Date().toISOString(),
    end_date: new Date().toISOString(),
    discount_to_be_deducted_from: '',
    other_details_of_discount: ''
  })

  React.useEffect(() => {
    if (confirmCancel || deletedSucessfully) {
      arrToDelete = []
    }
  }, [confirmCancel, deletedSucessfully])

  React.useEffect(() => {
    LimitRowsDef = LimitRowsRef ? LimitRowsRef.current : null
  }, [LimitRows, LimitRowsRef])

  const deleteDiscount = async () => {
    try {
      const dataArr = currentDataRef.current
      const newArray = [...dataArr]
      const res = newArray.filter((newArray, i) => i !== index)
      setCurrentData(res)

      const rows = [...LimitRowsDef]
      const rowData = rows.filter((rows, i) => i !== index)

      const dataToDelete = rows.filter((rows, i) => i === index)

      arrToDelete.push(getNestedObjects(dataToDelete, ['0', 'props', 'currentFormData']))

      dispatch(setFacilityToDelete(arrToDelete))
      dispatch(setEnableButton(true))

      setLimitRows(rowData)

      if (haveData) {
        try {
          await ProfileService.deleteOneFacilityDiscount(dataArr[index]['_id'])
          setRowDeleted(true)
        } catch (e) {
          console.log('error', e)
        }
      }

      if (!haveData) {
        setIsDisabled(false)

        //   setShouldValidate(false)
        setForm({
          status: '',
          discount_type: '',
          claim_type: '',
          discount_application: '',
          included_procedure: [],
          payment_type: '',
          terms_in_days: '',
          rate: '',
          effectivity_date: new Date().toISOString(),
          end_date: new Date().toISOString(),
          discount_to_be_deducted_from: '',
          other_details_of_discount: ''
        })
      }
    } catch (e) {
      console.log('error', e)
    }
  }

  const HandleDelete = () => {
    setIsSave(false)
    setIsCancel(true)
    setTimeout(() => {
      deleteDiscount()
    }, 600)
  }

  const editDiscount = () => {
    setShowSave(true)
    setIsEdit(true)
    setIsDisabled(false)
    setEditIndex(index)
    const newData = { ...data.original }
    setForm(data.original)

    const rows = [...LimitRows]
    rows[index] = (
      <AddRowForm
        index={index}
        setCurrentData={setCurrentData}
        currentDataRef={currentDataRef}
        LimitRows={LimitRows}
        LimitRowsRef={LimitRowsRef}
        setLimitRows={setLimitRows}
        setEnableSaveButton={setEnableSaveButton}
        curretData={currentData}
        currentFormData={newData}
        form={form}
        setForm={setForm}
        errors={errors}
        setErrors={setErrors}
        setIsDisabled={setIsDisabled}
        isEdit
      />
    )
    setLimitRows(rows)
  }
  return (
    <TableCell>
      <div style={{ display: 'flex' }}>
        {haveData && (
          <IconButton onClick={editDiscount} style={{ padding: '0px' }}>
            <FontAwesomeIcon icon='edit' style={{ cursor: 'pointer', width: '15px', height: '15px' }} />
          </IconButton>
        )}

        <IconButton disabled={shouldValidate} onClick={() => setIsSave(true)} style={{ padding: '0px' }}>
          <FontAwesomeIcon
            icon='trash'
            style={{ marginLeft: '10px', cursor: 'pointer', width: '15px', height: '15px' }}
          />
        </IconButton>
      </div>
      <CustomDialogSave
        onClose={() => {
          setIsSave(false)
        }}
        id='saveCustomDialog'
        isSave={isSave}
        setIsSave={() => setIsSave(false)}
      >
        <div id='saveCustomDialogContainer' className={classes.dialogContainer}>
          <h3 id='saveCustomDialogMessage'>Are you sure you want to delete the Facility Discount? </h3>
          <div id='saveCustomDialogBtnGroup' className={classes.btnGroup}>
            <Button
              onClick={() => setIsSave(false)}
              id='saveCustomDialogBtnNo'
              variant='contained'
              style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '130px', marginBottom: '10px' }}
            >
              No
            </Button>
            <Button
              id='saveCustomDialogBtnYes'
              className={classes.btnCancel}
              variant='contained'
              style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              onClick={HandleDelete}
            >
              Yes
            </Button>
          </div>
          <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
        </div>
      </CustomDialogSave>
      <CustomDeleteSnackbar isSave={isCancel} setIsSave={() => setIsCancel(false)} />
    </TableCell>
  )
}

export default React.memo(RowActions)
