import React from 'react'
import Dropzone from 'react-dropzone'
import { parse } from 'papaparse'

import classes from './FileDropZone.module.scss'

const FileDropZone = props => {
  const { fileData, setFileData } = props
  let fileRejected = false

  const onDrop = (acceptedFiles) => {
    acceptedFiles.forEach(async (file) => {
      if (file.name.endsWith('.csv')) {
        const text = await file.text()
        console.log('text:', text)
        try {
          const result = parse(text, {
            header: true, // If true, the first row of parsed data will be interpreted as field names
            dynamicTyping: true, // If true, numeric and boolean data will be converted to their type instead of remaining strings
            comments: true, // Will skip the line that indicates a comment (for example, "#" or "//")
            skipEmptyLines: true }) // If true, lines that are completely empty (those which evaluate to an empty string) will be skipped
          console.log('result data:', result.data)
          setFileData([...result.data])
        } catch (e) {
          console.log('file parsing error:', e)
        }
      } else {
        fileRejected = true
      }
    })
    acceptedFiles = []
  }

  const maxSize = 5242880 // 5MB

  return (
    <div>
      <Dropzone
        onDrop={onDrop}
        accept='.csv, text/csv, application/vnd.ms-excel, application/csv, text/x-csv, application/x-csv, text/comma-separated-values, text/x-comma-separated-values'
        minSize={0}
        maxSize={maxSize}
        multiple={false}
      >
        {({
          getRootProps,
          getInputProps,
          isDragActive,
          isDragReject,
          isDragAccept,
          rejectedFiles,
          acceptedFiles }) => {
          const isFileTooLarge = rejectedFiles.length > 0 && rejectedFiles[0].size > maxSize
          const additionalClass = isDragAccept ? '#00e676'
            : isDragReject ? '#ff1744'
              : isDragActive ? '#2196f3' : ''

          return (
            <div {...getRootProps()} className={classes.dropzone} style={{borderColor: additionalClass}} >
              <input {...getInputProps()} />
              <span>{isDragActive ? '📂' : '📁'}</span>
              {!isDragActive && (
                <div className={classes.message}>
                  Drag and drop file here, or click to select file.
                </div>
              )}
              {(isDragReject || fileRejected) && (
                <div className={classes.fileInvalidMsg}>
                  File type not accepted, sorry!
                </div>
              )}
              {isFileTooLarge && (
                <div className={classes.largeFileMsg}>
                  File is too large.
                </div>
              )}
              <ul>
                {fileData.length < 1 && acceptedFiles.length > 0 &&
                  <li className={classes.error}>File is empty or errors has been encountered during parsing.</li>
                }
                {fileData.length > 0 && acceptedFiles.length > 0 && acceptedFiles.map(acceptedFile => (
                  <li className={classes.success}>
                    {acceptedFile.name}
                  </li>
                ))}
              </ul>
            </div>
          )
        }
        }
      </Dropzone>
    </div>
  )
}

export default FileDropZone
