import React from 'react'
import axios from 'axios'

const CancelToken = axios.CancelToken

const useAsyncHook = (asyncFunction = null) => {
  const cancelSource = React.useRef()
  const isAttached = React.useRef(true)
  const [loading, setLoading] = React.useState(false)
  const [args, setArgs] = React.useState(null)
  const [data, setData] = React.useState({})
  const [error, setError] = React.useState({})

  React.useEffect(() => {
    return () => {
      isAttached.current = false
    }
  }, [])

  React.useEffect(() => {
    cancelSource.current = CancelToken.source()
    const fetchData = async () => {
      setLoading(true)
      try {
        const { data } = await asyncFunction(args, { cancelToken: cancelSource.current.token })
        setError({})
        if (Object.keys(data).length > 0 && isAttached.current) setData(data)
        if (isAttached.current) setLoading(false)
      } catch (err) {
        if (isAttached.current) {
          // setLoading(false)
          setError(error)
        }
      }
    }
    if (args !== null) {
      fetchData()
    }
    return () => {
      cancelSource.current.cancel()
    }
  }, [args, asyncFunction])

  const executeAsync = args => {
    if (Array.isArray(args)) {
      setArgs([...args])
    } else {
      setArgs({ ...args })
    }
  }

  return [data, error, loading, executeAsync]
}

export default useAsyncHook
