import moment from 'moment'

export const forContactPrintFormat = data => {
  const defaultObject = {
    list: [],
    count: 0
  }
  if (Object.keys(data).length > 0) {
    if (data.data.length === 0) return defaultObject
    const { list, count } = data.data[0]
    if (!list || list.length === 0) return defaultObject
    if (!count || count.length === 0) return defaultObject
    return {
      list: list.map(obj => {
        return {
          id: obj.id,
          ...(obj.facilityID ? { facilityID: obj.facilityID } : {}),
          dateSubmitted: moment.utc(obj.dateSubmitted).format('MM/DD/YYYY'),
          type: obj.type === 'DENTAL_CLINIC' ? obj.type.replace('_', ' ') : obj.type,
          name: obj.name,
          location: obj.location
        }
      }),
      count: count[0].count
    }
  }
  return defaultObject
}

export const forRenewContractPrintFormat = data => {
  const defaultObject = {
    list: [],
    count: 0
  }
  if (Object.keys(data).length > 0) {
    if (data.data.length === 0) return defaultObject
    const { list, count } = data.data[0]
    if (!list || list.length === 0) return defaultObject
    if (!count || count.length === 0) return defaultObject
    return {
      list: list.map(obj => {
        return {
          id: obj.id,
          ...(obj.facilityID ? { facilityID: obj.facilityID } : {}),
          expirationDate: moment.utc(obj.expirationDate || obj.dateSubmitted).format('MM/DD/YYYY'),
          ...(obj.type ? { type: obj.type === 'DENTAL_CLINIC' ? obj.type.replace('_', ' ') : obj.type } : {}),
          name: obj.name,
          location: obj.location
        }
      }),
      count: count[0].count
    }
  }
  return defaultObject
}
