import React from 'react'
import HospitalPageOne from './HospitalAffiliation_p1'
import HospitalPageTwo from './HospitalAffiliation_p2'
import HospitalAffiliationContext from './context/HospitalAffiliationContext'
import { handleNextButton } from '../utils/hospitalApproverUtils'

const HospitalAffiliation = ({match, history}) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  const [form, setForm] = React.useState({})
  let inputRef = React.useRef()
  const [internalRemarks, setInternalRemarks] = React.useState({})

  const handleNext = async (value) => {
    if (!isApproved) {
      handleNextButton(internalRemarks, inputRef.current.value)
    }
    setIsNextPage(value)
  }

  return (
    <React.Fragment>
      <HospitalAffiliationContext.Provider value={{paramId: match.params.id, isApproved, setIsApproved, form, setForm, inputRef, setInternalRemarks, internalRemarks}}>
        {!isNextPage && <HospitalPageOne setPage={handleNext} history={history} /> }
        {isNextPage && <HospitalPageTwo setPage={setIsNextPage} history={history} /> }
      </HospitalAffiliationContext.Provider>
    </React.Fragment>
  )
}
export default HospitalAffiliation
