import React, { useRef, useEffect, memo } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './EncoderTextField.module.scss'
import { inputTypes } from 'Pages/Protected/Partner/Components/JoiValidationInputField/utils'
import onPasteInputHook from '../EncoderCustomHooks/useInputOnPasteHook'
import useInputHook from '../EncoderCustomHooks/UseInputHook'

let types = ['number', 'noSpecialChar', 'lettersOnly']

const EncoderTextField = (props) => {

  const {
    label = '',
    required,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    type,
    minWidth,
    clearTextField = false,
    setClearText = null,
    defaultValue = '',
    ...other
  } = props

  const textFieldRef = useRef()

  const [regex, setRegex] = React.useState({})

  useEffect(() => {
    if (clearTextField) {
      textFieldRef.current.value = ''
      setClearText(false)
    }
  }, [clearTextField])

  useEffect(() => {
    textFieldRef.current.value = defaultValue
  }, [defaultValue])

  useEffect(() => {
    setRegex(inputTypes(type))
  }, [])


  const [handleOnPaste] = onPasteInputHook(regex.forOnPaste)

  const [onChangeInput] = useInputHook(textFieldRef, onChange, regex.forInput)

  const handleChange = e => {
    const {target: { value }} = e
    textFieldRef.current.value = value
    onChange(e)
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {/* {!readOnly && required ? ' *' : ''} */}
      </Typography>
      <div className={classes.wrapper} style={minWidth}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                onChange={types.includes(type) ? onChangeInput : handleChange}
                onPaste={types.includes(type) ? handleOnPaste : null}
                type={type}
                inputRef={textFieldRef}
                autoComplete='new-password'
                {...other}
              />
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default memo(EncoderTextField)
