
import React, { useState, useEffect, memo, Fragment, useMemo } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import NativeSelect from '@material-ui/core/NativeSelect'
import classes from './DropDown.module.scss'
import { useSelector } from 'react-redux'

/**
 * Props
 * id required
 * label
 * required
 * value
 * items array {value, text}
 * onChange
 *
 */
const DoctorSpecializationDropdown = props => {
  const {
    id,
    label = '',
    required,
    readOnly,
    onChange,
    disableComponents = false,
    error,
    minWidth,
    ...other
  } = props
  const { value = '' } = props
  const { doctorSpecialization } = useSelector(state => state.autoSuggestData)

  const [_error, setError] = useState()

  useEffect(() => {
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  const itemArray = useMemo(() => {
    return (
      doctorSpecialization.map((item, index) => (
        <option key={item._id} id={item._id} value={item.description}>
          {item.description}
        </option>
      ))
    )
  }, [])
  return (
    <div className={classes.root}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper} style={minWidth}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <Fragment>
            <Paper
              className={`${classes['select-container']} ${_error && !value && classes['has-error']} ${
                disableComponents && classes.disabled
              }`}
            >
              <NativeSelect
                id={id}
                onChange={onChange}
                readOnly={readOnly}
                value={value}
                classes={{ root: classes.select }}
                input={<InputBase className={classes.input} />}
                disabled={disableComponents}
                {...other}
              >
                <option value='' />
                {itemArray}
              </NativeSelect>
            </Paper>

            {_error && !value && <span className={classes.error}>{_error}</span>}
          </Fragment>
        )}
      </div>
    </div>
  )
}

export default memo(DoctorSpecializationDropdown)
