import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { Provider } from 'react-redux'
import { getNestedObjects } from '../../Components/Utils/utils'
import ProfileList from '../../Components/ProfileList/ProfileList'
import Partner from './Pages/PartnerInformation/'
import ContactDetailsViewer from './Pages/ViewerContract/ContractDetailsViewer'
import ListOfAffiliatedDoctors from '../Components/ListOfAffiliatedDoctors/ListOfAffiliatedDoctors'
import ContactDetails from '../../Components/ContactDetails/ContactDetails'
import OtherDocuments from './Pages/OtherDocuments/OtherDocuments'
import placeholderImage from '../../Assets/Icons/aventuslogo.jpg'
// import StandardRates from '../Components/StandardRates/StandardRates'
import StandardRates from './Pages/StandardRates/StandardRatesFinal'
import Store from '../../../Store'
import FacilityDiscounts from '../FacilityDiscount/FacilityDiscountPage'
import ClinicProfileParams from './Pages/Context/ClinicProfileParams'

export default function MedicalClinicProfile () {
  const [isOpenAffiliatedDoctorsModal, setIsOpenAffiliatedDoctorsModal] = React.useState(false)
  const [open, setOpen] = React.useState(false)
  const [paramId, setParamId] = React.useState('')
  const [name, setName] = React.useState('')
  const [status, setStatus] = React.useState('')
  const [city, setCity] = React.useState('')
  const [headerData, setHeaderData] = React.useState({
    status: '',
    name: ''
  })
  const [changeName, setChangeName] = React.useState(null)
  const [isEdit, setIsEdit] = React.useState(false)
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isEditContact, setIsEditContact] = React.useState(false)

  const HandleClinicName = data => {
    setName(data)
  }

  const breadCrumbsWithEditPartner = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    MedicalClinicPartnerProfile: {
      text: 'MEDICAL CLINIC PARTNER PROFILE',
      to: '/medicalClinicAffiliationForm'
    },
    EditPartnerInfo: {
      text: 'EDIT PARTNER INFORMATION',
      to: '/editPartnerInformation'
    },
    Clinic: {
      text: `${headerData.name.toUpperCase()} - ${city}`,
      to: '/medicalClinicAffiliationForm'
    }
  }

  const breadCrumbsWithEditContact = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    MedicalClinicPartnerProfile: {
      text: 'MEDICAL CLINIC PARTNER PROFILE',
      to: '/medicalClinicAffiliationForm'
    },
    EditContactDetails: {
      text: 'EDIT CONTACT DETAILS',
      to: '/editContactDetails'
    },
    Clinic: {
      text: `${headerData.name.toUpperCase()} - ${city}`,
      to: '/medicalClinicAffiliationForm'
    }
  }

  const breadcrumbs = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    MedicalClinicPartnerProfile: {
      text: 'MEDICAL CLINIC PARTNER PROFILE',
      to: '/medicalClinicAffiliationForm'
    },
    Clinic: {
      text: `${headerData.name.toUpperCase()} - ${city}`,
      to: '/medicalClinicAffiliationForm'
    }
  }

  React.useEffect(() => {
    ShowBreadCrumbs(BreadcrumbsService)
  }, [headerData.name, paramId, name, status, city, isToUpdate, isEditContact])

  const ShowBreadCrumbs = BreadcrumbsService => {
    if (isToUpdate) {
      BreadcrumbsService.setItems(breadCrumbsWithEditPartner)
      BreadcrumbsService.setSelectedItemsWithoutHome([
        'Medical',
        'Partners',
        'MedicalClinicPartnerProfile',
        'EditPartnerInfo',
        'Clinic'
      ])
    } else if (isEditContact) {
      BreadcrumbsService.setItems(breadCrumbsWithEditContact)
      BreadcrumbsService.setSelectedItemsWithoutHome([
        'Medical',
        'Partners',
        'MedicalClinicPartnerProfile',
        'EditContactDetails',
        'Clinic'
      ])
    } else {
      BreadcrumbsService.setItems(breadcrumbs)
      BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'MedicalClinicPartnerProfile', 'Clinic'])
    }
  }

  const HandleClick = () => {
    setIsToUpdate(false)
    setIsEditContact(false)
  }

  const row = [
    {
      to: `/MedicalClinicProfile/PartnerInformation/${paramId}`,
      name: ' Partner Information',
      path: '/MedicalClinicProfile/PartnerInformation/:medicalId',
      click: () => setIsEditContact(false),
      importPage: (
        <ClinicProfileParams.Provider
          value={{
            setParamId: setParamId,
            paramId,
            isToUpdate,
            setIsToUpdate,
            setChangeName,
            setHeaderData,
            isEdit,
            setIsEdit,
            setStatus,
            setCity
          }}
        >
          <Partner ClinicName={HandleClinicName} setHeaderData={setHeaderData} setIsToUpdate={setIsToUpdate} />
        </ClinicProfileParams.Provider>
      )
    },
    {
      to: `/MedicalClinicProfile/ContractDetails/${paramId}`,
      name: 'Contract Details',
      path: '/MedicalClinicProfile/ContractDetails/:paramId',
      click: () => HandleClick(),
      importPage: <ContactDetailsViewer paramId={paramId} />
    },
    {
      to: `/MedicalClinicProfile/StandardRates/${paramId}`,
      name: 'Standard Rates',
      path: '/MedicalClinicProfile/StandardRates/:medicalId',
      importPage: <StandardRates paramId={paramId} type={'CLINIC'} open={open} setOpen={setOpen} />,
      open: setOpen,
      click: () => HandleClick()
    },
    {
      to: `/MedicalClinicProfile/FacilityDiscounts/${paramId}`,
      name: 'Facility Discounts',
      path: '/MedicalClinicProfile/FacilityDiscounts/:paramId',
      importPage: <FacilityDiscounts open={open} setOpen={setOpen} paramId={paramId} />,
      open: setOpen,
      click: () => HandleClick()
    },
    {
      to: `/MedicalClinicProfile/ListOfAffiliatedDoctors/${paramId}`,
      name: 'List Of Affiliated Doctors',
      path: '/MedicalClinicProfile/ListOfAffiliatedDoctors/:facilityId',
      open: setIsOpenAffiliatedDoctorsModal,
      click: () => HandleClick()
    },
    {
      to: `/MedicalClinicProfile/ContactDetails/${paramId}`,
      name: 'Contact Details',
      path: '/MedicalClinicProfile/ContactDetails/:medicalId',
      importPage: <ContactDetails paramId={paramId} setIsEditContact={setIsEditContact} />,
      open: setOpen,
      click: () => setIsToUpdate(false)
    },
    {
      to: `/MedicalClinicProfile/OtherDocuments/${paramId}`,
      name: 'Other Documents',
      path: '/MedicalClinicProfile/OtherDocuments/:paramId',
      click: () => HandleClick(),
      importPage: <OtherDocuments paramId={paramId} />
    }
  ]

  return (
    <Provider store={Store}>
      <ProfileList
        ListItems={row}
        HeaderTitle={isToUpdate ? changeName : headerData.name}
        editName={isToUpdate ? !getNestedObjects(changeName, ['props', 'children', '1', '_store', 'validated']) : false}
        leftSideImage={placeholderImage}
        status={status}
      />
      {isOpenAffiliatedDoctorsModal && (
        <ListOfAffiliatedDoctors
          open={isOpenAffiliatedDoctorsModal}
          setOpen={setIsOpenAffiliatedDoctorsModal}
          paramId={paramId}
          endPoint={'/affiliatedDoctors'}
        />
      )}
    </Provider>
  )
}
