import React from 'react'
import Modal from '@material-ui/core/Modal'
import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'
import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import PartnerHeaderCell from '../../../Components/PartnerHeaderCell/PartnerHeaderCell'
import PartnerDateHeaderCell from '../../../Components/PartnerHeaderCell/PartnerDateHeaderCell'
import TablePagination from '../../../Components/TablePagination/TablePagination'
import { forContactPrintFormat } from 'Utilities/Partner/DataFormatter'
import useTableHeader from '../../../CustomHooks/useTableHeader'
import useTableDateHeader from '../../../CustomHooks/useTableDateHeader'
import useTable from '../../../CustomHooks/useTable'
import { useHistory } from 'react-router-dom'

import classes from './ForPrintViewList.module.scss'

const PrintViewList = props => {
  const { open, setOpen, onHandleDoubleClick = () => {}, type = '' } = props
  const [filter, setFilter] = React.useState({})
  const [isLoaded, setIsLoaded] = React.useState(false)
  const tableState = useTable(type, ContractManagerServices.getContractViewListPrint, forContactPrintFormat)
  const partnerID = useTableHeader('id', type, ContractManagerServices.getContractViewListPrintField, 6)
  const partnerDateSubmitted = useTableDateHeader('dateSubmitted')
  const partnerType = useTableHeader('type', type, ContractManagerServices.getContractViewListPrintField)
  const partnerName = useTableHeader('name', type, ContractManagerServices.getContractViewListPrintField)
  const partnerLocation = useTableHeader('location', type, ContractManagerServices.getContractViewListPrintField)
  React.useEffect(() => {
    setIsLoaded(true)
  }, [])

  React.useEffect(() => {
    const filter = {
      ...partnerID.value,
      ...partnerDateSubmitted.value,
      ...partnerType.value,
      ...partnerName.value,
      ...partnerLocation.value
    }
    setFilter({ ...filter })
  }, [partnerID.value, partnerDateSubmitted.value, partnerType.value, partnerName.value, partnerLocation.value])

  React.useEffect(() => {
    partnerID.setFilter(filter)
    partnerType.setFilter(filter)
    partnerName.setFilter(filter)
    partnerLocation.setFilter(filter)
    tableState.setFilter(filter)
  }, [filter])

  const onHandleSort = (field, isSort) => {
    let newList = []
    if (isSort) {
      newList = tableState.data.sort((a, b) => {
        if (field === 'dateSubmitted') {
          return a[field]
            .split('/')
            .reverse()
            .join('')
            .localeCompare(
              b[field]
                .split('/')
                .reverse()
                .join('')
            )
        } else {
          return a[field].localeCompare(b[field])
        }
      })
    } else {
      newList = tableState.data.reverse((a, b) => {
        return a[field].localeCompare(b[field])
      })
    }
    tableState.setData([...newList])
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <Modal style={{ outline: 'none' }} open={open} onClose={() => setOpen(false)}>
          <div className={classes.root} style={{ outline: 'none' }}>
            <div id='tableWrapper' className={classes.tableWrapper}>
              <TableContainer style={{ height: 350, backgroundColor: '#f5f7fb' }}>
                <Table stickyHeader className={classes.table} size='small'>
                  <TableHead className={classes.tableHeader}>
                    <TableRow>
                      <PartnerHeaderCell
                        headerTitle='Medical Partner ID'
                        width={{ ...(type === 'facilities' ? { width: '17%' } : {}), verticalAlign: 'top' }}
                        field={partnerID.field}
                        loading={partnerID.loading}
                        inputValue={partnerID.inputValue}
                        value={partnerID.value}
                        options={partnerID.options}
                        onChangeValue={value => partnerID.setValue(value)}
                        onChangeInputValue={inputValue => partnerID.setInputValue(inputValue)}
                        enableSort
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                      />
                      {type === 'doctors' && (
                        <PartnerHeaderCell
                          headerTitle='Facility ID'
                          width={{ verticalAlign: 'top' }}
                          enableSort={false}
                          enableAutoComplete={false}
                        />
                      )}
                      <PartnerDateHeaderCell
                        headerTitle='Date Submitted'
                        width={{ width: '17%', verticalAlign: 'top' }}
                        field={partnerDateSubmitted.field}
                        enableSort
                        value={partnerDateSubmitted.value}
                        onChangeValue={value => partnerDateSubmitted.setValue(value)}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                      />
                      <PartnerHeaderCell
                        headerTitle='Partner Type'
                        width={{ width: '15%', verticalAlign: 'top' }}
                        field={partnerType.field}
                        loading={partnerType.loading}
                        inputValue={partnerType.inputValue}
                        value={partnerType.value}
                        options={partnerType.options}
                        onChangeValue={value => partnerType.setValue(value)}
                        onChangeInputValue={inputValue => partnerType.setInputValue(inputValue)}
                        enableSort
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                      />
                      <PartnerHeaderCell
                        headerTitle='Partner Name'
                        width={{ ...(type === 'doctors' ? { width: '20%' } : {}), verticalAlign: 'top' }}
                        field={partnerName.field}
                        loading={partnerName.loading}
                        inputValue={partnerName.inputValue}
                        value={partnerName.value}
                        options={partnerName.options}
                        onChangeValue={value => partnerName.setValue(value)}
                        onChangeInputValue={inputValue => partnerName.setInputValue(inputValue)}
                        enableSort
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                      />
                      <PartnerHeaderCell
                        headerTitle='Location'
                        width={{ ...(type === 'doctors' ? { width: '20%' } : {}), verticalAlign: 'top' }}
                        field={partnerLocation.field}
                        loading={partnerLocation.loading}
                        inputValue={partnerLocation.inputValue}
                        value={partnerLocation.value}
                        options={partnerLocation.options}
                        onChangeValue={value => partnerLocation.setValue(value)}
                        onChangeInputValue={inputValue => partnerLocation.setInputValue(inputValue)}
                        enableSort
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                      />
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    <React.Fragment>
                      {tableState.loading ? (
                        <TableRow>
                          <TableCell colSpan={type === 'facilities' ? 5 : 6}>
                            <div className={classes.loadingComponent}>
                              <CircularProgress />
                            </div>
                          </TableCell>
                        </TableRow>
                      ) : (
                        <React.Fragment>
                          {tableState.data.map((dataObj, dataIndex) => {
                            return (
                              <TableRow
                                onDoubleClick={() => {
                                  onHandleDoubleClick(dataObj)
                                }}
                                key={`dataList_${dataIndex}`}
                                className={classes.tableBodyRow}
                              >
                                {Object.keys(dataObj).map((objKey, keyIndex) => {
                                  return (
                                    <TableCell key={`objKey_${keyIndex}`} align='left'>
                                      {dataObj[objKey]}
                                    </TableCell>
                                  )
                                })}
                              </TableRow>
                            )
                          })}
                        </React.Fragment>
                      )}
                    </React.Fragment>
                  </TableBody>
                </Table>
              </TableContainer>
              <TablePagination
                page={tableState.page}
                total={tableState.data.length}
                pageCount={tableState.count}
                limit={tableState.limit}
                text={'Contracts'}
                onClickLimit={val => tableState.setLimit(val)}
                onClickPage={val => tableState.setPage(val)}
              />
            </div>
            <div className={classes.footer}>
              <Button onClick={() => setOpen(false)} id='rc_btn_cancel' className={classes.btnRed} variant='contained'>
                Close
              </Button>
            </div>
          </div>
        </Modal>
      )}
    </React.Fragment>
  )
}

export default PrintViewList
