
import React, { memo, useState, useEffect, useMemo } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import NativeSelect from '@material-ui/core/NativeSelect'
import Typography from '@material-ui/core/Typography'
import classes from './DropDown.module.scss'

/**
 * Props
 * id required
 * label
 * required
 * value
 * items array {value, text}
 * onChange
 *
 */
const DropDown = props => {
  const {
    id,
    label = '',
    required,
    items = [],
    readOnly,
    onChange,
    disableComponents = false,
    error,
    minWidth,
    ...other
  } = props
  const { value = '' } = props
  const [_error, setError] = useState()

  useEffect(() => {
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  const location = useMemo(() => {
    return items
  }, [items])

  return (
    <div className={classes.root}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper} style={minWidth}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['select-container']} ${_error && !value && classes['has-error']} ${
                disableComponents && classes.disabled
              }`}
            >
              <NativeSelect
                id={id}
                onChange={onChange}
                readOnly={readOnly}
                name={props.name}
                value={value}
                inputProps={{
                  name: props.name,
                  id: props.id
                }}
                classes={{ root: classes.select }}
                input={<InputBase className={classes.input} />}
                disabled={disableComponents}
                {...other}
              >
                <option value='' />
                { location.length > 0 && location.map((item, index) => (
                  <option key={item._id} id={item._id} value={item.name || ''}>
                    {item.name}
                  </option>
                ))}
              </NativeSelect>
            </Paper>

            {_error && !value && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default memo(DropDown)
