export function regionsAction (payload) {
  return {
    type: 'SET_REGIONS_ENCODER',
    payload
  }
}

export function barangayAction (payload) {
  return {
    type: 'SET_BARANGAYS_ENCODER',
    payload
  }
}

export function cityAction (payload) {
  return {
    type: 'SET_CITY_ENCODER',
    payload
  }
}

export function provinceAction (payload) {
  return {
    type: 'SET_PROVINCE_ENCODER',
    payload
  }
}

export function resetAddress () {
  return {
    type: 'SET_RESET_ADDRESS_ENCODER',
  }
}
