import React from 'react'
import classes from './TwoTextArea.module.scss'
import internalRemarksValidation from './internalRemarksValiation'

const TwoTextArea = ({ label1, label2, val1, inputRef, remarks = '', setDisabled, isApproved }) => {
  const [form, setForms] = React.useState({
    internalRemarks: ''
  })
  const handleOnChange = val => {
    const nForm = {...form}
    nForm.internalRemarks = val
    setForms(nForm)
    if (inputRef.current) inputRef.current.value = val
  }

  const disabledButton = React.useMemo(() => {
    const { isValid } = internalRemarksValidation(form)
    if (isValid) {
      return false
    } else {
      return true
    }
  }, [form])

  React.useEffect(() => {
    setDisabled(disabledButton)
  }, [form.internalRemarks])

  React.useEffect(() => {
    const nForm = {...form}
    nForm.internalRemarks = remarks
    setForms(nForm)
  }, [remarks])

  return (
    <div className={classes.RemarksField}>
      <span>
        <div>
          <label>{label1}</label>
          <textarea value={val1} readOnly />
        </div>
        <div>
          <label>{label2}</label>
          <textarea ref={inputRef} onChange={({target: {value}}) => handleOnChange(value)} readOnly={isApproved} />
          {disabledButton && <p className={classes.errorMessage}>"Internal Remarks" is required</p>}
        </div>
      </span>
    </div>
  )
}

export default TwoTextArea
