import React from 'react'
import Button from '@material-ui/core/Button'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import FileDropZone from '../../../Components/FileDropZone/FileDropZone'
import DownloadTemplate from '../DownloadTemplate/DownloadTemplate'
import ProfileServices from 'Services/Api/Partner/ProfileService'

import classes from './UploadRates.module.scss'

const hospitalHeaders = [
  [
    'status',
    'facilityCpt',
    'facilityProcedure',
    'cpt',
    'procedure',
    'opd',
    'standard',
    'roomType',
    'price',
    'effectivityDate',
    'remarks'
  ]
]

const clinicHeaders = [
  ['status', 'facilityCpt', 'facilityProcedure', 'cpt', 'procedure', 'price', 'effectivityDate', 'remarks']
]

const UploadRates = props => {
  const { isUpload, setIsUpload, id, type, cptCodes = [], customFunc = null } = props
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [fileData, setFileData] = React.useState([])
  const [headers, setHeaders] = React.useState(() => {})
  const [filename, setFilename] = React.useState('')
  const [isData, setIsData] = React.useState(false)

  React.useEffect(() => {
    switch (type) {
      case 'HOSPITAL':
        setHeaders(hospitalHeaders)
        setFilename('standard_rates_hospital.csv')
        break
      case 'CLINIC':
        setHeaders(clinicHeaders)
        setFilename('standard_rates_medical_clinic.csv')
        break
      case 'DENTAL_CLINIC':
        setHeaders(clinicHeaders)
        setFilename('standard_rates_dental_clinic.csv')
        break
      default:
        setFilename('template.csv')
        break
    }
    setIsLoaded(true)
  }, [])

  React.useEffect(() => {
    if (fileData.length > 0) {
      setIsData(true)
    } else {
      setIsData(false)
    }
  }, [fileData])

  const handleOnCancel = () => {
    setIsUpload(false)
    setFileData([])
  }

  const handleOnImport = async () => {
    if (!fileData.length > 0) return
    const updatedData = fileData.map(obj => {
      if (obj.cpt === '' || obj.cpt === null || !obj.cpt) {
        return { ...obj, cpt: '', procedure: '' }
      }
      const data = matchCPTCodes(cptCodes, obj)
      if (data && Object.keys(data).length > 0) {
        return { ...obj, cpt: data.code, procedure: data.description }
      }
      return { ...obj, procedure: '' }
    })
    const finalData = { id, type, fileData: updatedData }

    try {
      await ProfileServices.createTempStandardRates(finalData)
      if (customFunc !== null) {
        customFunc()
      }
    } catch (e) {
      console.log('handleOnImport error:', e)
    }
    setIsUpload(false)
    setFileData([])
  }

  const matchCPTCodes = (CPTList, data) => {
    return CPTList.find(obj => obj.code.toLowerCase() === data.cpt.toLowerCase())
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <CustomDialog id='uploadCustomDialog' isCancel={isUpload} setIsCancel={() => setIsUpload(false)}>
          <div id='uploadDialogContainer' className={classes.dialogContainer}>
            <h3 id='uploadDialogHeaderMessage'>Upload Rates</h3>
            <FileDropZone fileData={fileData} setFileData={setFileData} />
            <DownloadTemplate headers={headers} filename={filename} />
            <div id='uploadDialogButtonGroup' className={classes.btnGroup}>
              <Button
                onClick={handleOnCancel}
                id='uploadDialogButtonCancel'
                className={classes.btnCancel}
                variant='contained'
              >
                Cancel
              </Button>
              <Button
                id='uploadDialogButtonImport'
                className={classes.btnImport}
                variant='contained'
                onClick={handleOnImport}
                disabled={!isData}
                style={{ backgroundColor: !isData ? '#dddfe3' : '' }}
              >
                Import
              </Button>
            </div>
          </div>
        </CustomDialog>
      )}
    </React.Fragment>
  )
}

export default UploadRates
