import React from 'react'

const useTableDateHeader = field => {
  const isAttached = React.useRef(true)
  const [value, setValue] = React.useState({
    [field]: null
  })

  React.useEffect(() => {
    return () => {
      isAttached.current = false
    }
  }, [])

  const onChangeDate = val => {
    setValue({
      [field]: val
    })
  }

  return {
    field: field,
    value: value,
    setValue: onChangeDate
  }
}

export default useTableDateHeader
