import React, { useState, memo, useEffect, useContext } from 'react'
import isEmpty from 'lodash/isEmpty'
import classes from './DentistForm.module.scss'
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import DropDown from '../../../../../Components/NativeDropDown'

import DoctorSpecialization from 'Pages/Protected/Partner/Components/DoctorSpecialization'
import {
  genderList,
  civilStatusList
} from 'Pages/Protected/Partner/Constants/dropDownItems'

import CalendarInlineWithInlineEdit from '../../../../../../Components/DatePicker/DatePickerInlineEdit'
import EncoderInputFieldNumberFormat from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderInputFieldNumberFormat'
import dentistFormValidation from './dentistFormValidation'

import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'

const DentistForm = ({ data, isSubmitted = false, setData, setIsValid, isValidate, defaultVal }) => {

  const [form, setForm] = useState({
    prcExpiration: null,
    birthDate: null,
    philhealthExpiration: null,
    philhealthNumber: ''
  })
  const [errors, setError] = useState({})

  useEffect(() => {
    const newData = { ...form }
    setForm(Object.assign(newData, defaultVal))
  }, [defaultVal])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])


  useEffect(() => {
    if(isValidate) {
      validate()
    }
  }, [isValidate])

  const validate = () => {
    const {newErrors, isValid } = dentistFormValidation(form)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setError(Object.assign( nErrors, newErrors))
      return false
    }
  }

  const isValidForm = React.useMemo(() => {
    const nErrors = { ...errors }
    const { newErrors } = dentistFormValidation(form, nErrors)
    let isValid = isEmpty(Object.assign(nErrors, newErrors))
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form, errors])

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleDropdown = ({ target: {name, value} }) => {
    const nform = { ...form }
    nform[name] = value
    delete errors[name]
    setError(errors)
    setForm(nform)
  }

  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setError(e)
    setForm(nform)
  }

  const handleDatePicker = async (date, name) => {
    let newError = { ...errors }
    let newDate = (await utils()).dateFormat(date)
    if(newDate === 'Invalid date') {
      newError[name] = 'Invalid Date'
    } else {
      const nForm = { ...form }
      nForm[name]= newDate
      setForm(nForm)
      delete newError[name]
      if(name === 'birthDate') {
        console.log('is birhthdate is after')
        if((await utils()).isAfterDateNow(date)) {
          newError[name] = 'Invalid Date'
        }
      }
    }
    setError(newError)
  }

  const utils = async () => {
    let util = await import('Pages/Protected/Partner/Components/DatePicker/DatePickerUtils')
    return util
  }

  return (
    <div id='dentistInformation' className={classes.dentistInformation}>
      <div id='dentistText' className={classes.dentistText}>
        Dentist General Information
      </div>
      <div id='dentistFirstInputFields' className={classes.dentistInputFields}>
        <div id='FirstName' className={classes.FirstName}>
          <label>First Name *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type='noSpecialChar'
              error={errors['firstName']}
              defaultValue={form['firstName']}
              id='firstName'
              name='firstName'
              disabled={isSubmitted}
              required
            />
          </div>
        </div>
        <div id='middleName' className={classes.middleName}>
          <label>Middle Name *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type='noSpecialChar'
              error={errors['middleName']}
              defaultValue={form['middleName']}
              id='middleName'
              name='middleName'
              disabled={isSubmitted}
              required
            />
          </div>
        </div>
        <div id='lastName' className={classes.lastName}>
          <label>Last Name *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type='noSpecialChar'
              error={errors['lastName']}
              defaultValue={form['lastName']}
              id='lastName'
              name='lastName'
              disabled={isSubmitted}
              required
            />
          </div>
        </div>
      </div>
      <div id='dentistSecondInputFields' className={classes.dentistInputFields}>
        <div id='suffix' className={classes.suffix}>
          <label>Suffix</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type='noSpecialChar'
              error={errors['suffix']}
              id='suffix'
              defaultValue={form['suffix']}
              name='suffix'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='birthDate' className={classes.birthDate}>
          <label>Birthdate *</label>
          <div>
             <DatePicker
              error={errors['birthDate']}
              id='birthDate'
              onChange={(date) => handleDatePicker(date, 'birthDate', true, true)}
              value={form['birthDate']}
              invalidDateMessage={false}
              maxDateMessage={false}
              minDateMessage={false}
              disabled={isSubmitted}
              disableFuture
            />
          </div>
        </div>
        <div id='civilStatus' className={classes.civilStatus}>
          <label>Civil Status *</label>
          <div>
            <DropDown
              onChange={handleDropdown}
              value={form['civilStatus']}
              items={civilStatusList}
              name='civilStatus'
              error={errors['civilStatus']}
              id='civilStatus'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='gender' className={classes.gender}>
          <label>Gender *</label>
          <div>
            <DropDown
              onChange={handleDropdown}
              value={form['gender']}
              items={genderList}
              error={errors['gender']}
              id='gender'
              name='gender'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='dentistThirdInputFields' className={classes.dentistInputFields}>
        <div id='emailAddress' className={classes.emailAddress}>
          <label>Email Address *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['emailAddress']}
              defaultValue={form['emailAddress']}
              id='emailAddress'
              name='emailAddress'
              disabled={isSubmitted}
              required
            />
          </div>
        </div>
        <div id='contact' className={classes.contact}>
          <label>Contact Number (phone/mobile) *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              error={errors['contactNumber']}
              id='contactNumber'
              name='contactNumber'
              defaultValue={form['contactNumber']}
              disabled={isSubmitted}
              required
            />
          </div>
        </div>
      </div>
      <div id='dentistFourthInputFields' className={classes.dentistInputFields}>
        <div id='main' className={classes.main}>
          <label>Main Specialization *</label>
          <div>
            <DoctorSpecialization
              onChange={handleDropdown}
              value={form['mainSpecialization']}
              name='mainSpecialization'
              error={errors['mainSpecialization']}
              id='mainSpecialization'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='major' className={classes.major}>
          <label>Major Specialization *</label>
          <div>
            <DoctorSpecialization
              onChange={handleDropdown}
              value={form['majorSpecialization']}
              error={errors['majorSpecialization']}
              id='majorSpecialization'
              name='majorSpecialization'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='dentistFifthInputFields' className={classes.dentistInputFields}>
        <div id='sub' className={classes.sub}>
          <label>Sub Specialization</label>
          <div>
            <DoctorSpecialization
              onChange={handleDropdown}
              value={form['subSpecialization']}
              id='subSpecialization'
              name='subSpecialization'
              disabled={isSubmitted}
            />
          </div>
        </div>
        <div id='other' className={classes.other}>
          <label>Other Specialization</label>
          <div>
            <DoctorSpecialization
              onChange={handleDropdown}
              value={form['otherSpecialization']}
              id='otherSpecialization'
              name='otherSpecialization'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>
      <div id='dentistSixthInputFields' className={classes.dentistInputFields}>
        <div id='pda' className={classes.pda}>
          <label>PDA Number</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type={'noSpecialChar'}
              defaultValue={form['pda']}
              id='pda'
              error={errors['pda']}
              name='pda'
              disabled={isSubmitted}
            />
          </div>
        </div>
      </div>

      <div id='dentistSevenInputFields' className={classes.dentistInputFields}>
        <div id='prc' className={classes.prc}>
          <label>PRC License No. *</label>
          <div>
            <TextField
              onChange={handleOnChange}
              type='noSpecialChar'
              id='prcNumber'
              name='prcNumber'
              error={errors['prcNumber']}
              defaultValue={form['prcNumber']}
              disabled={isSubmitted}
              required
            />
          </div>
        </div>
        <div id='prcExpiry' className={classes.prcExpiry}>
          <label>PRC Expiry Date *</label>
          <div>
            <DatePicker
              error={errors['prcExpiration']}
              id='prcExpiration'
              name='prcExpiration'
              onChange={(date) => handleDatePicker(date, 'prcExpiration')}
              value={form['prcExpiration']}
              disabled={isSubmitted}
              invalidDateMessage={false}
              maxDateMessage={false}
              minDateMessage={false}
            />
          </div>
        </div>
      </div>
      <div id='dentistEightInputFields' className={classes.dentistInputFields}>
        <div id='phil' className={classes.phil}>
          <label>PhilHealth Accreditation No.</label>
          <div>
            <EncoderInputFieldNumberFormat
              id='philhealthNumber'
              defaultValue={form['philhealthNumber']}
              error={errors['philhealthNumber']}
              name='philhealthNumber'
              disabled={isSubmitted}
              placeholder='0000-0000000-0'
              format='####-#######-#'
              onChange={handleOnChange}
            />
          </div>
        </div>
        <div id='philExpiry' className={classes.philExpiry}>
          <label>PhilHealth Expiry Date</label>
          <div>
            <DatePicker
              error={errors['philhealthExpiration']}
              id='philhealthExpiration'
              name='philhealthExpiration'
              onChange={(date) => handleDatePicker(date, 'philhealthExpiration')}
              value={form['philhealthExpiration']}
              disabled={isSubmitted}
              invalidDateMessage={false}
              maxDateMessage={false}
              minDateMessage={false}
            />
          </div>
        </div>
      </div>
      <div id='dentisNineInputFields' className={classes.dentistInputFields}>
        <div id='tin' className={classes.tin}>
          <label>TIN Number *</label>
          <div>
            <EncoderInputFieldNumberFormat
              id='tinNumber'
              name='tinNumber'
              placeholder='000-000-000-000'
              defaultValue={form['tinNumber'] && form['tinNumber']}
              error={errors['tinNumber']}
              disabled={isSubmitted}
              format='###-###-###-###'
              onChange={handleOnChange}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default memo(DentistForm)
